import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import {
  HintResponseType,
  HintResponse,
  HintSetResponse,
  doesHintUseToken,
} from "sockapi/scope-specs/hint-spec";
import { AdminQueueTaskSummary } from "sockapi/scope-specs/admin-queue-spec";
import { AdminQueueTaskType } from "sockapi/scope-specs/admin-queue-task-spec";
import { TeamProgressEventReasonType } from "sockapi/team-progress";
import { HuntNotificationType } from "sockapi/client-notifications";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";
import { TeamController } from "sock-server/server-controllers/team-controller";

class HintEngine {
  ctx: ScopeEngineContext<ScopeType.HINT>;
  teamController: TeamController;
  state: SockAPIScopeState<ScopeType.HINT>;
  adminQueueTaskId?: string;

  constructor(
    ctx: ScopeEngineContext<ScopeType.HINT>,
    teamController: TeamController,
    state: SockAPIScopeState<ScopeType.HINT>
  ) {
    this.ctx = ctx;
    this.teamController = teamController;
    this.state = state;
  }

  init() {
    this.setAdminQueueTask();
  }

  getAdminQueueTask(): AdminQueueTaskSummary {
    const { teamId, puzName, request, response } = this.state;
    return {
      type: AdminQueueTaskType.HINT,
      teamId,
      puzName,
      requestTime: request.timestamp,
      respondTime: response?.timestamp ?? undefined,
    };
  }

  setAdminQueueTask() {
    const adminQueueEngine = this.ctx.server.getAdminQueueEngine();
    const taskSummary = this.getAdminQueueTask();
    if (taskSummary !== undefined) {
      if (this.adminQueueTaskId === undefined)
        this.adminQueueTaskId = adminQueueEngine.addTask(
          {
            type: AdminQueueTaskType.HINT,
            hintId: this.ctx.scope.hintId,
          },
          taskSummary
        );
      else adminQueueEngine.updateTask(this.adminQueueTaskId, taskSummary);
    }
  }

  getPermissions(conn: ClientConnection) {
    return conn.teamId === this.state.teamId
      ? ScopePermissions.READ
      : ScopePermissions.ADMIN_ONLY;
  }

  getState() {
    return this.state;
  }

  /**
   * Handles a response that has been synced to the backend.
   * If the response did not come from the backend, use setResponseAsync
   * instead.
   */
  handleResponseNoBackendSync(response: HintResponse) {
    const { server } = this.ctx;
    const { puzName, teamId } = this.state;

    const prevDoesHintUseToken = doesHintUseToken(
      this.state.request,
      this.state.response
    );
    const newDoesHintUseToken = doesHintUseToken(this.state.request, response);
    const diffNumUsedTokens =
      (newDoesHintUseToken ? 1 : 0) - (prevDoesHintUseToken ? 1 : 0);
    const hintsEngine = this.teamController.getHintsEngine();
    hintsEngine.updatePuzzle(
      puzName,
      (hintsEngine.state.puzzleNumUsedTokens[puzName] ?? 0) + diffNumUsedTokens,
      false
    );

    this.state.response = response;
    this.ctx.update(this.ctx.scope, { response });
    if (![HintResponseType.OBSOLETE].includes(response.type))
      server.broadcastNotifToTeam(teamId, {
        type: HuntNotificationType.HINT_ANSWERED,
        puzName,
      });

    this.setAdminQueueTask();
  }

  handleResponse(response: HintSetResponse) {
    const {
      server,
      scope: { hintId },
    } = this.ctx;
    this.handleResponseNoBackendSync({
      ...response,
      timestamp: server.clock.genTimestamp(),
    });
    this.teamController.mergeSlowSyncPacket({
      hints: { [hintId]: this.state },
    });
  }

  async setResponseAsync(response: HintSetResponse) {
    // If we answer a hint, also give the team all canned hints
    // for the puzzle.
    if ([HintResponseType.ANSWERED].includes(response.type))
      await this.teamController.modifyTeamProgressAsync((progressTracker) =>
        progressTracker.overridePuzzleNumCannedHintsFound(
          this.state.puzName,
          this.ctx.server.getCannedHintsForPuzzle(this.state.puzName).length,
          { type: TeamProgressEventReasonType.HINT_ANSWERED }
        )
      );

    this.handleResponse(response);
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.HINT>
  ) {
    const { setResponse } = payload;

    if (setResponse !== undefined) {
      await this.setResponseAsync(setResponse);
      return reqCtx.sendStepResp(this.ctx.scope, "");
    }
  }
}

export type HintServerScopeSpec = {
  engine: HintEngine;
};

export const hintServerScopeHooks: ServerScopeHooks<ScopeType.HINT> = {
  createEngine: ({ execCtx, engineCtx, initState }) => {
    if (initState === undefined)
      return execCtx.setErrCodeAndReturn(
        SockAPIErrorCode.PERMISSION_DENIED,
        null
      );

    const { controller } = engineCtx;
    const teamController = controller.getTeamControllerIfExists(
      initState.teamId
    );
    if (teamController === null)
      return execCtx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_TEAM, null);

    return new HintEngine(engineCtx, teamController, initState);
  },
  engineIdentityFunc: (engine) => engine,
};
