import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import {
  MiscLockContention,
  ServerHealthReport,
  SERVER_HEALTH_CONFIG,
} from "sockapi/scope-specs/server-health-spec";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";

class ServerHealthEngine {
  ctx: ScopeEngineContext<ScopeType.SERVER_HEALTH>;
  report?: ServerHealthReport;

  constructor(ctx: ScopeEngineContext<ScopeType.SERVER_HEALTH>) {
    this.ctx = ctx;
  }

  getPermissions() {
    return ScopePermissions.ADMIN_ONLY;
  }

  getMiscLockContentions() {
    const { server, controller } = this.ctx;
    const contentions: MiscLockContention[] = [];
    for (const engineTrackingStateForType of Object.values(
      controller.engines
    )) {
      for (const [scopeId, { stepLock }] of Object.entries(
        engineTrackingStateForType
      )) {
        if (stepLock === undefined) continue;
        contentions.push({
          id: scopeId,
          numActive: stepLock.getNumWaitingOrActive(),
        });
      }
    }
    for (const [teamId, lock] of server.teamInitLocks) {
      contentions.push({
        id: `team_init_${teamId}`,
        numActive: lock.getNumWaitingOrActive(),
      });
    }
    return contentions
      .filter(({ numActive }) => numActive > 0)
      .sort((a, b) => b.numActive - a.numActive)
      .slice(0, SERVER_HEALTH_CONFIG.maxNumMiscLockContentions);
  }

  updateReport(): ServerHealthReport {
    const { server } = this.ctx;
    this.report = {
      slowSyncMetrics: server.slowSyncMetrics.getSummary(),
      fastSyncMetrics: server.fastSyncMetrics.getSummary(),
      checkAnswerMetrics: server.checkAnswerMetrics.getSummary(),
      numAuthsMetrics: server.numAuthsMetrics.getSummary(),
      numRequestsMetrics: server.numRequestsMetrics.getSummary(),
      teamNumAuthsMetrics: server.teamMetricsTracker.numAuthsSummaries,
      teamNumRequestsMetrics: server.teamMetricsTracker.numRequestsSummaries,
      fastSyncQueueLength: server.syncThrottle.getNumWaitingOrActive(),
      numActivePeriodicTasksHandlers: server.numActivePeriodicTasksHandlers,
      numActiveConns: server.numActiveConns,
      downloadLockContention:
        server.slowSyncTracking.downloadLock.getNumWaitingOrActive(),
      syncLockContention: server.syncThrottle.getNumWaitingOrActive(),
      farmerLocksContention: 0,
      userTimeUsage: server.metricsDumpTracking.userTimeUsage,
      systemTimeUsage: server.metricsDumpTracking.systemTimeUsage,
      miscLocks: this.getMiscLockContentions(),
    };
    return this.report;
  }

  runPeriodicTasks() {
    const {
      server: { dirtyStateTracker },
    } = this.ctx;
    this.ctx.update(this.ctx.scope, {
      report: this.updateReport(),
      dirtyStateUpd: dirtyStateTracker.pendingDirtyStateUpd,
    });
    dirtyStateTracker.commitUpds();
  }

  getState(): SockAPIScopeState<ScopeType.SERVER_HEALTH> {
    const { server } = this.ctx;
    return {
      report: this.report ?? this.updateReport(),
      dirtyState: server.dirtyStateTracker.dirtyState,
    };
  }
}

export type ServerHealthServerScopeSpec = {
  engine: ServerHealthEngine;
};

export const serverHealthServerScopeHooks: ServerScopeHooks<ScopeType.SERVER_HEALTH> =
  {
    createEngine: ({ engineCtx }) => {
      return new ServerHealthEngine(engineCtx);
    },
    engineIdentityFunc: (engine) => engine,
  };
