import { KeysMatching } from "util/util";
import { Scope } from "sockapi/scopes";
import { SockAPIScopeType, SockAPIScopeState } from "sockapi/scope-state";
import {
  SockAPIUpdatableScopeType,
  SockAPIScopeUpdatePayload,
} from "sockapi/scope-updates";
import {
  SockAPISteppableScopeType,
  SockAPIScopeStepPayload,
  SockAPIScopeStep,
} from "sockapi/scope-steps";

import { ServerScopeSpecs } from "sock-server/server-scope-specs";
import { BackendScopeType } from "sockapi/backend-scope-state";
import { RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";
import { SockServer } from "sock-server/SockServer";
import { ScopeEnginesController } from "sock-server/scope-engines-controller";

type ScopeEngineDict = {
  [TScopeType in KeysMatching<
    ServerScopeSpecs,
    { engine: unknown }
  >]: ServerScopeSpecs[TScopeType]["engine"];
};
export type ScopeEngineType = keyof ScopeEngineDict;
export type ScopeEngine<TScopeType extends ScopeEngineType = ScopeEngineType> =
  ScopeEngineDict[TScopeType] & {
    init?(): void;
  };

export type SockAPIScopeEngineType = Extract<ScopeEngineType, SockAPIScopeType>;
type UpdatableScopeEngineType = Extract<
  ScopeEngineType,
  SockAPIUpdatableScopeType
>;
export type SteppableScopeEngineType = Extract<
  ScopeEngineType,
  SockAPISteppableScopeType
>;
export type BackendScopeEngineType = Extract<ScopeEngineType, BackendScopeType>;

export type ScopeEngineStep<
  TScope extends Scope<SteppableScopeEngineType> = Scope<SteppableScopeEngineType>
> = SockAPIScopeStep<TScope>;

export type ScopeEngineContext<TScopeType extends ScopeEngineType> = {
  scope: Scope<TScopeType>;
  server: SockServer;
  controller: ScopeEnginesController;
  // Utility functions to handle state updates.
  // - For updatable scopes, broadcasts the provided update.
  // - Marks the scope as dirty for automatic slow sync.
  // - Clears the cached state packet, if any.
  // This assumes that an automatic slow sync is needed whenever
  // the client-synced state is updated.
  // These functions take in the scope to play nice with typescript.
  assign: <TScopeType extends SockAPIScopeEngineType>(
    scope: Scope<TScopeType>,
    state: SockAPIScopeState<TScopeType>
  ) => void;
  update: <TScopeType extends UpdatableScopeEngineType>(
    scope: Scope<TScopeType>,
    payload: SockAPIScopeUpdatePayload<TScopeType>
  ) => void;
  /**
   * Utility function to mark the automatic slow sync state as dirty
   * without updating any client-synced state.
   */
  onModify: () => void;
  /** Fast sync a scope's state to the backend and wait for completion. */
  fastSyncAsync: <TScopeType extends BackendScopeEngineType>(
    scope: Scope<TScopeType>
  ) => Promise<void>;
};

export enum ScopePermissions {
  ADMIN_ONLY = "admin_only",
  READ = "read",
  FULL = "full",
}

export interface SockAPIScopeEngine<TScopeType extends SockAPIScopeEngineType> {
  getState(conn: ClientConnection): SockAPIScopeState<TScopeType>;
}
export interface SteppableScopeEngine<
  TScopeType extends SteppableScopeEngineType
> {
  handleStepAsync: (
    ctx: RequestContext,
    step: SockAPIScopeStepPayload<TScopeType>
  ) => Promise<void>;
}

export const doesScopeEngineHaveSlowSyncState = <
  TScopeType extends ScopeEngineType
>(
  engine: ScopeEngine<TScopeType>
): engine is Extract<
  ScopeEngine<TScopeType>,
  { getSlowSyncState: unknown }
> => {
  return "getSlowSyncState" in engine;
};
