import { ScopeType } from "sockapi/scope-types";
import { Scope } from "sockapi/scopes";
import {
  SockAPIScopeUpdatePayload,
  applyScopeUpdate,
} from "sockapi/scope-updates";
import { SockAPIScopeState } from "sockapi/scope-state";
import {
  AdminQueueTaskSummary,
  AdminQueuePuzzleSubmissionTaskSummary,
} from "sockapi/scope-specs/admin-queue-spec";
import {
  AdminQueueTaskType,
  AdminQueueTask,
} from "sockapi/scope-specs/admin-queue-task-spec";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { ExecContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";

class AdminQueueEngine {
  ctx: ScopeEngineContext<ScopeType.ADMIN_QUEUE>;
  state: SockAPIScopeState<ScopeType.ADMIN_QUEUE>;

  constructor(ctx: ScopeEngineContext<ScopeType.ADMIN_QUEUE>) {
    this.ctx = ctx;
    this.state = {
      tasks: {},
    };
  }

  getPermissions(conn: ClientConnection) {
    return ScopePermissions.ADMIN_ONLY;
  }

  getState() {
    return this.state;
  }

  applyAndBroadcastUpdate(
    payload: SockAPIScopeUpdatePayload<ScopeType.ADMIN_QUEUE>
  ) {
    applyScopeUpdate(ScopeType.ADMIN_QUEUE, this.state, payload);
    this.ctx.update(this.ctx.scope, payload);
  }

  /** Add a task if it does not already exist. */
  addTask(task: AdminQueueTask, taskSummary: AdminQueueTaskSummary): string {
    // Use timestamps as the task ID, since it should never change,
    // and we should be generating unique timestamps for everything.
    const taskId = taskSummary.requestTime.toString();
    const scope: Scope<ScopeType.ADMIN_QUEUE_TASK> = {
      type: ScopeType.ADMIN_QUEUE_TASK,
      taskId,
    };
    const { controller } = this.ctx;
    const taskEngine =
      controller.getEngineIfExists(scope) ??
      controller.createEngine(new ExecContext(), scope, { task });
    if (taskEngine === null)
      throw new Error(`failed to create task engine for ${taskId}`);
    this.applyAndBroadcastUpdate({
      setTasks: {
        [taskId]: taskSummary,
      },
    });
    taskEngine.notifyChangeToAdmin(true);
    return taskId;
  }

  addSubmissionTask(
    taskSummary: AdminQueuePuzzleSubmissionTaskSummary
  ): string {
    const { submissionType, teamId } = taskSummary;
    return this.addTask(
      {
        type: AdminQueueTaskType.PUZZLE_SUBMISSION,
        submissionType,
        teamId,
      },
      taskSummary
    );
  }

  updateTask(taskId: string, task: AdminQueueTaskSummary): void {
    const taskEngine = this.ctx.controller.getEngineIfExists({
      type: ScopeType.ADMIN_QUEUE_TASK,
      taskId,
    });
    if (taskEngine === undefined)
      throw new Error(`could not find task engine for ${taskId}`);
    this.applyAndBroadcastUpdate({
      setTasks: {
        [taskId]: task,
      },
    });

    // Keep the claimer when a task is closed for tracking purposes,
    // but clear the claimer if a task is reopened.
    if (task.respondTime !== undefined && task.respondTime === null)
      taskEngine.clearClaimer();

    taskEngine.notifyChangeToAdmin(false);
  }
}

export type AdminQueueServerScopeSpec = {
  engine: AdminQueueEngine;
};

export const adminQueueServerScopeHooks: ServerScopeHooks<ScopeType.ADMIN_QUEUE> =
  {
    createEngine: ({ engineCtx }) => {
      return new AdminQueueEngine(engineCtx);
    },
    engineIdentityFunc: (engine) => engine,
  };
