import { randomInRange } from "util/util";
import { ScopeType } from "sockapi/scope-types";
import { SockServer } from "sock-server/SockServer";
import { HintResponseType } from "sockapi/scope-specs/hint-spec";
import { HOPE_POOL_CONFIG } from "sockapi/scope-specs/hope-pool-spec";
import { Puzzle105StatusType } from "sockapi/scope-specs/team-puzzle-105-spec";
import { Puzzle139StatusType } from "sockapi/scope-specs/team-puzzle-139-spec";
import {
  PuzzleSubmissionType,
  AdminQueueTaskType,
} from "sockapi/scope-specs/admin-queue-task-spec";

const MOCK_HUNT_STAFF_CONFIG = {
  fullHopePoolClaimChance: 0.5,
  hopePoolActIntervalRangeMs: {
    min: 0,
    max: 15 * 60 * 1000, // 15 minutes
  },
  hopePoolNumAddRange: {
    min: 1,
    max: 5,
  },
};

export class MockHuntStaff {
  server: SockServer;
  nextHopePoolActTime?: number;

  constructor(server: SockServer) {
    this.server = server;
  }

  private runHopePoolTasks() {
    const hopePoolEngine = this.server.getOrCreateEngineOrThrow({
      type: ScopeType.HOPE_POOL,
    });
    const isPoolFull =
      hopePoolEngine.state.poolAmt >= HOPE_POOL_CONFIG.minToClaim;
    if (
      isPoolFull &&
      Math.random() < MOCK_HUNT_STAFF_CONFIG.fullHopePoolClaimChance
    ) {
      hopePoolEngine.advanceCycle();
      return;
    }
    hopePoolEngine.addHope(
      Math.floor(
        randomInRange(
          MOCK_HUNT_STAFF_CONFIG.hopePoolNumAddRange.min,
          MOCK_HUNT_STAFF_CONFIG.hopePoolNumAddRange.max
        )
      ) * HOPE_POOL_CONFIG.contributionAmt
    );
  }

  async runPeriodicTasksAsync() {
    const timeNow = this.server.clock.genTimestamp();

    if (timeNow < this.server.huntEndTime) return;

    if (this.server.opts.isPosthunt) {
      if (
        this.nextHopePoolActTime !== undefined &&
        timeNow >= this.nextHopePoolActTime
      ) {
        delete this.nextHopePoolActTime;
        this.runHopePoolTasks();
      }

      if (this.nextHopePoolActTime === undefined) {
        this.nextHopePoolActTime =
          timeNow +
          randomInRange(
            MOCK_HUNT_STAFF_CONFIG.hopePoolActIntervalRangeMs.min,
            MOCK_HUNT_STAFF_CONFIG.hopePoolActIntervalRangeMs.max
          );
      }
    }

    const adminQueueEngine = this.server.getOrCreateEngineOrThrow({
      type: ScopeType.ADMIN_QUEUE,
    });
    for (const [taskId, { respondTime }] of Object.entries(
      adminQueueEngine.state.tasks
    )) {
      if (respondTime !== undefined) continue;
      const { task } = this.server.getOrCreateEngineOrThrow({
        type: ScopeType.ADMIN_QUEUE_TASK,
        taskId,
      }).state;
      // Return something so that typescript will ensure it's exhaustive.
      await (async (): Promise<true> => {
        switch (task.type) {
          case AdminQueueTaskType.HINT: {
            const { hintId } = task;
            const hintEngine = this.server.getOrCreateEngineOrThrow({
              type: ScopeType.HINT,
              hintId,
            });
            await hintEngine.setResponseAsync({
              type: HintResponseType.ANSWERED,
              content:
                "We can't answer hints now that the hunt has ended, but we hope you'll figure it out soon!",
            });
            return true;
          }
          case AdminQueueTaskType.PUZZLE_SUBMISSION: {
            const { teamId, submissionType } = task;
            switch (submissionType) {
              case PuzzleSubmissionType.PUZZLE_105: {
                const engine = this.server.getOrCreateEngineOrThrow({
                  type: ScopeType.TEAM_PUZZLE_105,
                  teamId,
                });
                await engine.setStatusAsync({
                  type: Puzzle105StatusType.ACCEPTED,
                  moonickDialogue:
                    "Well, it meets the requirements. Of course, it won't be added to the pool.",
                  reviewTime: this.server.clock.genTimestamp(),
                  submitTime:
                    engine.state.status?.submitTime ??
                    this.server.clock.genTimestamp(),
                });
                return true;
              }
              case PuzzleSubmissionType.PUZZLE_139: {
                const engine = this.server.getOrCreateEngineOrThrow({
                  type: ScopeType.TEAM_PUZZLE_139,
                  teamId,
                });
                await engine.setStatusAsync({
                  type: Puzzle139StatusType.ACCEPTED,
                  comment:
                    "Hope you enjoyed a breath of fresh air! If you didn't, you should feel very obligated to get one now.",
                  reviewTime: this.server.clock.genTimestamp(),
                  submitTime:
                    engine.state.status?.submitTime ??
                    this.server.clock.genTimestamp(),
                });
                return true;
              }
              case PuzzleSubmissionType.PUZZLE_144: {
                const engine = this.server.getOrCreateEngineOrThrow({
                  type: ScopeType.TEAM_PUZZLE_144,
                  teamId,
                });
                await engine.setRespAsync({
                  text: "Thank you for your submission! Hope you took the question seriously. It'll become useful someday!",
                  numPoints: 4,
                });
                return true;
              }
            }
            break;
          }
        }
      })();
    }
  }
}
