import { PuzzleData } from "sockapi/puzzle-data";
import {
  SubmitAnswerResultType,
  SubmitAnswerResult,
  AnswerNormalization,
  normalizeAnswer,
} from "sockapi/answers";
import PUZZLE_CONFIG from "sock-server/puzzles/puzzle-config";
import { ordinalNumber } from "util/util";

const DEFAULT_CONFIRMATION_MESSAGE = "Keep going!";

const PUZ326_CONFIRMATION_MESSAGE =
  "Close, but the answer is actually LAST WORD.";

const PICTURE_GAME_ANSWER = normalizeAnswer(
  "ㄱㅋㆁㄷㅌㄴㅂㅍㅁㅈㅊㅅㆆㅎㅇㄹㅿㆍㅡㅣㅗㅏㅜㅓㅛㅑㅠㅕ",
  AnswerNormalization.UPPERCASE_LETTERS_OR_SYMBOLS_ONLY
);

const isLionEatingPoetTitle = (normalizedAnswer: string): boolean => {
  // Some attested titles:
  // "Lion-Eating Poet [in the Stone Den]"
  // "Story of Stone Grotto Poet Eating Lions"
  // "[The] Story/History of Mr Shi[h]['s] Eating Lions"
  return (
    normalizedAnswer.includes("LION") &&
    normalizedAnswer.includes("EATING") &&
    (normalizedAnswer.includes("POET") || normalizedAnswer.includes("SHI"))
  );
  // answer === "施氏食獅史" || answer === "施氏食狮史";
};

const getPictureGameMessage = (normalizedAnswer: string): string => {
  if (normalizedAnswer.length > PICTURE_GAME_ANSWER.length) {
    return "Your answer has too many characters.";
  }
  // we expect the case where we're missing exactly one character (and the
  // answer is otherwise correct) to be quite common; in that case we say
  // which character is missing
  if (normalizedAnswer.length === PICTURE_GAME_ANSWER.length - 1) {
    const missingIndices = [];
    for (let i = 0, j = 0; j < PICTURE_GAME_ANSWER.length; j++) {
      if (
        i < normalizedAnswer.length &&
        normalizedAnswer[i] === PICTURE_GAME_ANSWER[j]
      ) {
        i++;
      } else {
        missingIndices.push(j);
      }
    }
    if (missingIndices.length === 1) {
      return `The ${ordinalNumber(
        missingIndices[0] + 1
      )} character is missing.`;
    }
  }
  if (normalizedAnswer.length < PICTURE_GAME_ANSWER.length) {
    return "Your answer has too few characters.";
  }
  for (let i = 0; i < normalizedAnswer.length; i += 1) {
    if (normalizedAnswer[i] !== PICTURE_GAME_ANSWER[i]) {
      return `The ${ordinalNumber(i + 1)} character is incorrect.`;
    }
  }
  throw new Error("should not happen");
};

export const getCanonicalAnswers = (puzData: PuzzleData): string[] | null => {
  const { answer: answerSpec } = puzData;
  return answerSpec !== null
    ? answerSpec
        .split("/")
        .map((requiredAnswerSpec) => requiredAnswerSpec.split("|")[0].trim())
    : null;
};

const checkAnswerMatchAnyUnnormalized = (
  /** The submitted answer, already normalized. */
  submittedAnswer: string,
  answerNormalization: AnswerNormalization | undefined,
  correctAnswersUnnormalized: string[]
): string | null =>
  correctAnswersUnnormalized.find(
    (correctAnswerUnnormalized) =>
      normalizeAnswer(correctAnswerUnnormalized, answerNormalization) ===
      submittedAnswer
  ) ?? null;

/**
 * Checks if a single answer matches any of the possibilities
 * allowed in an answerSpec.
 * If the answer matches, returns the canonical matched answer.
 * Otherwise, returns null.
 */
const checkSingleAnswer = (
  /** The submitted answer, already normalized. */
  submittedAnswer: string,
  answerNormalization: AnswerNormalization | undefined,
  answerSpec: string
): string | null =>
  checkAnswerMatchAnyUnnormalized(
    submittedAnswer,
    answerNormalization,
    answerSpec.split("|").map((s) => s.trim())
  );

export const checkAnswer = (
  submittedAnswerUnnormalized: string,
  answerSpec: string,
  puzData: PuzzleData,
  puzzleMessages: { [answer: string]: string | null },
  previousAnswersUnnormalized: string[]
): SubmitAnswerResult => {
  const { puzName, answerNormalization } = puzData;
  const submittedAnswer = (() => {
    const normalizedAnswer = normalizeAnswer(
      submittedAnswerUnnormalized,
      answerNormalization
    );
    return (
      PUZZLE_CONFIG.puzzleAnswerMaps[puzName]?.[normalizedAnswer] ??
      normalizedAnswer
    );
  })();
  if (submittedAnswer.length <= 0)
    return { type: SubmitAnswerResultType.NO_ANSWER };

  const matchedPreviousAnswerUnnormalized = checkAnswerMatchAnyUnnormalized(
    submittedAnswer,
    answerNormalization,
    previousAnswersUnnormalized
  );
  if (puzName === PUZZLE_CONFIG.repeatAnswerPuzName) {
    const isCorrect = matchedPreviousAnswerUnnormalized !== null;
    return {
      type: SubmitAnswerResultType.SUCCESS,
      info: {
        answer: matchedPreviousAnswerUnnormalized ?? submittedAnswer,
        isCorrect,
        isWrong: !isCorrect,
      },
    };
  }
  if (matchedPreviousAnswerUnnormalized !== null)
    return {
      type: SubmitAnswerResultType.TRIED_BEFORE,
      submittedAnswer: matchedPreviousAnswerUnnormalized,
    };

  for (const [confirmationAnswerSpec, message] of Object.entries(
    puzzleMessages
  )) {
    const matchedConfirmationAnswerUnnormalized = checkSingleAnswer(
      submittedAnswer,
      answerNormalization,
      confirmationAnswerSpec
    );
    if (matchedConfirmationAnswerUnnormalized !== null)
      return {
        type: SubmitAnswerResultType.SUCCESS,
        info: {
          answer: matchedConfirmationAnswerUnnormalized,
          message: message ?? DEFAULT_CONFIRMATION_MESSAGE,
        },
      };
  }

  const rangeAnswerSpec = PUZZLE_CONFIG.rangeAnswers[puzName];
  if (rangeAnswerSpec !== undefined) {
    const submittedNumber = parseFloat(submittedAnswer);
    if (Number.isNaN(submittedNumber)) {
      return {
        type: SubmitAnswerResultType.NOT_NUMERICAL,
        submittedAnswer,
      };
    }
    const submittedAnswerRoundedString = new Intl.NumberFormat("en-US", {
      maximumFractionDigits: 2,
      useGrouping: false,
    }).format(submittedNumber);
    const submittedAnswerRounded = Number(submittedAnswerRoundedString);
    const isCorrect =
      submittedAnswerRounded >= rangeAnswerSpec.min &&
      submittedAnswerRounded <= rangeAnswerSpec.max;
    return {
      type: SubmitAnswerResultType.SUCCESS,
      info: {
        answer: submittedAnswerRoundedString,
        isCorrect,
        isWrong: !isCorrect,
        message:
          puzData.sequenceId === "google" && isCorrect
            ? "Correct! You may Google it now."
            : undefined,
      },
    };
  }

  const requiredAnswerSpecs = answerSpec.split("/").map((s) => s.trim());
  const matchedCorrectAnswerUnnormalized =
    requiredAnswerSpecs
      .map((requiredAnswerSpec) =>
        checkSingleAnswer(
          submittedAnswer,
          answerNormalization,
          requiredAnswerSpec
        )
      )
      .find((matchedRequiredAnswer) => matchedRequiredAnswer !== null) ?? null;
  if (matchedCorrectAnswerUnnormalized === null) {
    // Find the Answer VI
    if (puzData.puzName === "puz326") {
      return {
        type: SubmitAnswerResultType.SUCCESS,
        info: {
          answer: submittedAnswer,
          message: PUZ326_CONFIRMATION_MESSAGE,
        },
      };
    }
    // PictureGame
    if (puzData.puzName === "puz237") {
      return {
        type: SubmitAnswerResultType.SUCCESS,
        info: {
          answer: submittedAnswer,
          message: getPictureGameMessage(submittedAnswer),
          isWrong: true,
        },
      };
    }
    if (
      puzData.puzName === "ten_lions" &&
      isLionEatingPoetTitle(submittedAnswer)
    ) {
      return {
        type: SubmitAnswerResultType.SUCCESS,
        info: {
          answer: submittedAnswer,
          message: `石室诗士施氏，嗜狮，誓食十狮。
氏时时适市视狮。
十时，适十狮适市。
是时，适施氏适市。
氏视是十狮，恃矢势，使是十狮逝世。
氏拾是十狮尸，适石室。
石室湿，氏使侍拭石室。
石室拭，氏始试食是十狮。
食时，始识是十狮尸，实十石狮尸。
试释是事。`,
        },
      };
    }
    return {
      type: SubmitAnswerResultType.SUCCESS,
      info: {
        answer: submittedAnswer,
        isWrong: true,
      },
    };
  }

  if (requiredAnswerSpecs.length > 1) {
    const previousAnswers = previousAnswersUnnormalized.map(
      (previousAnswerUnnormalized) =>
        normalizeAnswer(previousAnswerUnnormalized, answerNormalization)
    );
    const isAllSatisfied =
      requiredAnswerSpecs.filter(
        (requiredAnswerSpec) =>
          ![submittedAnswer, ...previousAnswers].some(
            (answer) =>
              checkSingleAnswer(
                answer,
                answerNormalization,
                requiredAnswerSpec
              ) !== null
          )
      ).length <= 0;
    if (!isAllSatisfied)
      return {
        type: SubmitAnswerResultType.SUCCESS,
        info: {
          answer: matchedCorrectAnswerUnnormalized,
          message: `${matchedCorrectAnswerUnnormalized} is one of the answers to this puzzle!`,
        },
      };
  }

  return {
    type: SubmitAnswerResultType.SUCCESS,
    info: {
      answer: matchedCorrectAnswerUnnormalized,
      isCorrect: true,
    },
  };
};
