import {
  MapMap,
  objectEntriesUnsafe,
  objectNumberKeyEntriesUnsafe,
} from "util/util";
import { HopeSnapshot } from "sockapi/gacha/hope";

import {
  TeamProgressGlobalCounterEventType,
  TeamProgressEventType,
  TeamProgressEventRecord,
} from "sockapi/team-progress";
import { TeamProgressUpdateDiff } from "sock-server/team-progress/team-progress-diffs";
import {
  TeamHopePoolCycleContribution,
  getMergedTeamHopePoolCycleContributions,
} from "sock-server/scope-specs/team-hope-pool-server-spec";
import { CollectibleState } from "sockapi/scope-specs/team-collectibles-spec";

class TeamProgressView {
  /**
   * A snapshot that includes all events, to avoid recalculating this
   * value on every scope update. Reset during every update.
   */
  hopeSnapshot?: HopeSnapshot;
  /** Solve attempt timestamp-recod map sorted by puzName. */
  puzzleSolveAttempts: MapMap<
    string,
    number,
    TeamProgressEventRecord<TeamProgressEventType.SOLVE_ATTEMPT>
  >;
  /** The correct solve attempt for each puzzle, if any. */
  puzzleSolves: Map<
    string,
    TeamProgressEventRecord<TeamProgressEventType.SOLVE_ATTEMPT>
  >;
  puzzleSolveTimes: { [puzName: string]: number };
  /** Unlock event records, sorted by puzName. */
  puzzleUnlocks: Map<
    string,
    TeamProgressEventRecord<TeamProgressEventType.UNLOCK_PUZZLE>
  >;
  puzzleUnlockTimes: { [puzName: string]: number };
  unlockGroupsUnlockProgress: Map<string, number>;
  unlockGroupsSolveProgress: Map<string, number>;
  numSolvedMicropuzzles: number;
  displayedSolveCount: number;
  collectibleState: Map<string, CollectibleState>;
  puzzleNumExtraGuesses: Map<string, number>;
  puzzleNumTotalCannedHints: Map<string, number>;
  puzzleNumOpenedCannedHints: Map<string, number>;
  numWishesSinceLastHintToken?: number;
  lastHintTokenTime?: number;
  cutsceneChoices: Map<string, number>;
  firstCelestialPuzName?: string;
  nextUntitledPuzzleIndex: number;
  hopePoolLatestCycleContribution?: TeamHopePoolCycleContribution;
  teamGlobalCounters: {
    [eventType in TeamProgressGlobalCounterEventType]?: number;
  };

  constructor() {
    this.puzzleSolveAttempts = new MapMap();
    this.puzzleSolves = new Map();
    this.puzzleSolveTimes = {};
    this.puzzleUnlocks = new Map();
    this.puzzleUnlockTimes = {};
    this.unlockGroupsUnlockProgress = new Map();
    this.unlockGroupsSolveProgress = new Map();
    this.numSolvedMicropuzzles = 0;
    this.displayedSolveCount = 0;
    this.collectibleState = new Map();
    this.puzzleNumExtraGuesses = new Map();
    this.puzzleNumTotalCannedHints = new Map();
    this.puzzleNumOpenedCannedHints = new Map();
    this.teamGlobalCounters = {};
    this.cutsceneChoices = new Map();
    this.nextUntitledPuzzleIndex = 0;
  }

  /** Update view state in response to a team progress update diff. */
  handleDiff(diff: TeamProgressUpdateDiff) {
    if (diff.needInvalidateHopeSnapshot ?? false) delete this.hopeSnapshot;

    for (const [puzName, solveAttemptsForPuzzle] of Object.entries(
      diff.solveAttempts
    )) {
      for (const [timestamp, eventRecord] of objectNumberKeyEntriesUnsafe(
        solveAttemptsForPuzzle
      )) {
        if (eventRecord === null)
          this.puzzleSolveAttempts.delete(puzName, timestamp);
        else
          this.puzzleSolveAttempts.setIfAbsent(puzName, timestamp, eventRecord);
      }
    }
    for (const [puzName, eventRecord] of Object.entries(diff.solveRecords)) {
      if (eventRecord === null) {
        this.puzzleSolves.delete(puzName);
        delete this.puzzleSolveTimes[puzName];
      } else {
        this.puzzleSolves.set(puzName, eventRecord);
        this.puzzleSolveTimes[puzName] = eventRecord.timestamp;
      }
    }
    for (const [puzName, eventRecord] of Object.entries(diff.unlockRecords)) {
      if (eventRecord === null) {
        this.puzzleUnlocks.delete(puzName);
        delete this.puzzleUnlockTimes[puzName];
      } else {
        this.puzzleUnlocks.set(puzName, eventRecord);
        this.puzzleUnlockTimes[puzName] = eventRecord.timestamp;
      }
    }

    for (const [unlockGroupId, diffProgress] of Object.entries(
      diff.diffUnlockGroupsUnlockProgress
    )) {
      this.unlockGroupsUnlockProgress.set(
        unlockGroupId,
        (this.unlockGroupsUnlockProgress.get(unlockGroupId) ?? 0) + diffProgress
      );
    }
    for (const [unlockGroupId, diffProgress] of Object.entries(
      diff.diffUnlockGroupsSolveProgress
    )) {
      this.unlockGroupsSolveProgress.set(
        unlockGroupId,
        (this.unlockGroupsSolveProgress.get(unlockGroupId) ?? 0) + diffProgress
      );
    }
    this.numSolvedMicropuzzles += diff.diffNumSolvedMicropuzzles ?? 0;
    this.displayedSolveCount += diff.diffDisplayedSolveCount ?? 0;
    for (const [collectibleId, state] of Object.entries(
      diff.diffCollectibleState ?? {}
    )) {
      const existingState = this.collectibleState.get(collectibleId);
      if (existingState === undefined) {
        this.collectibleState.set(collectibleId, state);
      } else {
        existingState.num += state.num;
        existingState.timestamp = Math.min(
          existingState.timestamp,
          state.timestamp
        );
      }
    }

    for (const [puzName, diffNum] of Object.entries(
      diff.diffPuzzleNumExtraGuesses ?? {}
    )) {
      this.puzzleNumExtraGuesses.set(
        puzName,
        (this.puzzleNumExtraGuesses.get(puzName) ?? 0) + diffNum
      );
    }
    for (const [puzName, { diffTotal, diffOpened }] of Object.entries(
      diff.puzzleCannedHints ?? {}
    )) {
      this.puzzleNumTotalCannedHints.set(
        puzName,
        (this.puzzleNumTotalCannedHints.get(puzName) ?? 0) + diffTotal
      );
      this.puzzleNumOpenedCannedHints.set(
        puzName,
        (this.puzzleNumOpenedCannedHints.get(puzName) ?? 0) + diffOpened
      );
    }
    for (const [eventType, diffNum] of objectEntriesUnsafe(
      diff.diffTeamGlobalCounters ?? {}
    )) {
      if (diffNum === undefined) continue;
      this.teamGlobalCounters[eventType] =
        (this.teamGlobalCounters[eventType] ?? 0) + diffNum;
    }
    if (diff.numWishesSinceLastHintToken !== undefined)
      this.numWishesSinceLastHintToken = diff.numWishesSinceLastHintToken;
    if (diff.lastHintTokenTime !== undefined)
      this.lastHintTokenTime = diff.lastHintTokenTime;
    for (const [keyframeId, index] of Object.entries(
      diff.cutsceneChoices ?? {}
    )) {
      this.cutsceneChoices.set(keyframeId, index);
    }
    this.firstCelestialPuzName ??= diff.firstCelestialPuzName;
    if (diff.maxUntitledPuzzleIndex !== undefined)
      this.nextUntitledPuzzleIndex = Math.max(
        this.nextUntitledPuzzleIndex,
        diff.maxUntitledPuzzleIndex + 1
      );
    if (diff.hopePoolLatestCycleContribution !== undefined)
      this.hopePoolLatestCycleContribution =
        getMergedTeamHopePoolCycleContributions(
          this.hopePoolLatestCycleContribution,
          diff.hopePoolLatestCycleContribution
        );
  }

  getNumTotalHintTokens(): number {
    return this.teamGlobalCounters[TeamProgressEventType.GET_HINT_TOKEN] ?? 0;
  }

  getNumOliveWishes(): number {
    return this.teamGlobalCounters[TeamProgressEventType.GET_OLIVE_WISH] ?? 0;
  }

  getNumBlocksWishes(): number {
    return this.teamGlobalCounters[TeamProgressEventType.GET_BLOCKS_WISH] ?? 0;
  }
}

export default TeamProgressView;
