import { SockServer } from "sock-server/SockServer";
import { convertTeamProgressToUpdate } from "sock-server/team-progress/TeamProgressTracker";
import { doesHintUseToken } from "sockapi/scope-specs/hint-spec";
import { SockAPIScopeState } from "sockapi/scope-state";
import { ScopeType } from "sockapi/scope-types";
import { TeamProgressEventType } from "sockapi/team-progress";
import {
  TeamProgressEventUpdateType,
  TeamProgressUpdate,
  getTeamProgressUpdateForType,
} from "sockapi/team-progress-updates";

export class StatsController {
  server: SockServer;

  constructor(server: SockServer) {
    this.server = server;
  }

  private getAdminStatsEngine(puzName: string) {
    return this.server.getOrCreateEngineOrThrow({
      type: ScopeType.ADMIN_STATS,
      puzName,
    });
  }

  private getStatsEngine(puzName: string) {
    return this.server.getOrCreateEngineOrThrow({
      type: ScopeType.STATS,
      puzName,
    });
  }

  getHuntStatsEngine() {
    return this.server.getOrCreateEngineOrThrow({
      type: ScopeType.HUNT_STATS,
    });
  }

  private handleUpdate(teamId: string, teamProgressUpd: TeamProgressUpdate) {
    for (const {
      type,
      eventRecord: { timestamp, info },
    } of Object.values(
      getTeamProgressUpdateForType(
        teamProgressUpd,
        TeamProgressEventType.SOLVE_ATTEMPT
      )
    )) {
      if (timestamp >= this.server.huntEndTime) continue;
      const { puzName, answer = "", isCorrect = false, isWrong = false } = info;
      this.getAdminStatsEngine(puzName).handleSolveAttempt(
        type,
        timestamp,
        answer,
        isCorrect,
        isWrong
      );
      this.getStatsEngine(puzName).handleSolveAttempt(
        type,
        teamId,
        timestamp,
        isCorrect
      );
      this.getHuntStatsEngine().handleSolveAttempt(
        type,
        timestamp,
        answer,
        isCorrect
      );
    }
    for (const {
      type,
      eventRecord: { timestamp, info },
    } of Object.values(
      getTeamProgressUpdateForType(
        teamProgressUpd,
        TeamProgressEventType.UNLOCK_PUZZLE
      )
    )) {
      if (timestamp >= this.server.huntEndTime) continue;
      const { puzName } = info;
      this.getStatsEngine(puzName).handleUnlockPuzzle(type, teamId, timestamp);
    }
    for (const {
      type,
      eventRecord: { timestamp },
    } of Object.values(
      getTeamProgressUpdateForType(
        teamProgressUpd,
        TeamProgressEventType.SPIN_GACHA
      )
    )) {
      if (timestamp >= this.server.huntEndTime) continue;
      this.getHuntStatsEngine().handleSpinGacha(type, timestamp);
    }
    for (const {
      type,
      eventRecord: { timestamp, info },
    } of Object.values(
      getTeamProgressUpdateForType(
        teamProgressUpd,
        TeamProgressEventType.GET_CANNED_HINT
      )
    )) {
      if (timestamp >= this.server.huntEndTime) continue;
      const { puzName, num } = info;
      this.getStatsEngine(puzName).handleGetCannedHint(type, timestamp, num);
    }
    for (const {
      type,
      eventRecord: { timestamp, info },
    } of Object.values(
      getTeamProgressUpdateForType(
        teamProgressUpd,
        TeamProgressEventType.OPEN_CANNED_HINT
      )
    )) {
      if (timestamp >= this.server.huntEndTime) continue;
      const { puzName } = info;
      this.getStatsEngine(puzName).handleOpenCannedHint(type, timestamp);
    }
    for (const {
      type,
      eventRecord: { timestamp, info },
    } of Object.values(
      getTeamProgressUpdateForType(
        teamProgressUpd,
        TeamProgressEventType.GET_HINT_TOKEN
      )
    )) {
      if (timestamp >= this.server.huntEndTime) continue;
      const { num } = info;
      this.getHuntStatsEngine().handleGetHintToken(type, timestamp, num);
    }
    for (const {
      type,
      eventRecord: { timestamp, info },
    } of Object.values(
      getTeamProgressUpdateForType(
        teamProgressUpd,
        TeamProgressEventType.GET_COLLECTIBLE
      )
    )) {
      if (timestamp >= this.server.huntEndTime) continue;
      const { collectibleId, num } = info;
      this.getHuntStatsEngine().handleGetCollectible(
        type,
        timestamp,
        collectibleId,
        num
      );
    }
  }

  private handleMembers(teamId: string, type: TeamProgressEventUpdateType) {
    const { members, creationTime } = this.server.getTeamController(teamId);
    if (creationTime >= this.server.huntEndTime) return;
    this.getHuntStatsEngine().handleMembers(type, teamId, members.length);
  }

  private handleHint(
    hint: SockAPIScopeState<ScopeType.HINT>,
    type: TeamProgressEventUpdateType
  ) {
    const { teamId, puzName, request, response } = hint;
    const { timestamp } = request;
    if (timestamp >= this.server.huntEndTime) return;
    const usesToken = doesHintUseToken(request, response);
    this.getStatsEngine(puzName).handleHint(type, teamId, timestamp, usesToken);
  }

  private processTeam(teamId: string, type: TeamProgressEventUpdateType) {
    const teamController = this.server.getTeamController(teamId);
    const { state } = teamController.progressTracker;
    this.handleUpdate(teamId, convertTeamProgressToUpdate(state, type));
    this.handleMembers(teamId, type);
    const { hints } = teamController.getHintsEngine().getState();
    for (const hintIds of Object.values(hints))
      for (const hintId of Object.keys(hintIds)) {
        const engine = this.server.scopeEnginesController.getEngineIfExists({
          type: ScopeType.HINT,
          hintId,
        });
        if (engine) this.handleHint(engine.getState(), type);
      }
  }

  registerTeam(teamId: string) {
    if (this.server.getTeamController(teamId).isHidden) return;
    this.processTeam(teamId, TeamProgressEventUpdateType.ADD);
  }

  handleTeamUpdates(teamId: string, teamProgressUpd: TeamProgressUpdate) {
    if (this.server.getTeamController(teamId).isHidden) return;
    this.handleUpdate(teamId, teamProgressUpd);
  }

  hideTeam(teamId: string) {
    this.processTeam(teamId, TeamProgressEventUpdateType.REMOVE);
  }

  unhideTeam(teamId: string) {
    this.processTeam(teamId, TeamProgressEventUpdateType.ADD);
  }

  setMembers(teamId: string) {
    if (this.server.getTeamController(teamId).isHidden) return;
    this.handleMembers(teamId, TeamProgressEventUpdateType.ADD);
  }

  addHint(hint: SockAPIScopeState<ScopeType.HINT>) {
    if (this.server.getTeamController(hint.teamId).isHidden) return;
    this.handleHint(hint, TeamProgressEventUpdateType.ADD);
  }
}
