import { PuzzlesDB } from "sock-server/puzzles/PuzzlesDB";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeUpdatePayload } from "sockapi/scope-updates";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import {
  AdminModifyTeamParams,
  comparePuzzlesDefaultUnlockOrder,
} from "sockapi/scope-specs/hunt-admin-spec";
import { actualHope } from "sockapi/gacha/hope";
import {
  TeamProgressEventType,
  TeamProgressEventReasonType,
  TeamProgressEventReason,
} from "sockapi/team-progress";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { RequestContext } from "sock-server/exec-contexts";

class HuntAdminEngine {
  ctx: ScopeEngineContext<ScopeType.HUNT_ADMIN>;

  constructor(ctx: ScopeEngineContext<ScopeType.HUNT_ADMIN>) {
    this.ctx = ctx;
  }

  getPermissions() {
    return ScopePermissions.ADMIN_ONLY;
  }

  getState() {
    const { server } = this.ctx;
    return {
      teamAdminData: Object.fromEntries(
        Array.from(server.teamControllers.values(), (teamController) => [
          teamController.teamId,
          teamController.getClientAdminData(),
        ])
      ),
      puzzles: Object.fromEntries(
        Array.from(server.getPuzzles(), (puzData) => [puzData.puzName, puzData])
      ),
      numCannedHints: Object.fromEntries(
        Array.from(
          server.getCannedHints(),
          ([puzName, cannedHintsForPuzzle]) => [
            puzName,
            cannedHintsForPuzzle.length,
          ]
        )
      ),
      errata: Object.fromEntries(
        Object.values(
          server.scopeEnginesController.getEnginesForType(
            ScopeType.ERRATUM_CONTENT
          )
        ).map((engine) => [engine.ctx.scope.erratumId, engine.getSummary()])
      ),
    };
  }

  broadcastUpdate(
    payload: SockAPIScopeUpdatePayload<ScopeType.HUNT_ADMIN>
  ): void {
    this.ctx.update(this.ctx.scope, payload);
  }

  addTeam(teamId: string) {
    const { server } = this.ctx;
    const teamController = server.getTeamController(teamId);
    this.ctx.update(this.ctx.scope, {
      addTeams: { [teamId]: teamController.getClientAdminData() },
    });
  }

  refresh() {
    this.ctx.assign(this.ctx.scope, this.getState());
  }

  async modifyTeamAsync(
    teamId: string,
    params: AdminModifyTeamParams,
    reasonOpt?: TeamProgressEventReason
  ) {
    const { server } = this.ctx;
    const teamController = server.getTeamController(teamId);
    const reason = reasonOpt ?? {
      type: TeamProgressEventReasonType.MISC,
    };
    const {
      isInactive,
      isHidden,
      isTempBlocked,
      displayName,
      overrideSolved,
      overrideUnlocked,
      overridePuzzleNumExtraGuesses,
      overridePuzzleNumTotalCannedHints,
      overrideNumTotalHintTokens,
      overrideHope,
      overrideNumOliveWishes,
      overrideNumBlocksWishes,
    } = params;
    if (isInactive !== undefined) teamController.setTeamInactive(isInactive);
    if (isHidden !== undefined) teamController.setTeamHidden(isHidden);
    if (isTempBlocked !== undefined)
      teamController.setTeamTempBlocked(isTempBlocked);
    // Disallow team names that generate invalid URLs.
    if (displayName !== undefined && !["", ".", ".."].includes(displayName))
      teamController.setDisplayName(displayName);

    await teamController.modifyTeamProgressAsync((progressTracker) => {
      for (const [puzName, isSolved] of Object.entries(overrideSolved ?? {})) {
        if (isSolved) progressTracker.addSolvePuzzle(puzName);
        else progressTracker.removeSolvePuzzle(puzName);
      }
      for (const [puzName, isUnlocked] of Object.entries(
        overrideUnlocked ?? {}
      ).sort(([puzName1], [puzName2]) => {
        // Sort manual unlocks so that they appear in a consistent
        // order in the sidebar for posthunt.
        const puz1 = server.getPuzzle(puzName1);
        const puz2 = server.getPuzzle(puzName2);
        return comparePuzzlesDefaultUnlockOrder(puz1, puz2);
      })) {
        if (isUnlocked) progressTracker.addUnlockPuzzle(puzName);
        else progressTracker.removeUnlockPuzzle(puzName);
      }
      for (const [puzName, newNum] of Object.entries(
        overridePuzzleNumExtraGuesses ?? {}
      )) {
        const currNum =
          progressTracker.view.puzzleNumExtraGuesses.get(puzName) ?? 0;
        const diffNum = newNum - currNum;
        if (diffNum === 0) continue;
        progressTracker.addEvent(TeamProgressEventType.GET_EXTRA_GUESSES, {
          puzName,
          num: diffNum,
          reason,
        });
      }
      for (const [puzName, newNum] of Object.entries(
        overridePuzzleNumTotalCannedHints ?? {}
      )) {
        progressTracker.overridePuzzleNumCannedHintsFound(
          puzName,
          newNum,
          reason
        );
      }
      if (overrideNumTotalHintTokens !== undefined) {
        const currNumTotal = progressTracker.view.getNumTotalHintTokens();
        const diffNumTotal = overrideNumTotalHintTokens - currNumTotal;

        if (diffNumTotal === 0) return;
        progressTracker.addEvent(TeamProgressEventType.GET_HINT_TOKEN, {
          num: diffNumTotal,
          reason,
        });
      }
      if (overrideHope !== undefined) {
        const currHope = actualHope(
          progressTracker.getTotalHope(),
          server.clock.genTimestamp()
        );
        const diffHope = overrideHope - currHope;

        if (diffHope === 0) return;
        progressTracker.addEvent(TeamProgressEventType.GET_HOPE, {
          hopeAmt: diffHope,
          reason,
        });
      }
      if (overrideNumOliveWishes !== undefined) {
        const currNumTotal = progressTracker.view.getNumOliveWishes();
        const diffNumTotal = overrideNumOliveWishes - currNumTotal;

        if (diffNumTotal === 0) return;
        progressTracker.addEvent(TeamProgressEventType.GET_OLIVE_WISH, {
          num: diffNumTotal,
          reason,
        });
      }
      if (overrideNumBlocksWishes !== undefined) {
        const currNumTotal = progressTracker.view.getNumBlocksWishes();
        const diffNumTotal = overrideNumBlocksWishes - currNumTotal;

        if (diffNumTotal === 0) return;
        progressTracker.addEvent(TeamProgressEventType.GET_BLOCKS_WISH, {
          num: diffNumTotal,
          reason,
        });
      }
    });
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.HUNT_ADMIN>
  ) {
    const { server } = this.ctx;
    if (payload.config !== undefined)
      Object.assign(server.config, payload.config);
    for (const [teamId, teamUpds] of Object.entries(payload.teams ?? {})) {
      await this.modifyTeamAsync(teamId, teamUpds);
    }
    if (payload.reloadPuzzles ?? false) await server.loadPuzzlesAsync();
    if (payload.setPuzzlesDB !== undefined)
      server.setPuzzlesDB(new PuzzlesDB(payload.setPuzzlesDB));
    if (payload.setForcedTimeOffset !== undefined)
      server.clock.setForcedTime(
        server.huntStartTime + payload.setForcedTimeOffset
      );
    reqCtx.sendStepResp(this.ctx.scope, "");
  }
}

export type HuntAdminServerScopeSpec = {
  engine: HuntAdminEngine;
};

export const huntAdminServerScopeHooks: ServerScopeHooks<ScopeType.HUNT_ADMIN> =
  {
    createEngine: ({ engineCtx }) => {
      return new HuntAdminEngine(engineCtx);
    },
    engineIdentityFunc: (engine) => engine,
  };
