import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { BackendScopeState } from "sockapi/backend-scope-state";
import { ClientConnection } from "sock-server/client-connections";

class HopePoolEngine {
  ctx: ScopeEngineContext<ScopeType.HOPE_POOL>;
  state: SockAPIScopeState<ScopeType.HOPE_POOL>;
  cycleId: number;

  constructor(
    ctx: ScopeEngineContext<ScopeType.HOPE_POOL>,
    initState?: BackendScopeState<ScopeType.HOPE_POOL>
  ) {
    this.ctx = ctx;
    this.state = {
      poolAmt: initState?.poolAmt ?? 0,
    };
    this.cycleId = initState?.cycleId ?? 0;
  }

  getPermissions(conn: ClientConnection): ScopePermissions {
    return ScopePermissions.READ;
  }

  getState(): SockAPIScopeState<ScopeType.HOPE_POOL> {
    return this.state;
  }

  getSlowSyncState(): BackendScopeState<ScopeType.HOPE_POOL> {
    const { poolAmt } = this.state;
    const { cycleId } = this;
    return { poolAmt, cycleId };
  }

  addHope(amt: number) {
    this.state.poolAmt += amt;
    this.ctx.assign(this.ctx.scope, this.getState());
  }

  advanceCycle() {
    this.state.poolAmt = 0;
    this.cycleId++;
    for (const engine of Object.values(
      this.ctx.controller.getEnginesForType(ScopeType.TEAM_HOPE_POOL)
    )) {
      engine.broadcastAssign();
    }
    this.ctx.assign(this.ctx.scope, this.getState());
  }
}

export type HopePoolServerScopeSpec = {
  backendState: {
    poolAmt: number;
    cycleId: number;
  };
  engine: HopePoolEngine;
};

export const hopePoolServerScopeHooks: ServerScopeHooks<ScopeType.HOPE_POOL> = {
  createEngine: ({ execCtx, engineCtx, initState }) => {
    return new HopePoolEngine(engineCtx, initState);
  },
  engineIdentityFunc: (engine) => engine,
};
