import { AnswerNormalization } from "sockapi/answers";

/**
 * Puzzles where the canonical answer is not normalized,
 * to exclude from the answer normalization import check.
 */
const answerNormalizations: {
  [puzName: string]: AnswerNormalization;
} = {
  /** Go home! */
  puz207: AnswerNormalization.UPPERCASE_LETTERS_ONLY,
  /** 50-50 puzzle */
  puz233: AnswerNormalization.UPPERCASE_LETTERS_ONLY,
  /** PictureGame */
  puz237: AnswerNormalization.UPPERCASE_LETTERS_OR_SYMBOLS_ONLY,
};
const rangeAnswers: {
  [puzName: string]: { min: number; max: number };
} = {
  puz136: { min: 1, max: 6 },
  puz177: { min: 0, max: 20 },
};

/**
 * Fake puzzle items that are used to track content, mapped to the
 * puzzle that their unlocks are dependent on.
 */
const contentOnlyPuzzles: { [puzName: string]: string } = {
  "puz212.1": "puz212",
};

/**
 * Mappings for post-normalized answers. This lets us treat certain
 * answers as other answers, as a pseudo-normalization step.
 * Mapped answers should be normalized.
 */
const puzzleAnswerMaps: {
  [puzName: string]: {
    [answer: string]: string;
  };
} = {
  ["puz284"]: { JASON: "JASONVOORHEES" },
};

const PUZZLE_CONFIG = {
  finalMetaPuzName: "night_of_the_celestials",
  unlockPrizePuzNames: ["puz146", "puz189"],
  hintPrizePuzNames: ["puz231"],
  hintsDisabledUntilAllCannedHintsFoundPuzNames: ["puz266", "puz322"],
  repeatAnswerPuzName: "puz260",
  scrambleSequelPuzName: "puz173",
  cutscenePuzNames: {
    intro: "intro",
    firstUnlock: "first-wish",
    firstSolve: "first-solve",
    introCelestials: "brighter-skies",
    final: "the_end",
  },
  contentOnlyPuzzles,
  answerNormalizations,
  rangeAnswers,
  puzzleAnswerMaps,
};
export default PUZZLE_CONFIG;
