type WeightedChoice<T> = {
  weight: number;
  choice: T;
};

export function weightedSelectWithIndex<T>(choices: WeightedChoice<T>[]): {
  choice: T;
  index: number;
} | null {
  if (choices.length === 0) return null;
  const partialSums: number[] = [];
  choices.reduce((partialSum, choice) => {
    const newPartialSum = partialSum + choice.weight;
    partialSums.push(newPartialSum);
    return newPartialSum;
  }, 0);
  const randomChoice = Math.random() * partialSums[partialSums.length - 1];
  for (let i = 0; i < choices.length; i++) {
    if (randomChoice < partialSums[i]) {
      return { choice: choices[i].choice, index: i };
    }
  }
  return {
    choice: choices[choices.length - 1].choice,
    index: choices.length - 1,
  };
}

export function weightedSelect<T>(choices: WeightedChoice<T>[]): T | null {
  return weightedSelectWithIndex(choices)?.choice ?? null;
}

/**
 * Selects from a list of weighted children, where some children may
 * be invalid. In such cases, keep rerunning with the invalid choices
 * removed, until we get a result.
 */
export function forkSelect<T>(
  choices: WeightedChoice<() => T | null>[]
): T | null {
  while (true) {
    const result = weightedSelectWithIndex(choices);
    if (result === null) return null;
    const choice = result.choice();
    if (choice !== null) return choice;
    choices.splice(result.index, 1);
  }
}
