import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import {
  SockAPIScopeUpdatePayload,
  applyScopeUpdate,
} from "sockapi/scope-updates";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import {
  HintResponseType,
  doesHintUseToken,
} from "sockapi/scope-specs/hint-spec";
import {
  SubmitHintResultType,
  checkSubmitHint,
  isPuzzleEligibleForHint,
  getNumUsedHintTokensFromScopeState,
  HINT_REQUEST_MAX_LENGTH,
} from "sockapi/scope-specs/team-hints-spec";
import { TeamProgressEventType } from "sockapi/team-progress";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { ExecContext, RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";
import { TeamProgressUpdateDiff } from "sock-server/team-progress/team-progress-diffs";
import { TeamController } from "sock-server/server-controllers/team-controller";

class TeamHintsEngine {
  ctx: ScopeEngineContext<ScopeType.TEAM_HINTS>;
  teamController: TeamController;
  state: SockAPIScopeState<ScopeType.TEAM_HINTS>;

  constructor(
    ctx: ScopeEngineContext<ScopeType.TEAM_HINTS>,
    teamController: TeamController,
    initState?: SockAPIScopeState<ScopeType.TEAM_HINTS>
  ) {
    this.ctx = ctx;
    this.teamController = teamController;
    this.state = initState ?? {
      hints: {},
      numTotalTokens: 0,
      puzzleNumUsedTokens: {},
      openHints: {},
    };
  }

  getPermissions(conn: ClientConnection) {
    return conn.teamId === this.ctx.scope.teamId
      ? ScopePermissions.FULL
      : ScopePermissions.ADMIN_ONLY;
  }

  getState() {
    return this.state;
  }

  applyUpdate(payload: SockAPIScopeUpdatePayload<ScopeType.TEAM_HINTS>) {
    const {
      server,
      scope: { teamId },
    } = this.ctx;

    applyScopeUpdate(ScopeType.TEAM_HINTS, this.state, payload);
    this.ctx.update(this.ctx.scope, payload);
    server.bigBoardStateTracker.updateTeam(teamId, {
      numHintsTotal: payload.setNumTotalTokens,
      unlocks: Object.fromEntries(
        Object.entries(payload.updPuzzleNumUsedTokens ?? {}).map(
          ([puzName, numHintsUsed]) => [puzName, { numHintsUsed }]
        )
      ),
    });
  }

  getNumUnusedHintTokens() {
    return (
      this.state.numTotalTokens - getNumUsedHintTokensFromScopeState(this.state)
    );
  }

  addHint(hintId: string, hint: SockAPIScopeState<ScopeType.HINT>): string {
    const { server } = this.ctx;
    const { puzName } = hint;

    const hintEngine = server.scopeEnginesController.createEngine(
      new ExecContext(),
      {
        type: ScopeType.HINT,
        hintId,
      },
      hint
    );
    if (hintEngine === null) throw new Error("failed to create hint engine");

    const usesToken = doesHintUseToken(hint.request, hint.response);
    this.applyUpdate({
      addHint: { puzName, hintId },
      updPuzzleNumUsedTokens: usesToken
        ? { [puzName]: (this.state.puzzleNumUsedTokens[puzName] ?? 0) + 1 }
        : undefined,
      updOpenHints: hint.response === null ? { [puzName]: true } : undefined,
    });
    server.statsController.addHint(hint);

    return hintId;
  }

  updatePuzzle(puzName: string, numUsedTokens: number, isOpen: boolean) {
    this.applyUpdate({
      updPuzzleNumUsedTokens: { [puzName]: numUsedTokens },
      updOpenHints: { [puzName]: isOpen },
    });
  }

  handleTeamProgressUpdate(diff: TeamProgressUpdateDiff): void {
    if (
      diff.diffTeamGlobalCounters?.[TeamProgressEventType.GET_HINT_TOKEN] !==
      undefined
    ) {
      this.applyUpdate({
        setNumTotalTokens:
          this.teamController.progressTracker.view.getNumTotalHintTokens(),
      });
    }

    for (const hintsForPuzzle of Object.values(this.state.hints)) {
      for (const hintId of Object.keys(hintsForPuzzle)) {
        const hintEngine =
          this.ctx.server.scopeEnginesController.getEngineIfExists({
            type: ScopeType.HINT,
            hintId,
          });
        if (hintEngine === undefined)
          throw new Error(`could not find engine for hint ${hintId}`);

        // Filter for unanswered hints for newly solved puzzles.
        const hint = hintEngine.state;
        if (hint.response !== null || !(diff.solves[hint.puzName] ?? false))
          continue;

        hintEngine.handleResponse({
          type: HintResponseType.OBSOLETE,
          content: "",
        });
      }
    }
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.TEAM_HINTS>
  ) {
    const {
      server,
      scope: { teamId },
    } = this.ctx;
    const { puzName, req } = payload;

    const puzData = server.tryGetPuzzle(puzName);
    if (puzData === null || !isPuzzleEligibleForHint(puzData)) {
      return reqCtx.setErrCode(SockAPIErrorCode.INVALID_PUZZLE);
    }

    if (req.content.length > HINT_REQUEST_MAX_LENGTH)
      return reqCtx.setBadRequest();

    const numUnusedHintTokens = this.getNumUnusedHintTokens();
    const { openHints } = this.state;
    const openHintPuzName = Object.keys(openHints)[0] ?? null;

    const res = checkSubmitHint(
      server.isHuntClosed(),
      server.opts.isPosthunt ?? false,
      numUnusedHintTokens,
      openHintPuzName,
      req.isFollowup ?? false
    );
    if (res.type === SubmitHintResultType.SUCCESS) {
      const hint = {
        teamId,
        puzName,
        request: {
          ...req,
          timestamp: server.clock.genTimestamp(),
        },
        response: null,
      };
      const hintId = this.teamController.genHintId();
      this.addHint(hintId, hint);
      this.teamController.mergeSlowSyncPacket({
        hints: { [hintId]: hint },
      });
    }
    reqCtx.sendStepResp(this.ctx.scope, res);
  }
}

export type TeamHintsServerScopeSpec = {
  engine: TeamHintsEngine;
};

export const teamHintsServerScopeHooks: ServerScopeHooks<ScopeType.TEAM_HINTS> =
  {
    createEngine: ({ execCtx, engineCtx, initState }) => {
      const { controller, scope } = engineCtx;
      const teamController = controller.getTeamControllerIfExists(scope.teamId);
      if (teamController === null)
        return execCtx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_TEAM, null);
      return new TeamHintsEngine(engineCtx, teamController, initState);
    },
    engineIdentityFunc: (engine) => engine,
  };
