import { AsyncLock } from "util/locks";
import { SockAPIErrorCode, SockAPIError } from "sockapi/errors";
import {
  SockAPIStepResponseScope,
  SockAPIScopeStepResponsePayload,
} from "sockapi/scope-steps";
import { WSRespType, WSResp } from "sockapi/sockapi-messages";
import { SockServer } from "sock-server/SockServer";

export type ClientInterface = {
  send: (msg: string) => void;
  close: () => void;
};

export class RawClientConnection {
  private server: SockServer;

  connId: string;
  private client: ClientInterface;
  // teamId only gets set once the connection is authenticated,
  // so it can be used to check if a connection is authenticated.
  teamId?: string;
  spectateTeamId?: string;
  /**
   * Whether the connection is an admin connection. An admin can be
   * connected as a non-admin team through impersonation.
   */
  isAdmin: boolean;

  /**
   * The cursor group that the client is in, denoted by puzName.
   * A client can only be part of one cursor group at a time.
   */
  cursorGroupName?: string;

  /**
   * Lock to ensure that we only process one request from each
   * connection at a time, to prevent requests from contending for
   * transient state.
   */
  handleMsgLock: AsyncLock;
  /**
   * Transient state. The stepId of the step that is currently
   * being handled, if any.
   */
  activeStepId?: string;

  constructor(server: SockServer, connId: string, client: ClientInterface) {
    this.server = server;
    this.connId = connId;
    this.client = client;
    // The connection only ever gets marked as admin after auth.
    this.isAdmin = false;
    this.handleMsgLock = new AsyncLock();
  }

  close() {
    this.client.close();
  }

  sendRaw(msg: string) {
    this.client.send(msg);
  }

  send(resp: WSResp): void {
    this.sendRaw(JSON.stringify(resp));
  }

  /**
   * Directly send an error to the client. Should only be used when
   * the error is not triggered by the client we are sending the error to.
   */
  sendErr(err: SockAPIError) {
    this.send({
      type: WSRespType.ERROR,
      err,
      stepId: this.activeStepId,
    });
    delete this.activeStepId;
  }

  /**
   * Directly send an error code to the client. Should only be used when
   * the error is not triggered by the client we are sending the error to.
   */
  sendErrCode(errCode: SockAPIErrorCode) {
    this.sendErr({ errCode });
  }

  sendStepResp<TScope extends SockAPIStepResponseScope>(
    scope: TScope,
    payload: SockAPIScopeStepResponsePayload<TScope["type"]>
  ): void {
    // If activeStepId is undefined, then the client is not waiting
    // for a step response. We don't currently detect if an engine
    // erroneously sends a duplicate step response.
    if (this.activeStepId === undefined) return;
    this.send({
      type: WSRespType.SCOPE_STEP_RESPONSE,
      stepResponse: {
        scope,
        payload,
      },
      stepId: this.activeStepId,
    });
    delete this.activeStepId;
  }
}

/** An authenticated client connection. */
export type ClientConnection = RawClientConnection & {
  teamId: string;
};

export const isConnAuthed = (
  conn: RawClientConnection
): conn is ClientConnection => {
  return conn.teamId !== undefined;
};
