import {
  HuntJwtPayload,
  AuthenticationInterface,
} from "sock-server/authentication-interface/AuthenticationInterface";

export class MockAuthenticationInterface implements AuthenticationInterface {
  verify(jwt: string): HuntJwtPayload | null {
    // In mock authentication, just use the payload directly as the JWT.
    return JSON.parse(jwt) as HuntJwtPayload;
  }
}
