/**
 * Server-wide clock to generate unique timestamps.
 * We may extend this later to provide clock manipulation utilities
 * for testing purposes.
 */
export class SockServerClock {
  latestTime: number;
  forcedTime?: number;

  constructor() {
    this.latestTime = 0;
  }

  genTimestamp(): number {
    const timestamp = Math.max(
      this.forcedTime ?? Date.now(),
      this.latestTime + 1
    );
    this.bumpLatestTime(timestamp);
    return timestamp;
  }

  bumpLatestTime(timestamp: number) {
    this.latestTime = Math.max(timestamp, this.latestTime);
  }

  setForcedTime(timestamp: number) {
    this.forcedTime = timestamp;
    this.latestTime = 0;
  }
}

const makeDefaultSockServerConfig = (): SockServerConfig => ({
  miscTeamStateTeamsSyncLimit: 10,
  syncThrottleParallelism: 2,
  scopesSyncLimit: 10,
  slowSyncThrottle: {
    [SyncObjectType.FAST]: 0,
    [SyncObjectType.MISC]: 0,
  },
});

type SockServerOpts = {
  backend: BackendInterface;
  dumpWsMessages: boolean;
  dumpBackendRequests: boolean;
  loadPuzzlesDBAsync: () => Promise<PuzzlesDB>;
  periodicSyncInterval?: number;
  authInterface: AuthenticationInterface;
  isWSReq: (req: object) => req is WSReq;
  cursorsMode?: boolean;
  logMetrics?: (msg: string) => Promise<void>;
  logCursorsMetrics?: (msg: string) => Promise<void>;
  metricsDumpInterval?: number;
  getCpuUsage?: () => {
    user: number;
    system: number;
  };
  enableDevFeatures?: boolean;
  logRequestErrDebugInfoAsync?: (msg: string) => Promise<void>;
  farmerServerInterface: FarmerServerInterface;
  logWord2Vec?: boolean;
  adminNotifier?: AdminNotifier;
  forceHuntEnd?: boolean;
  /**
   * Only for use when we want the posthunt behavior to be
   * different for logged-in teams than for public access.
   */
  isPosthunt?: boolean;
};

export class SockServer {
  /** Unique identifier from the backend for this instance. */
  sockServerId: string;
  puzzlesDB: PuzzlesDB;
  opts: SockServerOpts;
  /**
   * Functions to be called once the SockServer is ready to
   * accept connections.
   */
  onReadyCallbacks: (() => void)[];

  // Static hunt config synced from Django on startup.

  maxGuessesPerPuzzle: number;
  huntStartTime: number;
  huntEndTime: number;
  huntCloseTime: number;

  backend: BackendInterfaceController;
  clock: SockServerClock;
  numActiveConns: number;
  /**
   * Map of team ID to the client connections authenticated for it,
   * indexed by connId.
   */
  teamConns: Map<string, Map<string, ClientConnection>>;
  /** Map of team ID to team controller. */
  teamControllers: Map<string, TeamController>;
  scopeEnginesController: ScopeEnginesController;
  statsController: StatsController;
  cursorsController: CursorsController;
  subscriptionsTracker: SubscriptionsTracker;
  dirtyStateTracker: ServerDirtyStateTracker;
  bigBoardStateTracker: BigBoardStateTracker;
  teamMetricsTracker: TeamMetricsTracker;
  farmerServerInterface: FarmerServerInterface;
  mockHuntStaff?: MockHuntStaff;

  fastSyncMetrics: IntervalMetrics;
  slowSyncMetrics: IntervalMetrics;
  checkAnswerMetrics: IntervalMetrics;
  cursorEventsMetrics: IntervalMetrics;
  numAuthsMetrics: IntervalMetrics;
  numRequestsMetrics: IntervalMetrics;

  metricsDumpTracking: {
    lastDumpTime: number;
    lastUserTime: number;
    lastSystemTime: number;
    userTimeUsage: number;
    systemTimeUsage: number;
  };

  /** Whether a init backend request is pending. */
  isInitPending: boolean;
  /** Whether the server is ready to accept requests. */
  isReady: boolean;
  /** Whether the server has been torn down. */
  isStopped: boolean;

  /** A lock for each team's initialization. */
  teamInitLocks: Map<string, AsyncLock>;

  nextConnId: number;
  nextAnswerSubmissionId: number;

  /** Prevent opening too many connections at a time. */
  syncThrottle: AsyncSemaphore;

  /**
   * Tracking state for the periodic backend sync.
   */
  slowSyncTracking: {
    downloadLock: AsyncLock;
    miscTeamStateSyncController: MiscTeamStateSyncController;
    /** The notif index to start the next sync from. */
    notifsNextIndex: number;
  };

  numActivePeriodicTasksHandlers: number;
  periodicSyncInterval: number;
  /** Timer handle for periodic tasks. */
  periodicTasksTimer?: ReturnType<typeof setInterval>;

  customPuzzles: CustomPuzzles;
  customPuzzlesCannedHints: CannedHints;

  jwtCache: Map<string, HuntJwtPayload>;

  config: SockServerConfig;

  constructor(opts: SockServerOpts) {
    // Unused data to initialize the fields with.
    // These should only be used after the sock server is initialized
    // with the first getInitData backend call.
    // TODO: Probably wrap this in something that does the initialization
    // and passes these in during construction, to ensure that they
    // are only ever used after initialization.
    this.sockServerId = "__UNINITIALIZED__";
    this.maxGuessesPerPuzzle = 0;
    this.huntStartTime = 0;
    this.huntEndTime = 0;
    this.huntCloseTime = 0;

    // PuzzlesDB is initialized when the server is initialized.
    this.puzzlesDB = makeEmptyPuzzlesDB();
    this.opts = opts;
    this.onReadyCallbacks = [];

    this.backend = new BackendInterfaceController({
      backend: this.opts.backend,
      dumpBackendRequests: this.opts.dumpBackendRequests,
    });

    this.clock = new SockServerClock();
    this.numActiveConns = 0;
    this.teamConns = new Map();
    this.teamControllers = new Map();
    this.scopeEnginesController = new ScopeEnginesController(this);
    this.statsController = new StatsController(this);
    this.cursorsController = new CursorsController(this);
    this.subscriptionsTracker = new SubscriptionsTracker();
    this.dirtyStateTracker = new ServerDirtyStateTracker();
    this.bigBoardStateTracker = new BigBoardStateTracker();
    this.teamMetricsTracker = new TeamMetricsTracker(this);
    this.farmerServerInterface = this.opts.farmerServerInterface;
    this.mockHuntStaff = new MockHuntStaff(this);
    this.fastSyncMetrics = new IntervalMetrics(
      this.clock,
      SERVER_HEALTH_CONFIG.healthMonitoringIntervalMs
    );
    this.slowSyncMetrics = new IntervalMetrics(
      this.clock,
      SERVER_HEALTH_CONFIG.healthMonitoringIntervalMs
    );
    this.checkAnswerMetrics = new IntervalMetrics(
      this.clock,
      SERVER_HEALTH_CONFIG.healthMonitoringIntervalMs
    );
    this.cursorEventsMetrics = new IntervalMetrics(
      this.clock,
      SERVER_HEALTH_CONFIG.cursorEventsMonitoringIntervalMs
    );
    this.numAuthsMetrics = new IntervalMetrics(
      this.clock,
      SERVER_HEALTH_CONFIG.healthMonitoringIntervalMs
    );
    this.numRequestsMetrics = new IntervalMetrics(
      this.clock,
      SERVER_HEALTH_CONFIG.healthMonitoringIntervalMs
    );
    this.metricsDumpTracking = {
      lastDumpTime: 0,
      lastUserTime: 0,
      lastSystemTime: 0,
      userTimeUsage: 0,
      systemTimeUsage: 0,
    };
    this.isInitPending = false;
    this.isReady = false;
    this.isStopped = false;
    this.teamInitLocks = new Map();
    this.nextConnId = 0;
    this.nextAnswerSubmissionId = 0;
    this.syncThrottle = new AsyncSemaphore(
      () => this.config.syncThrottleParallelism
    );
    this.slowSyncTracking = {
      downloadLock: new AsyncLock(),
      miscTeamStateSyncController: new MiscTeamStateSyncController(this),
      notifsNextIndex: 0,
    };

    this.numActivePeriodicTasksHandlers = 0;
    // 3 seconds by default
    this.periodicSyncInterval = this.opts.periodicSyncInterval ?? 3000;
    this.periodicTasksTimer = setInterval(() => {
      this.runPeriodicTasks();
    }, this.periodicSyncInterval);

    this.customPuzzles = {};
    this.customPuzzlesCannedHints = {};

    this.jwtCache = new Map();

    this.config = makeDefaultSockServerConfig();

    if (this.opts.isPosthunt ?? false) {
      // The hunt was made easier as time progressed. For posthunt, use
      // settings closer to hunt start since we're expecting posthunt
      // solvers to be stronger.
      // HACK: Do this here temporarily instead of changing the global struct
      // so that we can support the same nerfs for on-site hunt end.
      GACHA_CONFIG.celestialRigging.thresholds = [
        0.1, 0.2, 0.32, 0.45, 0.59, 0.74, 0.9,
      ];
      GACHA_CONFIG.huntWidthRigging.solvesBuff = {
        min: 5 / 180,
        max: 10 / 180,
      };
      GACHA_CONFIG.huntWidthRigging.ignoreProbability = 0;
      GACHA_CONFIG.huntWidthRigging.dustContribution = 1 / 100000;
    }
  }

  /**
   * TODO: This is just a test utility to remove pending tasks and
   * allow tests to end gracefully. If this is to be used in a
   * production server, it needs to ensure that all pending promises
   * are responded to (e.g. onReadyCallbacks).
   *
   * This is now also used for the posthunt mock server. There
   * are memory leaks with pending promises, but it should not be
   * okay for now since users are not expected to reset repeatedly.
   */
  teardown(): void {
    if (this.periodicTasksTimer !== undefined)
      clearInterval(this.periodicTasksTimer);
    for (const teamConnsForTeam of this.teamConns.values()) {
      for (const conn of teamConnsForTeam.values()) {
        conn.close();
      }
    }
    this.isStopped = true;
  }

  genConnId() {
    const connId = this.nextConnId;
    this.nextConnId++;
    return `${connId}`;
  }

  getTeamController(teamId: string): TeamController {
    const teamController = this.teamControllers.get(teamId);
    if (teamController === undefined)
      throw new Error(`team ${teamId} not initialized yet`);
    return teamController;
  }

  tryGetPuzzle(puzName: string): PuzzleData | null {
    return this.puzzlesDB.tryGetPuzzle(puzName, this.customPuzzles);
  }

  getPuzzle(puzName: string): PuzzleData {
    return this.puzzlesDB.getPuzzle(puzName, this.customPuzzles);
  }

  getPuzNames(): Iterable<string> {
    return this.puzzlesDB.getPuzNames(this.customPuzzles);
  }

  getPuzzles(): Iterable<PuzzleData> {
    return this.puzzlesDB.getPuzzles(this.customPuzzles);
  }

  getCannedHints(): Iterable<[string, string[]]> {
    return this.puzzlesDB.getCannedHints(this.customPuzzlesCannedHints);
  }

  getCannedHintsForPuzzle(puzName: string): string[] {
    return this.puzzlesDB.getCannedHintsForPuzzle(
      puzName,
      this.customPuzzlesCannedHints
    );
  }

  getIsHuntOver(): boolean {
    return this.clock.genTimestamp() >= this.huntEndTime;
  }

  setCustomPuzzle(puzData: PuzzleData, cannedHint?: string) {
    const { puzName } = puzData;
    const isNew = this.customPuzzles[puzName] === undefined;
    this.customPuzzles[puzName] = puzData;
    const newCannedHints = cannedHint !== undefined ? [cannedHint] : [];
    const didMaxCannedHintsChange =
      newCannedHints.length !== this.getCannedHintsForPuzzle(puzName).length;
    // This only takes effect if there are no canned hints already
    // registered in the JSON canned hints for the puzzle.
    this.customPuzzlesCannedHints[puzName] = newCannedHints;
    if (didMaxCannedHintsChange) {
      this.handleHuntAdminUpdate({
        numCannedHints: { [puzName]: newCannedHints.length },
      });
      for (const teamCannedHintsEngine of Object.values(
        this.scopeEnginesController.getEnginesForType(
          ScopeType.TEAM_CANNED_HINTS
        )
      )) {
        teamCannedHintsEngine.handlePuzzlesUpdate([puzName]);
      }
    }
    for (let i = 0; i < newCannedHints.length; i++) {
      this.scopeEnginesController
        .getEngineIfExists({
          type: ScopeType.CANNED_HINT,
          puzName,
          index: i,
        })
        ?.updateContent();
    }
    if (isNew) {
      this.handleHuntAdminUpdate({
        addPuzzles: { [puzName]: puzData },
      });
    }
    const puzzleContentEngine = this.scopeEnginesController.getEngineIfExists({
      type: ScopeType.PUZZLE_CONTENT,
      puzName,
    });
    if (
      puzzleContentEngine !== undefined &&
      puzzleContentEngine.rawContent === undefined
    )
      puzzleContentEngine.ctx.assign(
        puzzleContentEngine.ctx.scope,
        puzzleContentEngine.getState()
      );
    const solutionContentEngine = this.scopeEnginesController.getEngineIfExists(
      {
        type: ScopeType.SOLUTION_CONTENT,
        puzName,
      }
    );
    if (
      solutionContentEngine !== undefined &&
      solutionContentEngine.rawContent === undefined
    )
      solutionContentEngine.ctx.assign(
        solutionContentEngine.ctx.scope,
        solutionContentEngine.getState()
      );
    const numCustomPuzzles = Object.values(this.customPuzzles).filter(
      (puzzle) => puzzle.isCustomPuzzleAvailableInGacha
    ).length;
    for (const teamController of this.teamControllers.values()) {
      if (this.getIsHuntOver() || teamController.getIsFinished()) {
        teamController.handleTeamUpdate({
          finishedData: {
            numSolveCountPuzzles: this.puzzlesDB.numSolveCountPuzzles,
            numCustomPuzzles,
          },
        });
      }
    }
  }

  async waitForReadyAsync(): Promise<void> {
    if (this.isReady) return;
    await new Promise<void>((resolve, reject) => {
      this.onReadyCallbacks.push(resolve);
    });
  }

  private async tryInitAsync(): Promise<void> {
    if (this.isReady) return;
    if (this.isInitPending) return;

    this.isInitPending = true;
    try {
      const resp = await (async () => {
        try {
          return (
            await this.backend.sendReqAsync({
              type: BackendReqType.GET_INIT_DATA,
              // The architecture does not yet support multiple worker
              // node servers, so use a constant workerId for now.
              payload: { workerId: "main_worker" },
            })
          ).payload;
        } catch (err) {
          // If we fail, the periodic tasks handler will call this again.
          console.error(err);
        }
      })();

      if (resp !== undefined) {
        const {
          sockServerId,
          teams,
          scopeState,
          maxGuessesPerPuzzle,
          huntStartTime,
          huntEndTime,
          huntCloseTime,
          notifsNextIndex,
          errata,
        } = resp;

        this.sockServerId = sockServerId;
        this.maxGuessesPerPuzzle = maxGuessesPerPuzzle;
        this.huntStartTime = huntStartTime;
        this.huntEndTime =
          this.opts.forceHuntEnd ?? false
            ? Math.min(this.clock.genTimestamp(), huntEndTime)
            : huntEndTime;
        this.huntCloseTime = huntCloseTime;
        this.slowSyncTracking.notifsNextIndex = notifsNextIndex;
        for (const [erratumId, erratum] of Object.entries(errata)) {
          if (erratum === null) continue;
          this.scopeEnginesController.createEngine(
            new ExecContext(),
            {
              type: ScopeType.ERRATUM_CONTENT,
              erratumId,
            },
            erratum
          );
        }
        for (const [teamId, teamData] of Object.entries(teams)) {
          this.createTeamController(teamId, teamData);
        }
        const engines = Object.values(scopeState).map(({ scope, state }) => {
          const engine = this.scopeEnginesController.createEngine(
            new ExecContext(),
            scope,
            state,
            true
          );
          if (engine === null)
            throw new Error(
              `error creating init engine for ${scopeToString(scope)}`
            );
          return engine;
        });
        for (const engine of engines) {
          engine.init?.();
        }
        // Only init team controllers after all engines have been loaded.
        for (const [teamId, teamData] of Object.entries(teams)) {
          await this.initTeamControllerAsync(teamId, teamData);
        }

        this.isReady = true;
        for (const callback of this.onReadyCallbacks) {
          callback();
        }
        this.onReadyCallbacks = [];
        console.log("Sock server init complete.");
      }
    } catch (err) {
      this.scopeEnginesController.teardown();
      throw err;
    } finally {
      this.isInitPending = false;
    }
  }

  async initAsync(): Promise<void> {
    if (this.isReady || this.isInitPending)
      throw new Error("don't expect to request init more than once");
    if (this.opts.cursorsMode ?? false) {
      this.isReady = true;
      return;
    }
    await this.loadPuzzlesAsync();
    await this.tryInitAsync();
    await this.waitForReadyAsync();
  }

  /**
   * Returns an interface for the added client, containing
   * a function to handle received messages.
   * For development, a particular connId may be specified
   * to aid debugging.
   */
  addClient(client: ClientInterface, connId?: string) {
    this.numActiveConns++;

    const conn = new RawClientConnection(this, connId ?? this.genConnId(), {
      // Add a logging wrapper to the client send func.
      send: (msg) => {
        if (this.opts.dumpWsMessages) {
          console.log(
            `${conn.connId} < ${JSON.stringify(JSON.parse(msg), null, 2)}`
          );
        }
        client.send(msg);
      },
      close: () => client.close(),
    });

    return {
      handleMsg: (msg: string) => {
        const ctx = new RawRequestContext(
          conn,
          this.opts.logRequestErrDebugInfoAsync
        );
        (async () => {
          await conn.handleMsgLock.acquireAndRunAsync(async () => {
            ctx.activeMsgRaw = msg;
            try {
              await this.handleMsgAsync(ctx, msg);
            } catch (err) {
              console.error(err);
              conn.sendErrCode(SockAPIErrorCode.SERVER_ERROR);
            } finally {
              ctx.flushErr();
              delete ctx.activeMsgRaw;
            }
          });
        })().catch((err) => console.error);
      },
      removeClient: () => {
        this.removeClient(conn);
      },
    };
  }

  isCtxAuthed(ctx: RawRequestContext): ctx is RequestContext {
    return isConnAuthed(ctx.conn);
  }

  removeClient(conn: RawClientConnection) {
    this.numActiveConns--;

    if (!isConnAuthed(conn)) return;
    this.removeClientAuthed(conn);
  }

  private removeClientAuthed(conn: ClientConnection) {
    const { connId, teamId } = conn;

    this.subscriptionsTracker.clearConnSubscriptions(conn);
    this.cursorsController.removeConnFromGroup(conn);

    const teamConnsForTeam = this.teamConns.get(teamId);
    if (teamConnsForTeam === undefined)
      throw new Error("expect team conns to be defined for team");
    teamConnsForTeam.delete(connId);
  }

  private createTeamController(
    teamId: string,
    teamData: GetInitTeamDataBackendResp
  ) {
    const teamController = new TeamController(teamId, teamData, this);
    this.teamControllers.set(teamId, teamController);
  }

  private async initTeamControllerAsync(
    teamId: string,
    teamData?: GetInitTeamDataBackendResp
  ) {
    const teamController = this.getTeamController(teamId);
    teamController.init(teamData);

    this.handleHuntAdminUpdate({
      addTeams: { [teamId]: teamController.getClientAdminData() },
    });
    // We add the team even if it's hidden. We will filter the hidden teams
    // downstream.
    this.handleTeamListAddTeam(teamId);
    this.getHuntAdminEngine().addTeam(teamId);

    const bigBoardState = teamController.getClientBigBoardState();
    this.bigBoardStateTracker.addTeam(teamId, bigBoardState);
    this.handleBigBoardUpdate({
      addTeams: { [teamId]: bigBoardState },
    });

    await teamController.initTeamProgressAsync();
  }

  /**
   * Currently, we rely on this only being called for teams
   * created after the interactive server has started,
   * so there should be no scope state to initialize.
   */
  private async initTeamAsync(teamId: string) {
    let resp: GetInitTeamDataBackendResp | null = null;
    try {
      resp = (
        await this.backend.sendReqAsync({
          type: BackendReqType.GET_INIT_TEAM_DATA,
          payload: { teamId },
        })
      ).payload;
    } catch (err) {
      console.error(err);
    }
    if (resp === null) return;

    this.createTeamController(teamId, resp);
    await this.initTeamControllerAsync(teamId);

    if (!(this.opts.cursorsMode ?? false)) {
      const teamController = this.getTeamController(teamId);
      for (let i = 0; i < (this.teamConns.get(teamId)?.size ?? 0); i++) {
        teamController.numAuthsMetrics.record();
        this.teamMetricsTracker.recordAuth(teamId);
      }
    }

    // Tell all connections for the team that we're ready to
    // accept input.
    this.broadcastRespToTeam(teamId, {
      type: WSRespType.AUTH_SUCCESS,
      result: { teamId },
    });
  }

  async loadPuzzlesAsync() {
    this.setPuzzlesDB(await this.opts.loadPuzzlesDBAsync());
  }

  setPuzzlesDB(puzzlesDB: PuzzlesDB) {
    const maxCannedHintsChangedPuzNames = [...this.getPuzNames()].filter(
      (puzName) =>
        puzzlesDB.getCannedHintsForPuzzle(
          puzName,
          this.customPuzzlesCannedHints
        ).length !== this.getCannedHintsForPuzzle(puzName).length
    );
    this.puzzlesDB = puzzlesDB;

    // HACK: We're using PuzzlesDB as temporary storage for custom puzzle
    // canned hints (just for GPH2024; otherwise it should be immutable),
    // so set the puzzles again here.
    for (const engine of Object.values(
      this.scopeEnginesController.getEnginesForType(ScopeType.TEAM_PUZZLE_105)
    )) {
      engine.updateCustomPuzzle();
    }

    // If the rest of the server is not set up yet, don't update
    // the engines.
    if (!this.isReady) return;

    for (const engine of Object.values(
      this.scopeEnginesController.getEnginesForType(ScopeType.PUZZLE_CONTENT)
    )) {
      engine.updateContent();
    }
    for (const engine of Object.values(
      this.scopeEnginesController.getEnginesForType(ScopeType.SOLUTION_CONTENT)
    )) {
      engine.updateContent();
    }
    for (const engine of Object.values(
      this.scopeEnginesController.getEnginesForType(ScopeType.CANNED_HINT)
    )) {
      engine.updateContent();
    }

    this.getHuntAdminEngine().refresh();
    if (maxCannedHintsChangedPuzNames.length > 0) {
      for (const teamCannedHintsEngine of Object.values(
        this.scopeEnginesController.getEnginesForType(
          ScopeType.TEAM_CANNED_HINTS
        )
      )) {
        teamCannedHintsEngine.handlePuzzlesUpdate(
          maxCannedHintsChangedPuzNames
        );
      }
    }
  }

  isHuntClosed(): boolean {
    return this.clock.genTimestamp() >= this.huntCloseTime;
  }

  subscribeToUpdates(
    ctx: RequestContext,
    scope: SockAPIScope,
    tag?: string
  ): boolean {
    if (this.subscriptionsTracker.isSubscribed(ctx.conn, scope))
      return ctx.setErrCodeAndReturn(
        SockAPIErrorCode.ALREADY_SUBSCRIBED,
        false
      );
    if (!this.scopeEnginesController.handleSubscribe(ctx, scope, tag))
      return false;
    if (!this.subscriptionsTracker.subscribe(ctx, scope)) return false;
    return true;
  }

  unsubscribeFromUpdates(ctx: RequestContext, scope: SockAPIScope): boolean {
    if (!this.subscriptionsTracker.unsubscribe(ctx, scope)) return false;
    return true;
  }

  sendRespMultiple(conns: Iterable<ClientConnection>, resp: WSResp): void {
    const serializedResp = JSON.stringify(resp);
    for (const conn of conns) {
      conn.sendRaw(serializedResp);
    }
  }

  private async handleMsgAsync(ctx: RawRequestContext, msg: string) {
    await this.waitForReadyAsync();

    const { conn } = ctx;

    // If we are stopped, disconnect connections on auth.
    if (this.isStopped) {
      conn.close();
      return;
    }

    delete conn.activeStepId;

    this.numRequestsMetrics.record();

    const req = JSON.parse(msg.toString());

    if (typeof req !== "object" || typeof req.type !== "string")
      return ctx.setBadRequest();

    if (this.opts.dumpWsMessages) {
      console.log(`${conn.connId} > ${JSON.stringify(req, null, 2)}`);
    }

    if (req.type === WSReqType.AUTH) {
      const { jwt } = req;
      if (typeof jwt !== "string") return ctx.setBadRequest();

      this.numAuthsMetrics.record();
      const payload =
        this.jwtCache.get(jwt) ?? this.opts.authInterface.verify(jwt);
      if (payload === null) return ctx.setErrCode(SockAPIErrorCode.AUTH_ERROR);
      const { teamId = this.backend.getDefaultTeamId(), isAdmin = false } =
        payload;
      if (teamId === null) return ctx.setPermissionDenied();

      // Set the fields in conn and record the new type.
      const authedConn: ClientConnection = Object.assign(conn, {
        teamId,
        isAdmin,
      });

      const teamConnsForTeam = this.teamConns.get(teamId);
      if (teamConnsForTeam === undefined) {
        const conns = new Map<string, ClientConnection>();
        conns.set(authedConn.connId, authedConn);
        this.teamConns.set(teamId, conns);
      } else {
        teamConnsForTeam.set(authedConn.connId, authedConn);
      }

      if (!(this.opts.cursorsMode ?? false)) {
        // Delay the AUTH_SUCCESS until the team state is initialized.
        // This needs to be awaited on to ensure that errors are
        // properly contained.
        const teamInitLock = (() => {
          const existingLock = this.teamInitLocks.get(teamId);
          if (existingLock !== undefined) return existingLock;
          const newLock = new AsyncLock();
          this.teamInitLocks.set(teamId, newLock);
          return newLock;
        })();
        await teamInitLock.acquireAndRunAsync(async () => {
          if (!this.teamControllers.has(teamId)) {
            await this.initTeamAsync(teamId);
          }
        });

        const teamController = this.teamControllers.get(teamId);
        // Teams can't be removed once they have been initialized.
        if (teamController === undefined)
          throw new Error(`team ${teamId} not initialized`);
        this.teamMetricsTracker.recordAuth(teamId);
        teamController.numAuthsMetrics.record();
      }

      authedConn.send({
        type: WSRespType.AUTH_SUCCESS,
        result: { teamId },
      });
      return;
    }

    if (!this.isCtxAuthed(ctx))
      // All other requests, including pings, should be rejected
      // if the client is not yet authenticated.
      return ctx.setErrCode(SockAPIErrorCode.PERMISSION_DENIED, true);

    await this.handleReqAuthedAsync(ctx, req);
  }

  private async handleReqAuthedAsync(
    ctx: RequestContext,
    req: { type: string }
  ) {
    const { conn } = ctx;
    const { teamId } = conn;

    // Requests that don't depend on teamController.
    // This special-casing is necessary since the cursors server
    // does not set up TeamControllers.
    //
    // Make sure that all these cases end with a return instead of
    // a break!
    switch (req.type) {
      case WSReqType.PING: {
        conn.send({
          type: WSRespType.PONG,
        });
        return;
      }
      case WSReqType.SET_CURSOR_GROUP: {
        if (!("reqId" in req) || !("cursorGroupName" in req))
          return ctx.setBadRequest();
        const { reqId, cursorGroupName } = req;
        if (!(typeof reqId === "number")) return ctx.setBadRequest();
        if (!(cursorGroupName === null || typeof cursorGroupName === "string"))
          return ctx.setBadRequest();
        if (cursorGroupName === null)
          this.cursorsController.removeConnFromGroup(conn);
        else this.cursorsController.addConnToGroup(conn, cursorGroupName);
        conn.send({
          type: WSRespType.SET_CURSOR_GROUP_ACK,
          reqId,
        });
        return;
      }
      case WSReqType.CURSOR_EVENT: {
        if (!("event" in req)) return ctx.setBadRequest();
        const { event } = req;
        if (typeof event !== "object") return ctx.setBadRequest();
        const parsedEvent = safeParseCursorEvent(event);
        if (parsedEvent === null) return ctx.setBadRequest();
        this.cursorsController.broadcastEvent(conn, parsedEvent);
        this.cursorEventsMetrics.record();
        return;
      }
    }

    const teamController = this.teamControllers.get(teamId);
    // Any further requests after AUTH should be rejected until the
    // team state is initialized. Normally, this is enforced since
    // we delay AUTH_SUCCESS until the team state is initialized,
    // but we may still get this from a faulty or modded client.
    if (teamController === undefined)
      return ctx.setErrCode(SockAPIErrorCode.PERMISSION_DENIED, true);
    // If the team is inactive, block all connections
    // (except admin impersonators).
    if (teamController.isInactive && !conn.isAdmin)
      return ctx.setErrCode(SockAPIErrorCode.TEAM_INACTIVE, true);
    // If the team is temporarily blocked for maintenance,
    // block all connections (except admin impersonators).
    if (teamController.isTempBlocked && !conn.isAdmin)
      return ctx.setErrCode(SockAPIErrorCode.TEAM_TEMP_BLOCKED, true);

    this.teamMetricsTracker.recordRequest(teamId);
    teamController.numRequestsMetrics.record();
    const parseResult = this.opts.isWSReq(req);
    if (!parseResult)
      // req is a malformed WSReq
      return ctx.setBadRequest();
    await this.handleValidatedReqAsync(ctx, req, teamController);
  }

  private async handleValidatedReqAsync(
    ctx: RequestContext,
    req: WSReq,
    teamController: TeamController
  ) {
    const { conn } = ctx;
    if (
      conn.spectateTeamId !== undefined &&
      req.type !== WSReqType.UPDATE_SUBSCRIPTIONS
    )
      // Spectators aren't allowed to take any actions.
      return ctx.setErrCode(SockAPIErrorCode.SPECTATE_ACTION);

    switch (req.type) {
      case WSReqType.SCOPE_STEP: {
        conn.activeStepId = req.stepId;
        await this.handleScopeStepAsync(ctx, req.step.scope, req.step.payload);
        break;
      }
      case WSReqType.VIEW_PUZZLE: {
        const { puzName } = req;
        teamController.viewPuzzle(ctx, puzName);
        break;
      }
      case WSReqType.UPDATE_SUBSCRIPTIONS: {
        const errors: SockAPIUpdateSubscriptionError[] = [];
        for (const upd of req.upds) {
          const { scope } = upd;
          const success = (() => {
            switch (upd.type) {
              case WSReqUpdateSubscriptionRequestType.ADD:
                return this.subscribeToUpdates(ctx, scope, upd.tag);
              case WSReqUpdateSubscriptionRequestType.REMOVE:
                return this.unsubscribeFromUpdates(ctx, scope);
            }
          })();
          const err = ctx.flushErr(false);
          if (success !== (err === undefined))
            console.error(
              `expected error to match success flag, but got ${err} (${success})`
            );
          if (err !== undefined)
            errors.push({
              type: upd.type,
              scope,
              err,
            });
        }
        if (errors.length > 0)
          ctx.conn.send({
            type: WSRespType.UPDATE_SUBSCRIPTIONS_ERRORS,
            errors,
          });
        break;
      }
    }
  }

  async handleScopeStepAsync<TScope extends SockAPISteppableScope>(
    ctx: RequestContext,
    scope: TScope,
    payload: SockAPIScopeStepPayload<TScope["type"]>
  ) {
    await this.scopeEnginesController.handleStepAsync(ctx, { scope, payload });
  }

  // Raw broadcast function. Avoid using this directly.
  // Instead, go through the scope engine to ensure scope caches
  // are correctly invalidated.
  broadcastToScope<TScope extends SockAPIScope>(
    scope: TScope,
    resp: WSResp
  ): void {
    const subscriptions = this.subscriptionsTracker.getSubscribedConns(scope);
    this.sendRespMultiple(subscriptions.values(), resp);
  }

  // Raw broadcast function. Avoid using this directly.
  // Instead, go through the scope engine to ensure scope caches
  // are correctly invalidated.
  broadcastScopeAssign<TScope extends SockAPIScope>(
    scope: TScope,
    state: SockAPIScopeState<TScope["type"]>,
    tag?: string
  ): void {
    return this.broadcastToScope(scope, {
      type: WSRespType.SCOPE_ASSIGN,
      assign: { scope, state, tag },
    });
  }

  // Raw broadcast function. Avoid using this directly.
  // Instead, go through the scope engine to ensure scope caches
  // are correctly invalidated.
  broadcastScopeUpdate<TScope extends SockAPIUpdatableScope>(
    scope: TScope,
    payload: SockAPIScopeUpdatePayload<TScope["type"]>,
    tag?: string
  ): void {
    return this.broadcastToScope(scope, {
      type: WSRespType.SCOPE_UPDATE,
      upd: { scope, payload, tag },
    });
  }

  handleHuntAdminUpdate(
    payload: SockAPIScopeUpdatePayload<ScopeType.HUNT_ADMIN>
  ): void {
    this.getHuntAdminEngine().ctx.update(
      { type: ScopeType.HUNT_ADMIN },
      payload
    );
  }

  /**
   * Get or create an engine that we know must either exist or
   * can be created without errors.
   */
  getOrCreateEngineOrThrow<TScope extends Scope<ScopeEngineType>>(
    scope: TScope
  ): ScopeEngine<TScope["type"]> {
    const engine = this.scopeEnginesController.getOrCreateEngine(
      new ExecContext(),
      scope
    );
    if (engine === null)
      throw new Error(`failed to create engine for ${scopeToString(scope)}`);
    return engine;
  }

  getHuntAdminEngine(): ScopeEngine<ScopeType.HUNT_ADMIN> {
    return this.getOrCreateEngineOrThrow({
      type: ScopeType.HUNT_ADMIN,
    });
  }

  getBigBoardEngine(): ScopeEngine<ScopeType.BIG_BOARD> {
    return this.getOrCreateEngineOrThrow({
      type: ScopeType.BIG_BOARD,
    });
  }

  getServerHealthEngine(): ScopeEngine<ScopeType.SERVER_HEALTH> {
    return this.getOrCreateEngineOrThrow({
      type: ScopeType.SERVER_HEALTH,
    });
  }

  getTeamListEngine(): ScopeEngine<ScopeType.TEAM_LIST> {
    return this.getOrCreateEngineOrThrow({
      type: ScopeType.TEAM_LIST,
    });
  }

  getAdminQueueEngine(): ScopeEngine<ScopeType.ADMIN_QUEUE> {
    return this.getOrCreateEngineOrThrow({
      type: ScopeType.ADMIN_QUEUE,
    });
  }

  handleTeamSummaryUpdate(teamId: string, upd: TeamSummaryUpdate): void {
    // Broadcast team list updates even for hidden teams. We will filter
    // hidden teams downstream.
    this.getTeamListEngine().updateTeam(teamId, upd);

    const { isHidden, displayName } = upd;
    const huntAdminTeamUpd = { isHidden, displayName };
    if (
      Object.values(huntAdminTeamUpd).filter((v) => v !== undefined).length > 0
    )
      this.handleHuntAdminUpdate({
        updTeams: { [teamId]: upd },
      });
  }

  handleTeamListAddTeam(teamId: string) {
    const teamController = this.getTeamController(teamId);
    const teamListState = teamController.getTeamSummaryState();
    this.getTeamListEngine().addTeam(teamId, teamListState);
  }

  handleTeamListRemoveTeam(teamId: string) {
    this.getTeamListEngine().removeTeam(teamId);
  }

  private broadcastAndCommitTeamSummaryUpdates() {
    const { pendingUpds } = this.getTeamListEngine();
    if (Object.keys(pendingUpds).length <= 0) return;
    this.getTeamListEngine().commitUpds();
  }

  handleBigBoardUpdate(
    payload: SockAPIScopeUpdatePayload<ScopeType.BIG_BOARD>
  ): void {
    this.getBigBoardEngine().ctx.update({ type: ScopeType.BIG_BOARD }, payload);
  }

  broadcastRespToTeam(teamId: string, resp: WSResp): void {
    const teamConnsForTeam = this.teamConns.get(teamId);
    if (teamConnsForTeam === undefined) return;

    this.sendRespMultiple(teamConnsForTeam.values(), resp);
  }

  broadcastNotifToTeam(teamId: string, notif: HuntNotification) {
    this.broadcastRespToTeam(teamId, {
      type: WSRespType.NOTIFICATION,
      notif,
    });
  }

  async handleImageSubmissionAsync(
    teamId: string,
    slot: SubmitImageSlot,
    url: string
  ): Promise<true> {
    // Ensure that all cases are handled by returning true.
    switch (slot) {
      case SubmitImageSlot.DUMMY:
        return true;
      case SubmitImageSlot.PUZZLE_105:
        await this.getOrCreateEngineOrThrow({
          type: ScopeType.TEAM_PUZZLE_105,
          teamId,
        }).handleImageSubmissionAsync(url);
        return true;
      case SubmitImageSlot.PUZZLE_139:
        await this.getOrCreateEngineOrThrow({
          type: ScopeType.TEAM_PUZZLE_139,
          teamId,
        }).handleImageSubmissionAsync(url);
        return true;
    }
  }

  async syncDataThrottledAsync(payload: SyncDataBackendReq) {
    return await this.syncThrottle.acquireAndRunAsync(async () => {
      return (
        await this.backend.sendReqAsync({
          type: BackendReqType.SYNC_DATA,
          payload,
        })
      ).payload;
    });
  }

  private async triggerSlowSyncDownloadUnderLockAsync(): Promise<void> {
    const syncDataReq = {
      // The presence of this field in general indicates that this is
      // a slow, periodic update, and is used for things like getting
      // notifications and updating hint counts
      notifsStartIndex: this.slowSyncTracking.notifsNextIndex,
    };

    const { notifs, notifsNextIndex } = await this.syncDataThrottledAsync(
      syncDataReq
    );

    if (notifsNextIndex !== undefined)
      this.slowSyncTracking.notifsNextIndex = notifsNextIndex;
    for (const data of notifs ?? []) {
      try {
        switch (data.type) {
          case BackendNotificationDataType.HINT_ANSWERED: {
            // Translate the backend notif into a client notif.
            const { hintId, response } = data;
            const engine = this.scopeEnginesController.getEngineIfExists({
              type: ScopeType.HINT,
              hintId,
            });
            if (engine === undefined)
              throw new Error(
                `received hint response for unknown hint ${hintId}`
              );
            engine.handleResponseNoBackendSync(response);
            break;
          }
          case BackendNotificationDataType.TEAM_MEMBERS: {
            const { teamId, members } = data;
            this.teamControllers.get(teamId)?.handleSetMembers(members);
            break;
          }
          case BackendNotificationDataType.ERRATUM: {
            const { erratumId } = data;
            await this.handleErratumUpdateAsync(erratumId, data);
            break;
          }
          case BackendNotificationDataType.SUBMIT_IMAGE: {
            const { teamId, slot, url } = data;
            await this.handleImageSubmissionAsync(teamId, slot, url);
            break;
          }
        }
      } catch (err) {
        // Isolate errors to individual notifications.
        console.error(err);
      }
    }
  }

  async triggerSlowSyncDownloadAsync(force?: boolean): Promise<void> {
    const { downloadLock } = this.slowSyncTracking;
    // If we're already doing a download, then we don't need to
    // do another one right away, unless we specifically have
    // something we want to download that might have changed
    // while performing the last download.
    if (!(force ?? false) && downloadLock.getIsActive()) return;
    await downloadLock.acquireAndRunAsync(() =>
      this.triggerSlowSyncDownloadUnderLockAsync()
    );
  }

  async triggerSlowSyncUpload(): Promise<void> {
    await this.scopeEnginesController.syncLoopController.triggerLoopAsync();
    await this.slowSyncTracking.miscTeamStateSyncController.syncLoopController.triggerLoopAsync();
  }

  private async runPeriodicSyncTasks(): Promise<void> {
    // If a fast sync fails, the periodic task handler is responsible
    // for kicking it off again after a while.
    for (const [teamId, teamController] of this.teamControllers) {
      try {
        await teamController.fastSyncTracking.syncLoopController.triggerLoopAsync();
      } catch (err) {
        // Isolate fast sync problems to within individual teams.
        console.error(err);
      }
    }

    try {
      await this.triggerSlowSyncUpload();
    } catch (err) {
      // Isolate errors for the slow sync upload.
      console.error(err);
    }

    try {
      await this.triggerSlowSyncDownloadAsync();
    } catch (err) {
      // Isolate errors for the slow sync download.
      console.error(err);
    }
  }

  runPeriodicTasks(): void {
    // Allow multiple periodic tasks runners to run at once,
    // so that if any gets stuck, future ones may pick up where
    // earlier ones left off.
    (async () => {
      if (!this.isReady) {
        // If init failed, try again.
        await this.tryInitAsync();
        return;
      }

      try {
        const nowTime = this.clock.genTimestamp();
        const { lastDumpTime, lastUserTime, lastSystemTime } =
          this.metricsDumpTracking;
        // default interval of 30s
        if (
          nowTime >
          lastDumpTime + (this.opts.metricsDumpInterval ?? 30 * 1000)
        ) {
          const { user: userTime, system: systemTime } =
            this.opts.getCpuUsage?.() ?? { user: 0, system: 0 };
          const userTimeUsage =
            (userTime - lastUserTime) / 1000 / (nowTime - lastDumpTime);
          const systemTimeUsage =
            (systemTime - lastSystemTime) / 1000 / (nowTime - lastDumpTime);
          this.metricsDumpTracking = {
            lastDumpTime: nowTime,
            lastUserTime: userTime,
            lastSystemTime: systemTime,
            userTimeUsage,
            systemTimeUsage,
          };
          await this.opts.logMetrics?.(
            `[${nowTime}] Metrics: ${JSON.stringify([
              0, // version
              this.fastSyncMetrics.getSummary().totNumEvents,
              this.slowSyncMetrics.getSummary().totNumEvents,
              this.checkAnswerMetrics.getSummary().totNumEvents,
              this.numAuthsMetrics.getSummary().totNumEvents,
              this.numRequestsMetrics.getSummary().totNumEvents,
              this.syncThrottle.getNumWaitingOrActive(),
              this.numActivePeriodicTasksHandlers,
              0,
              userTime,
              systemTime,
            ])}`
          );
        }
      } catch (err) {
        console.error(err);
      }

      this.numActivePeriodicTasksHandlers++;
      try {
        if (this.cursorEventsMetrics.prevNumEvents > 0) {
          await this.opts.logCursorsMetrics?.(
            `[${new Date().toLocaleString()}] Cursor events: ${this.cursorEventsMetrics.getSummaryString()}`
          );
        }
      } catch (err) {
        console.error(err);
      }

      if (!(this.opts.cursorsMode ?? false)) {
        try {
          this.getServerHealthEngine().runPeriodicTasks();
        } catch (err) {
          // Isolate problems with server health reporting.
          console.error(err);
        }

        try {
          this.broadcastAndCommitTeamSummaryUpdates();
        } catch (err) {
          // Isolate problems with team list reporting.
          console.error(err);
        }

        try {
          const { pendingUpds } = this.bigBoardStateTracker;
          if (Object.keys(pendingUpds).length > 0) {
            this.handleBigBoardUpdate({
              updTeams: pendingUpds,
            });
            this.bigBoardStateTracker.commitUpds();
          }
        } catch (err) {
          // Isolate problems with big board reporting.
          console.error(err);
        }

        try {
          this.statsController.getHuntStatsEngine().flushAllUpdates();
        } catch (err) {
          // Isolate problems with stats reporting.
          console.error(err);
        }

        await this.runPeriodicSyncTasks();

        try {
          if (this.mockHuntStaff !== undefined)
            await this.mockHuntStaff.runPeriodicTasksAsync();
        } catch (err) {
          console.error(err);
        }
      }
      this.numActivePeriodicTasksHandlers--;
    })().catch(console.error);
  }

  recordSlowSyncPacket(): void {
    this.slowSyncMetrics.record();
  }

  private async handleErratumUpdateAsync(
    erratumId: string,
    content: ErratumContent
  ): Promise<void> {
    const { isPublished } = content;
    const { engine, isAlreadyPublished } = (() => {
      const existingEngine = this.scopeEnginesController.getEngineIfExists({
        type: ScopeType.ERRATUM_CONTENT,
        erratumId,
      });
      if (existingEngine === undefined) {
        const engine = this.scopeEnginesController.createEngine(
          new ExecContext(),
          {
            type: ScopeType.ERRATUM_CONTENT,
            erratumId,
          },
          content
        );
        if (engine === null)
          throw new Error(`failed to create engine for erratum ${erratumId}`);
        return {
          engine,
          isAlreadyPublished: false,
        };
      } else {
        const isAlreadyPublished = existingEngine.state.isPublished;
        existingEngine.overrideContent(content);
        return { engine: existingEngine, isAlreadyPublished };
      }
    })();

    const erratumSummary = engine.getSummary();
    this.handleHuntAdminUpdate({
      setErrata: { [erratumId]: erratumSummary },
    });

    if (isPublished) {
      const erratumUpdate = { [erratumId]: erratumSummary };
      for (const teamErrataEngine of Object.values(
        this.scopeEnginesController.getEnginesForType(ScopeType.TEAM_ERRATA)
      )) {
        teamErrataEngine.ctx.update(teamErrataEngine.ctx.scope, erratumUpdate);
      }
    }

    const isNewlyPublished = !isAlreadyPublished && isPublished;
    if (isNewlyPublished) {
      for (const teamId of this.teamControllers.keys()) {
        if (engine.isVisibleToTeam(teamId))
          this.broadcastNotifToTeam(teamId, {
            type: HuntNotificationType.ERRATUM,
          });
      }
    }
  }
}
