import { SockAPIErrorCode } from "sockapi/errors";
import { SubmitAnswerResultType, SubmitAnswerResult } from "sockapi/answers";
import { ScopeType } from "sockapi/scope-types";
import { SolveAttemptInfo } from "sockapi/solve-attempts";
import {
  TeamProgressEventType,
  TeamProgressEventReasonType,
} from "sockapi/team-progress";

import { RequestContext } from "sock-server/exec-contexts";
import { checkAnswer } from "sock-server/checkAnswer";
import { SockServer } from "sock-server/SockServer";
import { TeamController } from "sock-server/server-controllers/team-controller";
import PUZZLE_CONFIG from "sock-server/puzzles/puzzle-config";

export class TeamAnswerableController {
  teamId: string;
  puzName: string;
  server: SockServer;

  constructor(teamId: string, puzName: string, server: SockServer) {
    this.teamId = teamId;
    this.puzName = puzName;
    this.server = server;
  }

  getTeamController(): TeamController {
    return this.server.getTeamController(this.teamId);
  }

  getNumWrongGuesses(): number {
    const { progressTracker } = this.getTeamController();
    return (
      [
        ...(progressTracker
          .getPuzzleSolveAttempts()
          .l1Get(this.puzName)
          ?.values() ?? []),
      ].filter((eventRecord) => eventRecord.info.isWrong ?? false).length ?? 0
    );
  }

  getNumGuessesRemaining(): number {
    const { progressTracker } = this.getTeamController();
    return (
      this.server.maxGuessesPerPuzzle +
      (progressTracker.view.puzzleNumExtraGuesses.get(this.puzName) ?? 0) -
      this.getNumWrongGuesses()
    );
  }

  handleUpdate(upds?: { [timestamp: number]: SolveAttemptInfo | null }): void {
    const numGuessesRemaining = this.getNumGuessesRemaining();
    this.server.bigBoardStateTracker.updateTeam(this.teamId, {
      unlocks: {
        [this.puzName]: {
          numWrongGuesses: this.getNumWrongGuesses(),
        },
      },
    });

    const engine = this.server.scopeEnginesController.getEngineIfExists({
      type: ScopeType.TEAM_ANSWERABLE,
      teamId: this.teamId,
      puzName: this.puzName,
    });
    if (engine !== undefined)
      engine.ctx.update(engine.ctx.scope, {
        updSubmissions: upds,
        numGuessesRemaining,
      });
  }

  async checkAnswerAsync(
    ctx: RequestContext,
    submittedAnswerUnnormalized: string
  ): Promise<SubmitAnswerResult | null> {
    const puzData = this.server.getPuzzle(this.puzName);
    const { answer: answerSpec } = puzData;
    if (answerSpec === null)
      // We shouldn't show the answer submission form if there's no answer,
      // but maybe there's a misbehaving client.
      return ctx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_PUZZLE, null);

    const teamController = this.getTeamController();
    const { progressTracker } = teamController;
    if (progressTracker.hasSolvePuzzle(this.puzName))
      return { type: SubmitAnswerResultType.ALREADY_SOLVED };
    const previousAnswersUnnormalized = [
      ...progressTracker.getSolveAttemptRecordsForPuzzle(this.puzName),
    ].flatMap(({ info }) => (info.answer === undefined ? [] : [info.answer]));
    const puzzleMessages =
      this.server.puzzlesDB.puzzleMessages[this.puzName] ?? {};

    // Maintain the guess limit for logged-in teams even after the
    // hunt ends since we don't have a rate limit.
    if (
      !(this.server.opts.isPosthunt ?? false) &&
      this.getNumGuessesRemaining() <= 0
    ) {
      return { type: SubmitAnswerResultType.NO_GUESSES };
    }

    this.server.checkAnswerMetrics.record();
    const submitAnswerResult = checkAnswer(
      submittedAnswerUnnormalized,
      answerSpec,
      puzData,
      puzzleMessages,
      previousAnswersUnnormalized
    );

    if (submitAnswerResult.type === SubmitAnswerResultType.SUCCESS) {
      await teamController.modifyTeamProgressAsync((progressTracker) => {
        const solveAttemptInfo = submitAnswerResult.info;
        const addSolveAttempt = (overrides?: Partial<SolveAttemptInfo>) => {
          return progressTracker.addSolveAttempt(this.puzName, {
            ...solveAttemptInfo,
            ...overrides,
          });
        };
        const addSolveAttemptWithMessage = (message?: string) => {
          return addSolveAttempt({ message });
        };

        // special case for #322
        // if submitting the "wrong" answer, award a canned hint if they don't have one
        if (this.puzName === "puz322") {
          const currCannedHints =
            progressTracker.view.puzzleNumTotalCannedHints.get(this.puzName) ??
            0;

          if (
            solveAttemptInfo.message?.includes("Check the canned hints") && // "wrong" answer submitted
            currCannedHints === 0 // no canned hints
          ) {
            progressTracker.addEvent(TeamProgressEventType.GET_CANNED_HINT, {
              num: 1,
              puzName: this.puzName,
              reason: {
                type: TeamProgressEventReasonType.MISC,
              },
            });
          }
        }

        if (
          PUZZLE_CONFIG.unlockPrizePuzNames.includes(this.puzName) &&
          (solveAttemptInfo.isCorrect ?? false)
        ) {
          const teamGachaEngine = this.server.getOrCreateEngineOrThrow({
            type: ScopeType.TEAM_GACHA,
            teamId: this.teamId,
          });
          const analysis = teamGachaEngine.computeAnalysis(progressTracker);
          const prize = teamGachaEngine.getPuzzlePrize(
            progressTracker,
            analysis
          );
          if (prize === null) {
            return addSolveAttemptWithMessage(
              "You solved the puzzle, but you didn't get anything for it..."
            );
          }

          progressTracker.addUnlockPuzzle(prize.puzName);
          return addSolveAttemptWithMessage(
            "You solved the puzzle, but all you got was another puzzle..."
          );
        }

        if (
          PUZZLE_CONFIG.hintPrizePuzNames.includes(this.puzName) &&
          (solveAttemptInfo.isCorrect ?? false)
        ) {
          const teamGachaEngine = this.server.getOrCreateEngineOrThrow({
            type: ScopeType.TEAM_GACHA,
            teamId: this.teamId,
          });
          const analysis = teamGachaEngine.computeAnalysis(progressTracker);
          analysis.cannedHintEligiblePuzzles.delete(this.puzName);
          const prize = teamGachaEngine.getCannedHintPrize(
            progressTracker,
            analysis
          );
          if (prize === null) {
            return addSolveAttemptWithMessage(
              "You solved the puzzle! Unfortunately, none of your open puzzles were eligible for a free hint."
            );
          }

          progressTracker.addEvent(TeamProgressEventType.GET_CANNED_HINT, {
            num: 1,
            puzName: prize.puzName,
            reason: { type: TeamProgressEventReasonType.MISC },
          });
          return addSolveAttempt({
            solveHintPrizePuzName: prize.puzName,
          });
        }

        return addSolveAttempt();
      });
    }

    return submitAnswerResult;
  }
}
