type FarmerGraphSpec = {
  imgName: string;
  numNodes: number;
  /**
   * Check if the graph meets the spec. Assumes the graph has the
   * correct number of nodes.
   */
  checkGraph: (graph: FarmerGraph) => boolean;
};

export type FarmerStage = {
  displayName: string;
  instruction?: string;
  checkNode?: (node: FarmerGraphNode) => boolean;
  checkEdge?: (src: FarmerGraphNode, dst: FarmerGraphNode) => boolean;
  category?: FarmerStageCategory;
  targetGraphSpec?: FarmerGraphSpec;
  targetLetter?: string;
};

const haveDistinctChars = (s1: string, s2: string) =>
  [...s1].every((c) => !s2.includes(c));

const getFarmerAdjGraph = ({
  nodes,
  edges,
}: FarmerGraph): {
  [nodeId: string]: Set<string>;
} => {
  const adj: { [nodeId: string]: Set<string> } = Object.fromEntries(
    nodes.map(({ nodeId }) => [nodeId, new Set()])
  );
  for (const { nodeId1, nodeId2 } of edges) {
    adj[nodeId1].add(nodeId2);
    adj[nodeId2].add(nodeId1);
  }
  return adj;
};

const checkRegularGraph = (graph: FarmerGraph, layerSizes: number[]) => {
  const nodeIds = graph.nodes.map(({ nodeId }) => nodeId);
  const adjGraph = getFarmerAdjGraph(graph);
  const degrees = Object.fromEntries(
    Object.entries(adjGraph).map(([nodeId, adj]) => [nodeId, adj.size])
  );
  const seenNodeIds = new Set<string>();
  const getLayerAdjacentUnseen = (layer: Set<string>): Set<string> => {
    const newLayer = new Set<string>();
    for (const nodeId of layer) {
      for (const otherNodeId of adjGraph[nodeId]) {
        if (seenNodeIds.has(otherNodeId)) continue;
        newLayer.add(otherNodeId);
      }
    }
    return newLayer;
  };
  let currLayer = new Set(nodeIds.filter((nodeId) => degrees[nodeId] === 1));
  for (const layerSize of layerSizes) {
    if (currLayer.size !== layerSize) return false;
    const layerDegrees = [...currLayer].map((nodeId) => degrees[nodeId]);
    if (!layerDegrees.every((deg) => deg === layerDegrees[0])) return false;
    for (const nodeId of currLayer) seenNodeIds.add(nodeId);
    currLayer = getLayerAdjacentUnseen(currLayer);
  }
  return true;
};

const chain49GraphSpec: FarmerGraphSpec = {
  imgName: "chain49",
  numNodes: 49,
  checkGraph: (graph) => {
    const adjGraph = getFarmerAdjGraph(graph);
    return Object.values(adjGraph).every((adj) => [1, 2].includes(adj.size));
  },
};
