const FARMER_UNLOCK_EDGES_ORDERED: [string, string][] = [
  ["base", "substring"],
  ["base", "animals"],
  ["base", "exclusive"],
  ["substring", "scrabble"],
  ["substring", "keyboard"],
  ["substring", "anagrams"],
  ["animals", "stars"],
  ["animals", "sports"],
  ["animals", "music"],
  ["exclusive", "edit"],
  ["exclusive", "initials"],
  ["exclusive", "chain"],
];
const FARMER_EXTRACT_PATH = [
  "scrabble",
  "keyboard",
  "anagrams",
  "stars",
  "sports",
  "music",
  "edit",
  "initials",
  "chain",
  "substring",
  "animals",
  "exclusive",
];
export const FARMER_START_STAGE = "base";

const sortEdges = (edges: [string, string][]) =>
  edges.sort(([from1, to1], [from2, to2]) => {
    const diffFrom = from1.localeCompare(from2);
    if (diffFrom !== 0) return diffFrom;
    return to1.localeCompare(to2);
  });
export const FARMER_STAGES = Object.fromEntries(
  [...FARMER_STAGES_ORDERED]
    .sort((stage1, stage2) => stage1.graphId.localeCompare(stage2.graphId))
    .map(({ graphId, ...stage }): [string, FarmerStage] => [graphId, stage])
);
export const FARMER_UNLOCK_EDGES = sortEdges([...FARMER_UNLOCK_EDGES_ORDERED]);
export const FARMER_EXTRACT_EDGES = sortEdges(
  Array.from(Array(FARMER_EXTRACT_PATH.length - 1).keys(), (i) => [
    FARMER_EXTRACT_PATH[i],
    FARMER_EXTRACT_PATH[i + 1],
  ])
);

if (FARMER_STAGES[FARMER_START_STAGE] === undefined)
  throw new Error(`stage ${FARMER_START_STAGE} doesn't exist`);
for (const [from, to] of FARMER_UNLOCK_EDGES) {
  if (FARMER_STAGES[from] === undefined)
    throw new Error(`stage ${from} doesn't exist`);
  if (FARMER_STAGES[to] === undefined)
    throw new Error(`stage ${to} doesn't exist`);
}
const farmerExtractPhrase = FARMER_EXTRACT_PATH.map((graphId) => {
  const letter = FARMER_STAGES[graphId]?.targetLetter ?? null;
  if (letter === null)
    throw new Error(`no extract letter found for stage ${graphId}`);
  return letter;
}).join("");
if (farmerExtractPhrase !== "HENHOUSEFOUR")
  throw new Error(
    `farmer does not extract correctly, gives ${farmerExtractPhrase}`
  );
