import { normalizeAnswer } from "sockapi/answers";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import {
  HuntStats,
  HuntStatsUpdate,
} from "sockapi/scope-specs/hunt-stats-spec";
import { TeamProgressEventUpdateType } from "sockapi/team-progress-updates";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { ClientConnection } from "sock-server/client-connections";

export function sumMap(map: Map<unknown, number>): number {
  let sum = 0;
  for (const num of map.values()) sum += num;
  return sum;
}

export function incrementMap(
  map: Map<number, number>,
  type: TeamProgressEventUpdateType,
  timestamp: number,
  num: number
): number {
  switch (type) {
    case TeamProgressEventUpdateType.ADD: {
      if (!map.has(timestamp)) {
        map.set(timestamp, num);
        return num;
      }
      return 0;
    }
    case TeamProgressEventUpdateType.REMOVE: {
      if (map.has(timestamp)) {
        map.delete(timestamp);
        return -num;
      }
      return 0;
    }
  }
}

class HuntStatsEngine {
  ctx: ScopeEngineContext<ScopeType.HUNT_STATS>;

  members: Map<string, number>;
  wishes: Map<number, number>;
  dust: Map<number, number>;
  hintTokens: Map<number, number>;
  fairplayGuesses: Map<number, number>;
  fairplaySolves: Map<number, number>;

  state: HuntStats;
  update: HuntStatsUpdate;

  constructor(ctx: ScopeEngineContext<ScopeType.HUNT_STATS>) {
    this.ctx = ctx;

    this.members = new Map();
    this.wishes = new Map();
    this.dust = new Map();
    this.hintTokens = new Map();
    this.fairplayGuesses = new Map();
    this.fairplaySolves = new Map();

    this.state = this.calculateState();
    this.update = {};
  }

  getPermissions(conn: ClientConnection): ScopePermissions {
    if (this.ctx.server.getIsHuntOver()) return ScopePermissions.READ;
    if (this.ctx.server.getTeamController(conn.teamId).getIsFinished())
      return ScopePermissions.READ;
    return ScopePermissions.ADMIN_ONLY;
  }

  getState(): SockAPIScopeState<ScopeType.HUNT_STATS> {
    return this.state;
  }

  calculateState(): HuntStats {
    const stats: HuntStats = {
      teams: this.members.size,
      members: sumMap(this.members),
      wishes: this.wishes.size,
      dust: sumMap(this.dust),
      hintTokens: sumMap(this.hintTokens),
      fairplayGuesses: this.fairplayGuesses.size,
      fairplaySolves: this.fairplaySolves.size,
      puzzles: {},
    };
    for (const engine of Object.values(
      this.ctx.server.scopeEnginesController.getEnginesForType(ScopeType.STATS)
    ))
      stats.puzzles[engine.ctx.scope.puzName] = engine.summary;
    return stats;
  }

  handleMembers(
    type: TeamProgressEventUpdateType,
    teamId: string,
    num: number
  ): true {
    const prev = this.members.get(teamId);
    switch (type) {
      case TeamProgressEventUpdateType.ADD: {
        if (prev === undefined)
          this.update.teams = (this.update.teams ?? 0) + 1;
        if ((prev ?? 0) !== num)
          this.update.members = (this.update.members ?? 0) + num - (prev ?? 0);
        this.members.set(teamId, num);
        return true;
      }
      case TeamProgressEventUpdateType.REMOVE: {
        if (prev !== undefined)
          this.update.teams = (this.update.teams ?? 0) - 1;
        if ((prev ?? 0) !== 0)
          this.update.members = (this.update.members ?? 0) - (prev ?? 0);
        this.members.delete(teamId);
        return true;
      }
    }
  }

  handleSpinGacha(type: TeamProgressEventUpdateType, timestamp: number) {
    const delta = incrementMap(this.wishes, type, timestamp, 1);
    if (delta !== 0) this.update.wishes = (this.update.wishes ?? 0) + delta;
  }

  handleGetCollectible(
    type: TeamProgressEventUpdateType,
    timestamp: number,
    collectibleId: string,
    num: number
  ) {
    if (collectibleId !== "dust") return true;
    const delta = incrementMap(this.dust, type, timestamp, num);
    if (delta !== 0) this.update.dust = (this.update.dust ?? 0) + delta;
  }

  handleGetHintToken(
    type: TeamProgressEventUpdateType,
    timestamp: number,
    num: number
  ) {
    const delta = incrementMap(this.hintTokens, type, timestamp, num);
    if (delta !== 0)
      this.update.hintTokens = (this.update.hintTokens ?? 0) + delta;
  }

  handleSolveAttempt(
    type: TeamProgressEventUpdateType,
    timestamp: number,
    answer: string,
    isCorrect: boolean
  ) {
    if (normalizeAnswer(answer) !== "FAIRPLAY") return true;
    const delta = incrementMap(this.fairplayGuesses, type, timestamp, 1);
    if (delta !== 0)
      this.update.fairplayGuesses = (this.update.fairplayGuesses ?? 0) + delta;
    if (isCorrect) {
      const delta = incrementMap(this.fairplaySolves, type, timestamp, 1);
      if (delta !== 0)
        this.update.fairplaySolves = (this.update.fairplaySolves ?? 0) + delta;
    }
  }

  flushAllUpdates() {
    for (const engine of Object.values(
      this.ctx.server.scopeEnginesController.getEnginesForType(
        ScopeType.ADMIN_STATS
      )
    ))
      engine.flushUpdate();
    for (const engine of Object.values(
      this.ctx.server.scopeEnginesController.getEnginesForType(ScopeType.STATS)
    ))
      if (engine.flushUpdate())
        (this.update.puzzles ??= {})[engine.ctx.scope.puzName] = engine.summary;
    if (Object.keys(this.update).length === 0) return;
    this.ctx.update(this.ctx.scope, this.update);
    this.state = this.calculateState();
    this.update = {};
  }
}

export type HuntStatsServerScopeSpec = {
  engine: HuntStatsEngine;
};

export const huntStatsServerScopeHooks: ServerScopeHooks<ScopeType.HUNT_STATS> =
  {
    createEngine: ({ execCtx, engineCtx }) => {
      return new HuntStatsEngine(engineCtx);
    },
    engineIdentityFunc: (engine) => engine,
  };
