import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { ClientConnection } from "sock-server/client-connections";

class CannedHintEngine {
  ctx: ScopeEngineContext<ScopeType.CANNED_HINT>;
  hintContent: string;
  /** Used to mark canned hints that were deleted. */
  isInvalid: boolean;

  constructor(
    ctx: ScopeEngineContext<ScopeType.CANNED_HINT>,
    hintContent: string
  ) {
    this.ctx = ctx;
    this.hintContent = hintContent;
    this.isInvalid = false;
  }

  getPermissions(conn: ClientConnection) {
    if (this.isInvalid) return ScopePermissions.ADMIN_ONLY;
    return ScopePermissions.FULL;
  }

  getState() {
    return { content: this.hintContent };
  }

  updateContent() {
    const {
      server,
      scope: { puzName, index },
    } = this.ctx;
    const newState = server.getCannedHintsForPuzzle(puzName)[index];
    // Deal with deleted canned hints separately.
    if (newState === undefined) {
      this.isInvalid = true;
      return;
    }
    if (this.hintContent === newState) return;

    this.hintContent = newState;
    this.ctx.assign(this.ctx.scope, { content: this.hintContent });
  }
}

export type CannedHintServerScopeSpec = {
  engine: CannedHintEngine;
};

export const cannedHintServerScopeHooks: ServerScopeHooks<ScopeType.CANNED_HINT> =
  {
    createEngine: ({ execCtx, engineCtx }) => {
      const { server, scope } = engineCtx;
      const { puzName, index } = scope;
      const hintContent = server.getCannedHintsForPuzzle(puzName)[index];
      if (hintContent === undefined)
        return execCtx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_HINT, null);
      return new CannedHintEngine(engineCtx, hintContent);
    },
    engineIdentityFunc: (engine) => engine,
  };
