import {
  BackendSlowSyncReqPacket,
  SyncTracker,
  SyncUpdatesTracker,
} from "sock-server/backend-interface/backend-sync";

const mergeSlowSyncPacket = (
  upd1: BackendSlowSyncReqPacket,
  upd2: BackendSlowSyncReqPacket
): void => {
  const { isInactive, isHidden, displayName, viewTimes, hints } = upd2;
  if (isInactive !== undefined) upd1.isInactive = isInactive;
  if (isHidden !== undefined) upd1.isHidden = isHidden;
  if (displayName !== undefined) upd1.displayName = displayName;
  upd1.viewTimes = Object.assign(upd1.viewTimes ?? {}, viewTimes ?? {});
  upd1.hints = Object.assign(upd1.hints ?? {}, hints ?? {});
};

export class SlowSyncTracker extends SyncUpdatesTracker<BackendSlowSyncReqPacket> {
  constructor(syncTracker: SyncTracker) {
    super(
      syncTracker,
      () => ({}),
      (upd1, upd2) => mergeSlowSyncPacket(upd1, upd2)
    );
  }
}
