import { objectEntriesUnsafe } from "util/util";
import { HopeSnapshot } from "sockapi/gacha/hope";
import {
  TeamProgressEventType,
  TeamProgressEventRecord,
  TeamProgress,
  getTeamProgressEventsForType,
} from "sockapi/team-progress";

export enum TeamProgressEventUpdateType {
  ADD = "add",
  REMOVE = "remove",
}

export type TeamProgressEventUpdate<
  TEventType extends TeamProgressEventType = TeamProgressEventType
> =
  | {
      type: TeamProgressEventUpdateType.ADD;
      eventRecord: TeamProgressEventRecord<TEventType>;
    }
  | {
      type: TeamProgressEventUpdateType.REMOVE;
      // Provide the full eventRecord so that we can handle broadcasting
      // updates after the removal has been committed.
      // We don't need to optimize here since we should only rarely
      // be doing event removal in production.
      eventRecord: TeamProgressEventRecord<TEventType>;
    };

export type TeamProgressUpdateForType<
  TEventType extends TeamProgressEventType
> = {
  [timestamp: number]: TeamProgressEventUpdate<TEventType>;
};

export type TeamProgressUpdate = {
  events?: {
    [TEventType in TeamProgressEventType]?: TeamProgressUpdateForType<TEventType>;
  };
  hopeSnapshot?: HopeSnapshot;
  /**
   * Indicates that this update includes all triggered events,
   * and will not trigger any more events. This should only
   * be set by TeamProgressTracker after triggering updates
   * and computing a diff.
   */
  isComplete?: true;
};

const getTeamProgressUpdateEvents = (
  upd: TeamProgressUpdate
): {
  [TEventType in TeamProgressEventType]?: TeamProgressUpdateForType<TEventType>;
} => {
  const { events } = upd;
  if (events !== undefined) return events;
  const newEvents: {
    [TEventType in TeamProgressEventType]?: TeamProgressUpdateForType<TEventType>;
  } = {};
  upd.events = newEvents;
  return newEvents;
};

export const getTeamProgressUpdateForType = <
  TEventType extends TeamProgressEventType
>(
  upd: TeamProgressUpdate,
  eventType: TEventType
): TeamProgressUpdateForType<TEventType> => {
  const events = getTeamProgressUpdateEvents(upd);
  const existingUpdsForType = events[eventType];
  if (existingUpdsForType !== undefined) return existingUpdsForType;
  const updsForType: (typeof events)[TEventType] = {};
  events[eventType] = updsForType;
  return updsForType;
};

/** Merge updates from upd2 into upd1. */
export const mergeTeamProgressUpdate = (
  upd1: TeamProgressUpdate,
  upd2: TeamProgressUpdate
): void => {
  if ((upd1.isComplete ?? false) !== (upd2.isComplete ?? false))
    throw new Error("should not mix complete/incomplete updates when merging");

  for (const [eventType, updsForType2] of objectEntriesUnsafe(
    upd2.events ?? {}
  )) {
    if (updsForType2 === undefined) continue;
    const updsForType1 = getTeamProgressUpdateForType(upd1, eventType);
    Object.assign(updsForType1, updsForType2);
  }

  if (upd2.hopeSnapshot !== undefined) {
    upd1.hopeSnapshot = upd2.hopeSnapshot;
  }
};

export const applyTeamProgressUpdateNoTrigger = (
  teamProgress: TeamProgress,
  upd: TeamProgressUpdate
) => {
  for (const [eventType, updsForType] of objectEntriesUnsafe(
    upd.events ?? {}
  )) {
    if (updsForType === undefined) continue;
    const eventsForType = getTeamProgressEventsForType(teamProgress, eventType);
    for (const key in updsForType) {
      const timestamp = Number(key);
      const { type, eventRecord } = updsForType[timestamp];
      switch (type) {
        case TeamProgressEventUpdateType.ADD:
          eventsForType[timestamp] = eventRecord;
          break;
        case TeamProgressEventUpdateType.REMOVE:
          delete eventsForType[timestamp];
          break;
        default:
          throw new Error(`unexpected update type ${type}`);
      }
    }
  }

  if (upd.hopeSnapshot !== undefined) {
    teamProgress.hopeSnapshot = { ...upd.hopeSnapshot };
  }
};
