import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import { OpenCannedHintResponseType } from "sockapi/scope-specs/team-canned-hints-spec";
import { TeamProgressEventType } from "sockapi/team-progress";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";
import { TeamProgressUpdateDiff } from "sock-server/team-progress/team-progress-diffs";
import { TeamController } from "sock-server/server-controllers/team-controller";

class TeamCannedHintsEngine {
  ctx: ScopeEngineContext<ScopeType.TEAM_CANNED_HINTS>;
  teamController: TeamController;

  constructor(
    ctx: ScopeEngineContext<ScopeType.TEAM_CANNED_HINTS>,
    teamController: TeamController
  ) {
    this.ctx = ctx;
    this.teamController = teamController;
  }

  getPermissions(conn: ClientConnection) {
    return conn.teamId === this.ctx.scope.teamId
      ? ScopePermissions.FULL
      : ScopePermissions.ADMIN_ONLY;
  }

  getPuzzleCannedHintsState(puzName: string) {
    const { server } = this.ctx;
    const { progressTracker } = this.teamController;
    return {
      numTotal:
        progressTracker.view.puzzleNumTotalCannedHints.get(puzName) ?? 0,
      numOpened:
        progressTracker.view.puzzleNumOpenedCannedHints.get(puzName) ?? 0,
      numMax: server.getCannedHintsForPuzzle(puzName).length,
    };
  }

  getState() {
    const { progressTracker } = this.teamController;

    return {
      cannedHints: Object.fromEntries(
        Array.from(
          progressTracker.view.puzzleNumTotalCannedHints.keys(),
          (puzName) => [puzName, this.getPuzzleCannedHintsState(puzName)]
        )
      ),
    };
  }

  handlePuzzlesUpdate(puzNames: string[]) {
    if (puzNames.length > 0)
      this.ctx.update(this.ctx.scope, {
        cannedHints: Object.fromEntries(
          puzNames.map((puzName) => [
            puzName,
            this.getPuzzleCannedHintsState(puzName),
          ])
        ),
      });
  }

  handleTeamProgressUpdate(diff: TeamProgressUpdateDiff): void {
    const { puzzleCannedHints } = diff;

    if (puzzleCannedHints === undefined) return;

    this.handlePuzzlesUpdate(Object.keys(puzzleCannedHints));
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.TEAM_CANNED_HINTS>
  ) {
    const { openCannedHint } = payload;
    if (openCannedHint !== undefined) {
      const { puzName, index } = openCannedHint;
      await this.teamController.modifyTeamProgressAsync(
        async (progressTracker) => {
          const numTotal =
            progressTracker.view.puzzleNumTotalCannedHints.get(puzName) ?? 0;
          const numOpened =
            progressTracker.view.puzzleNumOpenedCannedHints.get(puzName) ?? 0;

          // Detect race conditions.
          if (
            !Number.isInteger(index) ||
            index !== numOpened ||
            numOpened >= numTotal
          )
            return reqCtx.sendStepResp(this.ctx.scope, {
              type: OpenCannedHintResponseType.INVALID_INDEX,
            });

          progressTracker.addEvent(TeamProgressEventType.OPEN_CANNED_HINT, {
            puzName,
          });

          reqCtx.sendStepResp(this.ctx.scope, {
            type: OpenCannedHintResponseType.SUCCESS,
          });
        }
      );
    }
  }
}

export type TeamCannedHintsServerScopeSpec = {
  engine: TeamCannedHintsEngine;
};

export const teamCannedHintsServerScopeHooks: ServerScopeHooks<ScopeType.TEAM_CANNED_HINTS> =
  {
    createEngine: ({ execCtx, engineCtx }) => {
      const { controller, scope } = engineCtx;
      const teamController = controller.getTeamControllerIfExists(scope.teamId);
      if (teamController === null)
        return execCtx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_TEAM, null);
      return new TeamCannedHintsEngine(engineCtx, teamController);
    },
    engineIdentityFunc: (engine) => engine,
  };
