// All functions here assume normalized input.

export const getVowels = (s: string) => s.replace(/[^AEIOU]/g, "");
export const getConsonants = (s: string) => s.replace(/[AEIOU]/g, "");

export const getCharScrabbleScore = (c: string): number => {
  if ("AEIOULNSTR".includes(c)) return 1;
  if ("DG".includes(c)) return 2;
  if ("BCMP".includes(c)) return 3;
  if ("FHVWY".includes(c)) return 4;
  if ("K".includes(c)) return 5;
  if ("JX".includes(c)) return 8;
  if ("QZ".includes(c)) return 10;
  throw new Error(`unhandled char ${c} for scrabble score`);
};

export const getScrabbleScore = (s: string) =>
  [...s].reduce((acc, c) => acc + getCharScrabbleScore(c), 0);

export const containsAnagramOf = (s1: string, s2: string) => {
  if (s2.length < s1.length) return false;
  const s2Remainder = [...s2];
  for (const c of s1) {
    const index = s2Remainder.indexOf(c);
    if (index === -1) return false;
    s2Remainder.splice(index, 1);
  }
  return true;
};

export const getEditDistance = (s1: string, s2: string) => {
  const dp = Array.from(Array(s1.length + 1), () => Array(s2.length + 1));
  for (let i = 0; i <= s1.length; i++) dp[i][0] = i;
  for (let j = 0; j <= s2.length; j++) dp[0][j] = j;
  for (let i = 1; i <= s1.length; i++) {
    for (let j = 1; j <= s2.length; j++) {
      if (s1[i - 1] === s2[j - 1]) dp[i][j] = dp[i - 1][j - 1];
      else
        dp[i][j] = 1 + Math.min(dp[i - 1][j], dp[i][j - 1], dp[i - 1][j - 1]);
    }
  }
  return dp[s1.length][s2.length];
};

export const getLongestCommonSubstringLength = (s1: string, s2: string) => {
  const dp = Array.from(Array(s1.length + 1), () => Array(s2.length + 1));
  for (let i = 0; i <= s1.length; i++) dp[i][0] = 0;
  for (let j = 0; j <= s2.length; j++) dp[0][j] = 0;
  for (let i = 1; i <= s1.length; i++) {
    for (let j = 1; j <= s2.length; j++) {
      if (s1[i - 1] === s2[j - 1]) dp[i][j] = 1 + dp[i - 1][j - 1];
      else dp[i][j] = 0;
    }
  }
  return Math.max(...dp.map((row) => Math.max(...row)));
};

