export const canSpellWithChemicalElements = (s: string) => {
  const dp = Array(s.length + 1);
  dp[0] = true;
  for (let i = 1; i <= s.length; i++) {
    dp[i] = CHEMICAL_ELEMENT_SYMBOLS.some((sym) => {
      if (i - sym.length < 0) return false;
      if (s.slice(i - sym.length, i) !== sym) return false;
      return dp[i - sym.length];
    });
  }
  return dp[s.length];
};

export const getCharKeyboardRow = (c: string): number => {
  if ("QWERTYUIOP".includes(c)) return 1;
  if ("ASDFGHJKL".includes(c)) return 2;
  if ("ZXCVBNM".includes(c)) return 3;
  throw new Error(`unhandled char ${c} for keyboard row`);
};
