import {
  BackendInterface,
  BackendReqType,
  BackendReq,
  BackendResp,
} from "sock-server/backend-interface/BackendInterface";

type BackendInterfaceControllerOpts = {
  backend: BackendInterface;
  dumpBackendRequests: boolean;
};

export class BackendInterfaceController implements BackendInterface {
  dumpRequests: boolean;
  backend: BackendInterface;

  constructor(opts: BackendInterfaceControllerOpts) {
    this.dumpRequests = opts.dumpBackendRequests;
    this.backend = opts.backend;
  }

  getDefaultTeamId(): string | null {
    return this.backend.getDefaultTeamId();
  }

  async sendReqAsync<TReqType extends BackendReqType>(
    req: BackendReq<TReqType>
  ): Promise<BackendResp<TReqType>> {
    if (this.dumpRequests)
      console.log(`backend < ${JSON.stringify(req, null, 2)}`);
    const resp = await this.backend.sendReqAsync(req);
    if (this.dumpRequests)
      console.log(`backend > ${JSON.stringify(resp, null, 2)}`);
    return resp;
  }
}
