import {
  PuzzleContent,
  arePuzzleContentEqual,
} from "sockapi/scope-specs/puzzle-content-spec";
import { isSolutionPageAvailable } from "sockapi/scope-specs/solution-content-spec";
import { ScopeType } from "sockapi/scope-types";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { ClientConnection } from "sock-server/client-connections";

class SolutionContentEngine {
  ctx: ScopeEngineContext<ScopeType.SOLUTION_CONTENT>;
  rawContent?: PuzzleContent;

  constructor(
    ctx: ScopeEngineContext<ScopeType.SOLUTION_CONTENT>,
    rawContent?: PuzzleContent
  ) {
    this.ctx = ctx;
    this.rawContent = rawContent;
  }

  getPermissions(conn: ClientConnection) {
    const {
      server,
      scope: { puzName },
    } = this.ctx;
    if (server.getIsHuntOver()) return ScopePermissions.READ;
    const teamController = server.getTeamController(conn.teamId);
    const { solutionInEdges = [] } = server.getPuzzle(puzName);
    if (
      !isSolutionPageAvailable(
        puzName,
        teamController.isPuzzleUnlocked(puzName),
        teamController.isPuzzleSolved(puzName)
      ) ||
      (solutionInEdges.length > 0 &&
        !teamController.progressTracker.isPuzzleSolutionReqsSatisfied(puzName))
    ) {
      return ScopePermissions.ADMIN_ONLY;
    }
    return ScopePermissions.READ;
  }

  getState() {
    const {
      server,
      scope: { puzName },
    } = this.ctx;
    const puzData = server.tryGetPuzzle(puzName);
    const {
      customPuzzleSolutionContent,
      answer = null,
      authors,
      otherCredits,
    } = puzData ?? {};
    return {
      ...(customPuzzleSolutionContent !== undefined &&
      this.rawContent === undefined
        ? {
            hash: "",
            content: customPuzzleSolutionContent,
          }
        : this.rawContent ?? {
            hash: "",
            content: "",
          }),
      answer,
      authors,
      otherCredits,
    };
  }

  updateContent(): void {
    const {
      server,
      scope: { puzName },
    } = this.ctx;
    const newContent = server.puzzlesDB.content.solutions[puzName];
    if (arePuzzleContentEqual(newContent, this.rawContent)) return;

    this.rawContent = newContent;
    this.ctx.assign(this.ctx.scope, this.getState());
  }
}

export type SolutionContentServerScopeSpec = {
  engine: SolutionContentEngine;
};

export const solutionContentServerScopeHooks: ServerScopeHooks<ScopeType.SOLUTION_CONTENT> =
  {
    createEngine: ({ execCtx, engineCtx }) => {
      const { server, scope } = engineCtx;
      const { puzName } = scope;

      return new SolutionContentEngine(
        engineCtx,
        server.puzzlesDB.content.solutions[puzName]
      );
    },
    engineIdentityFunc: (engine) => engine,
  };
