import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import {
  DirtyStateType,
  DirtyState,
} from "sockapi/scope-specs/server-health-spec";
import { TeamProgressUpdate } from "sockapi/team-progress-updates";
import { SockServerConfig, SyncObjectType } from "sockapi/server-config";
import { SockServerClock } from "sock-server/SockServer";

export type BackendFastSyncReqPacket = {
  teamProgressUpd?: TeamProgressUpdate;
};

export type BackendSlowSyncReqPacket = {
  /** Inactive state to update. */
  isInactive?: boolean;
  /** Hidden state to update. */
  isHidden?: boolean;
  /** Display name to update */
  displayName?: string;
  /** View times to update. Only used for battles. */
  viewTimes?: { [puzName: string]: number };
  /** Hint state. */
  hints?: { [hintId: string]: SockAPIScopeState<ScopeType.HINT> };
};

export type BackendSyncReqPacket = BackendFastSyncReqPacket &
  BackendSlowSyncReqPacket;

/**
 * Utility class to manage sync loops. A user of this controller
 * independently manages a queue of sync tasks, and provides a
 * runSyncAsync method that performs a single sync of some subset
 * of the sync tasks. startLoopAsync can then be used to repeatedly
 * run the provided runSyncAsync until there is nothing left to sync.
 *
 * The user may also add callbacks that wait on pending syncs.
 * These are called once a single iteration of the sync loop completes,
 * because those callbacks do not care if any additional sync tasks
 * are added while that iteration was running.
 */
export class SyncLoopController {
  isSyncInFlight: boolean;
  /** Callbacks waiting on the sync. */
  pendingCallbacks: (() => void)[];
  /**
   * Performs a sync if there is anything to sync at call time.
   * Returns true if the loop is complete, i.e. we have tried to
   * sync all pending sync items.
   */
  runSyncAsync: () => Promise<boolean>;

  constructor(runSyncAsync: () => Promise<boolean>) {
    this.isSyncInFlight = false;
    this.pendingCallbacks = [];
    this.runSyncAsync = runSyncAsync;
  }

  /**
   * Run the sync loop, if one isn't already running.
   * This does not guarantee the completion of any sync tasks.
   * If we need to wait for sync tasks, use triggerLoopAndWaitAsync.
   */
  async triggerLoopAsync() {
    if (this.isSyncInFlight) return;
    this.isSyncInFlight = true;

    // Keep syncing until the loop is complete.
    while (true) {
      // Don't actually modify pendingCallbacks until runSyncAsync
      // completes without errors.
      const numCallbacks = this.pendingCallbacks.length;

      let isLoopComplete = false;
      try {
        isLoopComplete = await this.runSyncAsync();
      } catch (err) {
        // If we hit an error with this sync, break out of the loop
        // to ensure that we release the lock, and try again later
        // as part of slow sync.
        console.error(err);
        break;
      }

      // Complete the callbacks even if nothing was synced.
      for (const callback of this.pendingCallbacks.splice(0, numCallbacks)) {
        try {
          callback();
        } catch (err) {
          // If we fail here, ensure that we fully clean up state
          // by continuing to process the other callbacks.
          console.error(err);
        }
      }

      if (isLoopComplete) break;
    }

    this.isSyncInFlight = false;
  }

  /**
   * Trigger the loop and wait for current sync tasks to finish syncing.
   * Note that the loop may continue running even after this function
   * returns, if more tasks got added to the queue in the meantime.
   */
  async triggerLoopAndWaitAsync() {
    await new Promise<void>((resolve, reject) => {
      this.pendingCallbacks.push(resolve);
      // Don't block on the loop completion.
      this.triggerLoopAsync().catch(reject);
    });
  }
}

export class SyncTracker {
  serverConfig: SockServerConfig;
  clock: SockServerClock;
  syncType: SyncObjectType;
  onDirtyStateChange: (dirtyState: DirtyState) => void;
  revNumber: number;
  syncedRevNumber: number;
  modifyTime: number;
  syncTime: number;

  /** The revision number that is currently being synced, if any. */
  syncingRevNumber: number | null;

  constructor(
    serverConfig: SockServerConfig,
    clock: SockServerClock,
    syncType: SyncObjectType,
    onDirtyStateChange: (dirtyState: DirtyState) => void
  ) {
    this.serverConfig = serverConfig;
    this.clock = clock;
    this.syncType = syncType;
    this.onDirtyStateChange = onDirtyStateChange;
    this.revNumber = 0;
    this.syncedRevNumber = 0;
    const timeNow = this.clock.genTimestamp();
    this.modifyTime = timeNow;
    this.syncTime = timeNow;

    this.syncingRevNumber = null;
  }

  /** Returns if a sync is needed. */
  snapshot(): boolean {
    if (this.revNumber === this.syncedRevNumber) return false;
    if (
      this.clock.genTimestamp() - this.syncTime <
      this.serverConfig.slowSyncThrottle[this.syncType]
    )
      return false;
    // Note that we may request a sync despite syncingRevNumber
    // already being set, due to a previous failed sync. In this
    // case, just overwrite it.
    this.syncingRevNumber = this.revNumber;
    this.onDirtyStateChange({
      type: DirtyStateType.SYNCING,
      lastSyncTime: this.syncTime,
    });
    return true;
  }

  handleModify() {
    const timeNow = this.clock.genTimestamp();
    if (this.revNumber === this.syncedRevNumber) this.syncTime = timeNow;
    this.modifyTime = timeNow;
    this.revNumber++;
    this.onDirtyStateChange({
      type: DirtyStateType.DIRTY,
      lastSyncTime: this.syncTime,
    });
  }

  handleSyncResult(success: boolean) {
    if (success) {
      this.syncTime = this.clock.genTimestamp();
      if (this.syncingRevNumber === null)
        throw new Error("no sync in progress");
      this.syncedRevNumber = this.syncingRevNumber;
    }
    this.syncingRevNumber = null;
    this.onDirtyStateChange({
      type: success ? DirtyStateType.SYNCED : DirtyStateType.FAILED,
      lastSyncTime: this.syncTime,
    });
  }
}

export class SyncUpdatesTracker<TUpd> {
  syncTracker: SyncTracker;
  private makeEmptyUpd: () => TUpd;
  private mergeUpdatesFunc: (upd1: TUpd, upd2: TUpd) => void;

  /**
   * Updates that have not been sent to the backend.
   * This does not include in-flight updates.
   */
  pendingUpdate?: TUpd;
  /** Updates currently being persisted to the backend. */
  inFlightUpdate?: TUpd;

  constructor(
    syncTracker: SyncTracker,
    makeEmptyUpd: () => TUpd,
    mergeUpdatesFunc: (upd1: TUpd, upd2: TUpd) => void
  ) {
    this.syncTracker = syncTracker;
    this.makeEmptyUpd = makeEmptyUpd;
    this.mergeUpdatesFunc = mergeUpdatesFunc;
  }

  mergeUpdate(upd: TUpd): void {
    if (this.pendingUpdate === undefined) this.pendingUpdate = upd;
    else this.mergeUpdatesFunc(this.pendingUpdate, upd);
    this.syncTracker.handleModify();
  }

  private mergePendingUpdateIntoInFlightUpdate(): void {
    if (this.pendingUpdate === undefined) return;
    const newInFlightUpdate = this.inFlightUpdate ?? this.makeEmptyUpd();
    this.mergeUpdatesFunc(newInFlightUpdate, this.pendingUpdate);
    this.inFlightUpdate = newInFlightUpdate;
    delete this.pendingUpdate;
  }

  /**
   * Starts a sync, and returns the updates to be synced
   * as a sync packet.
   *
   * Returns null if there is nothing to sync. In this case, a sync
   * is not considered to have started.
   */
  startSync(): TUpd | null {
    if (!this.syncTracker.snapshot()) return null;

    this.mergePendingUpdateIntoInFlightUpdate();
    const update = this.inFlightUpdate ?? null;
    if (update === null) {
      this.syncTracker.handleSyncResult(true);
      return null;
    }

    return update;
  }

  /**
   * If the sync succeeded, the tracker clears the synced updates
   * from future syncs.
   */
  endSync(didSyncSucceed: boolean): void {
    if (didSyncSucceed) delete this.inFlightUpdate;
    this.syncTracker.handleSyncResult(didSyncSucceed);
  }
}
