import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import { BackendScopeState } from "sockapi/backend-scope-state";
import {
  TeamBlocksProgressState,
  BLOCKS_CONFIG,
} from "sockapi/scope-specs/team-blocks-spec";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";
import { TeamController } from "sock-server/server-controllers/team-controller";

const BLOCKS_BOARD_SPECS = [
  ["(8)", "(8)", "(8)", "(8)", "(8)", "(8)"],
  ["*(8)", "(6)", "(6)", "(6)", "(6)", "(6)", "(6)", "(6)"],
  ["1(14)", "2(14)", "2(14)", "3(14)", "3(14)", "3(14)"],
  ["[8]", "(10)", "<12>", "<12>", "(10)"],
  ["(16)", "Q", "Q", "Q", "Q"],
  ["[6]", "<11>", "*R", "/e", "/e", "/w", "1N", "2N"],
  // ["<25>", "<25>", "<25>", "<25>", "!<2>"],
  ["/ne", "/ne", "/nw", "/nw", "/se", "/se", "/sw", "/sw", "7Q", "7Q"],
  ["*[2]", "*[2]", "*[2]", "*[2]", "!N"],
  ["!(19)", "3/w", "3/w", "4/w", "4/w"],
  // ["1(19)","1(19)","1(19)","![2]","*[8]","/s"],
  // ["*Q", "*/ne", "[7]", "[7]", "(7)", "(7)", "(7)", "(7)", "(7)"],
];
const BLOCKS_INSCRIPTION = `
2/ne 2/nw 2/se 2/sw *R (6) (6) (6) (6)
*Q */ne [7] [7] (7) (7) (7) (7) (7)
1(19) 1(19) 1(19) ![2] *[8] /s
`.trim();
export const BLOCKS_SERVER_CONFIG = {
  maxNumBoards: BLOCKS_BOARD_SPECS.length,
  initialNumBoards: 1,
  numBoardsPerWish: 2,
};

class TeamBlocksEngine {
  ctx: ScopeEngineContext<ScopeType.TEAM_BLOCKS>;
  teamController: TeamController;
  state: TeamBlocksProgressState;

  constructor(
    ctx: ScopeEngineContext<ScopeType.TEAM_BLOCKS>,
    teamController: TeamController,
    initState?: TeamBlocksProgressState
  ) {
    this.ctx = ctx;
    this.teamController = teamController;
    this.state = initState ?? { solvedBoards: {} };
  }

  getPermissions(conn: ClientConnection) {
    return conn.teamId === this.ctx.scope.teamId
      ? ScopePermissions.FULL
      : ScopePermissions.ADMIN_ONLY;
  }

  handleTeamProgressUpdate() {
    this.ctx.assign(this.ctx.scope, this.getState());
  }

  getNumBoardsSolved(): number {
    return Object.values(this.state.solvedBoards).filter((isSolved) => isSolved)
      .length;
  }

  getState(): SockAPIScopeState<ScopeType.TEAM_BLOCKS> {
    const numBlocksWishes =
      this.teamController.progressTracker.view.getNumBlocksWishes();
    const numBoardsUnlocked =
      BLOCKS_SERVER_CONFIG.initialNumBoards +
      numBlocksWishes * BLOCKS_SERVER_CONFIG.numBoardsPerWish;
    return {
      ...this.state,
      numWishes: numBlocksWishes,
      maxNumBoards: BLOCKS_SERVER_CONFIG.maxNumBoards,
      boardSpecs: BLOCKS_BOARD_SPECS.slice(0, numBoardsUnlocked),
      inscription:
        this.getNumBoardsSolved() >= BLOCKS_CONFIG.inscriptionNumBoards
          ? BLOCKS_INSCRIPTION
          : undefined,
    };
  }

  getSlowSyncState(): BackendScopeState<ScopeType.TEAM_BLOCKS> {
    const { solvedBoards } = this.state;
    return { solvedBoards };
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.TEAM_BLOCKS>
  ) {
    const { setBoardSolved } = payload;
    const puzName = BLOCKS_CONFIG.puzName;
    const numBoardsUnlocked = this.getState().boardSpecs.length;

    // Fail silently as this may be due to race conditions.
    // Don't check for unsolved to allow playing with the interface
    // after solving.
    if (!reqCtx.conn.isAdmin && !this.teamController.isPuzzleUnlocked(puzName))
      return reqCtx.sendStepResp(this.ctx.scope, "");

    if (setBoardSolved !== undefined) {
      const { boardIndex, isSolved } = setBoardSolved;
      if (
        !Number.isInteger(boardIndex) ||
        boardIndex < 0 ||
        boardIndex >= numBoardsUnlocked
      )
        return reqCtx.setBadRequest();
      if (isSolved) this.state.solvedBoards[boardIndex] = true;
      else delete this.state.solvedBoards[boardIndex];
      this.ctx.update(this.ctx.scope, {
        solvedBoards: this.state.solvedBoards,
        inscription:
          this.getNumBoardsSolved() >= BLOCKS_CONFIG.inscriptionNumBoards
            ? BLOCKS_INSCRIPTION
            : null,
      });
    }

    reqCtx.sendStepResp(this.ctx.scope, "");
  }
}

export type TeamBlocksServerScopeSpec = {
  engine: TeamBlocksEngine;
};

export const teamBlocksServerScopeHooks: ServerScopeHooks<ScopeType.TEAM_BLOCKS> =
  {
    createEngine: ({ execCtx, engineCtx, initState }) => {
      const { controller, scope } = engineCtx;
      const teamController = controller.getTeamControllerIfExists(scope.teamId);
      if (teamController === null)
        return execCtx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_TEAM, null);
      return new TeamBlocksEngine(engineCtx, teamController, initState);
    },
    engineIdentityFunc: (engine) => engine,
  };
