import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import { hopeSnapshotWithEvents } from "sockapi/gacha/hope";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { ClientConnection } from "sock-server/client-connections";
import { TeamController } from "sock-server/server-controllers/team-controller";

class TeamHistoryEngine {
  ctx: ScopeEngineContext<ScopeType.TEAM_HISTORY>;
  teamController: TeamController;
  state: SockAPIScopeState<ScopeType.TEAM_HISTORY>;

  constructor(
    ctx: ScopeEngineContext<ScopeType.TEAM_HISTORY>,
    teamController: TeamController
  ) {
    this.ctx = ctx;
    this.teamController = teamController;
    this.state = this.calculateState();
  }

  getPermissions(conn: ClientConnection): ScopePermissions {
    const { server, scope } = this.ctx;
    if (conn.teamId === scope.teamId) return ScopePermissions.FULL;
    if (server.getIsHuntOver()) return ScopePermissions.READ;
    return ScopePermissions.ADMIN_ONLY;
  }

  calculateState(): SockAPIScopeState<ScopeType.TEAM_HISTORY> {
    const deltas = this.teamController.progressTracker
      .getHopeChangeEvents()
      .sort(({ timestamp: a }, { timestamp: b }) => a - b);
    let snapshot =
      this.teamController.progressTracker.state.hopeSnapshot ?? null;
    const snapshots = snapshot === null ? [] : [snapshot];
    for (const delta of deltas) {
      snapshot = hopeSnapshotWithEvents(snapshot, [delta]);
      if (snapshot !== null) snapshots.push(snapshot);
    }
    return snapshots;
  }

  getState(): SockAPIScopeState<ScopeType.TEAM_HISTORY> {
    return this.state;
  }

  handleTeamProgressUpdate() {
    const newState = this.calculateState();
    let i = 0;
    for (; i < this.state.length; i++)
      if (
        this.state[i].hope !== newState[i]?.hope ||
        this.state[i].timestamp !== newState[i]?.timestamp
      )
        break;
    if (i === this.state.length && i === newState.length) return;
    this.state = newState;
    this.ctx.update(this.ctx.scope, { keep: i, add: newState.slice(i) });
  }
}

export type TeamHistoryServerScopeSpec = {
  engine: TeamHistoryEngine;
};

export const teamHistoryServerScopeHooks: ServerScopeHooks<ScopeType.TEAM_HISTORY> =
  {
    createEngine: ({ execCtx, engineCtx }) => {
      const { controller, scope } = engineCtx;
      const teamController = controller.getTeamControllerIfExists(scope.teamId);
      if (teamController === null)
        return execCtx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_TEAM, null);
      return new TeamHistoryEngine(engineCtx, teamController);
    },
    engineIdentityFunc: (engine) => engine,
  };
