import { objGetOrCreate } from "util/util";
import { PuzzlesDB } from "sock-server/puzzles/PuzzlesDB";
import { isUsedForSolveCount } from "sockapi/puzzle-data";
import { GachaPrizeType } from "sockapi/gacha/pulls";
import {
  TeamProgressGlobalCounterEventType,
  TeamProgressEventType,
  TeamProgressEventReasonType,
  TeamProgressEventRecord,
} from "sockapi/team-progress";
import {
  TeamProgressEventUpdateType,
  TeamProgressEventUpdate,
  TeamProgressUpdate,
} from "sockapi/team-progress-updates";
import TeamProgressTracker from "sock-server/team-progress/TeamProgressTracker";
import {
  TeamHopePoolCycleContribution,
  getMergedTeamHopePoolCycleContributions,
} from "sock-server/scope-specs/team-hope-pool-server-spec";
import { CollectibleState } from "sockapi/scope-specs/team-collectibles-spec";

type TeamProgressUpdateCannedHintsDiffForPuzzle = {
  diffTotal: number;
  diffOpened: number;
};

/**
 * Actual state changes upon applying an update, used to trigger
 * external effects after an update is applied.
 */
export type TeamProgressUpdateDiff = {
  solveAttempts: {
    [puzName: string]: {
      [
        timestamp: number
      ]: TeamProgressEventRecord<TeamProgressEventType.SOLVE_ATTEMPT> | null;
    };
  };
  /**
   * Newly written solve records, even if functioning as an overwrite.
   * Null if the solve was removed.
   */
  solveRecords: {
    [
      puzName: string
    ]: TeamProgressEventRecord<TeamProgressEventType.SOLVE_ATTEMPT> | null;
  };
  /**
   * Newly written unlock records, even if functioning as an overwrite.
   * Null if the unlock was removed.
   */
  unlockRecords: {
    [
      puzName: string
    ]: TeamProgressEventRecord<TeamProgressEventType.UNLOCK_PUZZLE> | null;
  };
  /**
   * If true, then the solve was newly added. If false, then the solve
   * was previously there, but just got removed.
   */
  solves: { [puzName: string]: boolean };
  /**
   * If true, then the unlock was newly added. If false, then the unlock
   * was previously there, but just got removed.
   */
  unlocks: { [puzName: string]: boolean };
  diffUnlockGroupsUnlockProgress: { [puzName: string]: number };
  diffUnlockGroupsSolveProgress: { [puzName: string]: number };
  diffNumSolvedMicropuzzles?: number;
  diffDisplayedSolveCount?: number;
  /** Change in the number of each collectible. */
  diffCollectibleState?: { [collectibleId: string]: CollectibleState };
  diffPuzzleNumExtraGuesses?: {
    [puzName: string]: number;
  };
  puzzleCannedHints?: {
    [puzName: string]: TeamProgressUpdateCannedHintsDiffForPuzzle;
  };
  numWishesSinceLastHintToken?: number;
  lastHintTokenTime?: number;
  needInvalidateHopeSnapshot?: true;
  cutsceneChoices?: { [keyframeId: string]: number };
  /** The first celestial unlock in the update. */
  firstCelestialPuzName?: string;
  maxUntitledPuzzleIndex?: number;
  hopePoolLatestCycleContribution?: TeamHopePoolCycleContribution;
  diffTeamGlobalCounters?: {
    [eventType in TeamProgressGlobalCounterEventType]?: number;
  };
};

export const makeEmptyTeamProgressUpdateDiff = (): TeamProgressUpdateDiff => ({
  solveAttempts: {},
  solveRecords: {},
  unlockRecords: {},
  solves: {},
  unlocks: {},
  diffUnlockGroupsUnlockProgress: {},
  diffUnlockGroupsSolveProgress: {},
});

const diffNumRecordsToStatusChangeDict = (diffNumRecords: {
  [puzName: string]: number;
}): { [puzName: string]: boolean } => {
  const changes: { [puzName: string]: boolean } = {};
  for (const puzName in diffNumRecords) {
    const puzDiff = diffNumRecords[puzName];
    switch (puzDiff) {
      case 0:
        break;
      case 1:
        changes[puzName] = true;
        break;
      case -1:
        changes[puzName] = false;
        break;
      // The number of correct solve attempts should always only
      // be either 0 or 1, so we can't have a diff of magnitude
      // greater than 1.
      default:
        throw new Error(
          `invalid diff for num correct solve attempts ${puzDiff}`
        );
    }
  }
  return changes;
};

export const computeTeamProgressUpdateDiff = (
  puzzlesDB: Readonly<PuzzlesDB>,
  progressTracker: TeamProgressTracker,
  upd: TeamProgressUpdate
): TeamProgressUpdateDiff => {
  const diff = makeEmptyTeamProgressUpdateDiff();

  // First, compute the results of the apply.

  const sortedUpdates: TeamProgressEventUpdate[] = Object.values(
    upd.events ?? {}
  )
    .flatMap((updsForType) => Object.values(updsForType))
    .sort(
      (upd1, upd2) => upd1.eventRecord.timestamp - upd2.eventRecord.timestamp
    );

  const diffNumUnlockRecords: { [puzName: string]: number } = {};
  const diffNumSolveRecords: { [puzName: string]: number } = {};
  const ctx = {
    diff,
    puzzlesDB,
    progressTracker,
    diffNumUnlockRecords,
    diffNumSolveRecords,
  };

  for (const upd of sortedUpdates) {
    const { type: updType, eventRecord } = upd;
    // Events should always be added after the hope snapshot.
    // If an event was removed, we need to recompute it.
    // We're not bothering to check here if the event actually
    // impacts hope.
    // Since removals in production are very rare, we don't need
    // to worry too much about optimizing this.
    if (updType === TeamProgressEventUpdateType.REMOVE)
      diff.needInvalidateHopeSnapshot = true;
    handleEvent(ctx, eventRecord, updType);
  }

  // Compute these only at the end to handle cases where consecutive
  // adds and removes does not change the status of a puzzle.
  diff.solves = diffNumRecordsToStatusChangeDict(diffNumSolveRecords);
  diff.unlocks = diffNumRecordsToStatusChangeDict(diffNumUnlockRecords);

  return diff;
};

const getPuzzleDiffCannedHints = (
  diff: TeamProgressUpdateDiff,
  puzName: string
): TeamProgressUpdateCannedHintsDiffForPuzzle => {
  return objGetOrCreate(
    objGetOrCreate(diff, "puzzleCannedHints", {}),
    puzName,
    { diffTotal: 0, diffOpened: 0 }
  );
};

const updTypeToDiff = (updType: TeamProgressEventUpdateType): number => {
  switch (updType) {
    case TeamProgressEventUpdateType.ADD:
      return 1;
    case TeamProgressEventUpdateType.REMOVE:
      return -1;
  }
};

const updGlobalCounter = (
  diff: TeamProgressUpdateDiff,
  updType: TeamProgressEventUpdateType,
  eventRecord: TeamProgressEventRecord<TeamProgressGlobalCounterEventType>
): void => {
  diff.diffTeamGlobalCounters ??= {};
  const { diffTeamGlobalCounters } = diff;
  diffTeamGlobalCounters[eventRecord.type] =
    (diffTeamGlobalCounters[eventRecord.type] ?? 0) +
    updTypeToDiff(updType) * eventRecord.info.num;
};

interface HandlerContext {
  diff: TeamProgressUpdateDiff;
  puzzlesDB: Readonly<PuzzlesDB>;
  progressTracker: TeamProgressTracker;
  diffNumUnlockRecords: { [puzName: string]: number };
  diffNumSolveRecords: { [puzName: string]: number };
}

const handlers: {
  [TEventType in TeamProgressEventType]: (
    ctx: HandlerContext,
    eventRecord: TeamProgressEventRecord<TEventType>,
    updType: TeamProgressEventUpdateType
  ) => void;
} = {
  [TeamProgressEventType.SOLVE_ATTEMPT]: (ctx, eventRecord, updType) => {
    const {
      timestamp,
      info: { puzName, isCorrect },
    } = eventRecord;

    if (isCorrect) {
      ctx.diff.needInvalidateHopeSnapshot = true;
    }

    ctx.diff.solveAttempts[puzName] ??= {};
    ctx.diff.solveAttempts[puzName][timestamp] =
      ((): TeamProgressEventRecord<TeamProgressEventType.SOLVE_ATTEMPT> | null => {
        switch (updType) {
          case TeamProgressEventUpdateType.ADD:
            return eventRecord;
          case TeamProgressEventUpdateType.REMOVE:
            return null;
        }
      })();

    if (isCorrect ?? false) {
      ctx.diffNumSolveRecords[puzName] =
        (ctx.diffNumSolveRecords[puzName] ?? 0) + updTypeToDiff(updType);
      switch (updType) {
        case TeamProgressEventUpdateType.ADD:
          ctx.diff.solveRecords[puzName] = eventRecord;
          break;
        case TeamProgressEventUpdateType.REMOVE:
          ctx.diff.solveRecords[puzName] = null;
          break;
      }

      const puzData = ctx.puzzlesDB.tryGetPuzzle(puzName, {});
      // A puzzle may not exist in the puzzlesDB if it is a custom
      // puzzle. Custom puzzles should never be unlocked automatically.
      if (puzData !== null) {
        const { unlockOutEdges } = puzData;
        for (const { unlockGroupId, val = 1 } of unlockOutEdges) {
          ctx.diff.diffUnlockGroupsSolveProgress[unlockGroupId] =
            (ctx.diff.diffUnlockGroupsSolveProgress[unlockGroupId] ?? 0) +
            updTypeToDiff(updType) * val;
        }
      }

      if (ctx.puzzlesDB.isMicropuzzle(puzName))
        ctx.diff.diffNumSolvedMicropuzzles =
          (ctx.diff.diffNumSolvedMicropuzzles ?? 0) + updTypeToDiff(updType);

      if (
        // Custom puzzles are included in the displayed solve count.
        (puzData === null || isUsedForSolveCount(puzData)) &&
        // Only include solves before hunt end.
        timestamp < ctx.progressTracker.huntEndTime
      )
        ctx.diff.diffDisplayedSolveCount =
          (ctx.diff.diffDisplayedSolveCount ?? 0) + updTypeToDiff(updType);
    }
  },
  [TeamProgressEventType.UNLOCK_PUZZLE]: (ctx, eventRecord, updType) => {
    const {
      info: { puzName, untitledPuzzleIndex },
    } = eventRecord;
    ctx.diffNumUnlockRecords[puzName] =
      (ctx.diffNumUnlockRecords[puzName] ?? 0) + updTypeToDiff(updType);
    switch (updType) {
      case TeamProgressEventUpdateType.ADD:
        ctx.diff.unlockRecords[puzName] = eventRecord;
        break;
      case TeamProgressEventUpdateType.REMOVE:
        ctx.diff.unlockRecords[puzName] = null;
        break;
    }

    const puzData = ctx.puzzlesDB.tryGetPuzzle(puzName, {});
    // A puzzle may not exist in the puzzlesDB if it is a custom
    // puzzle. Custom puzzles should never be unlocked automatically.
    if (puzData !== null) {
      const { unlockOutEdges, isCelestial } = puzData;
      for (const { unlockGroupId, val = 1 } of unlockOutEdges) {
        ctx.diff.diffUnlockGroupsUnlockProgress[unlockGroupId] =
          (ctx.diff.diffUnlockGroupsUnlockProgress[unlockGroupId] ?? 0) +
          updTypeToDiff(updType) * val;
      }

      if (isCelestial) ctx.diff.firstCelestialPuzName ??= puzName;
    }

    if (untitledPuzzleIndex !== undefined)
      ctx.diff.maxUntitledPuzzleIndex = Math.max(
        ctx.diff.maxUntitledPuzzleIndex ?? 0,
        untitledPuzzleIndex
      );
  },
  [TeamProgressEventType.SPIN_GACHA]: (ctx, eventRecord, updType) => {
    ctx.diff.needInvalidateHopeSnapshot = true;
    const {
      info: { prize },
    } = eventRecord;
    if (updType === TeamProgressEventUpdateType.ADD) {
      if (prize.type === GachaPrizeType.HINT_TOKEN) {
        ctx.diff.numWishesSinceLastHintToken = 0;
      } else {
        ctx.diff.numWishesSinceLastHintToken =
          (ctx.progressTracker.view.numWishesSinceLastHintToken ?? 0) + 1;
      }
    }
    return;
  },
  [TeamProgressEventType.GET_HOPE]: (ctx, eventRecord, updType) => {
    ctx.diff.needInvalidateHopeSnapshot = true;
    const {
      info: { hopeAmt, reason },
    } = eventRecord;
    if (updType === TeamProgressEventUpdateType.ADD) {
      if (reason.type === TeamProgressEventReasonType.HOPE_POOL) {
        const { cycleId } = reason;
        ctx.diff.hopePoolLatestCycleContribution =
          getMergedTeamHopePoolCycleContributions(
            ctx.diff.hopePoolLatestCycleContribution,
            // The hope gained would be negative since we're
            // spending hope.
            { cycleId, amt: -hopeAmt }
          );
      }
    }
    return;
  },
  [TeamProgressEventType.GET_EXTRA_GUESSES]: (ctx, eventRecord, updType) => {
    const {
      info: { puzName, num },
    } = eventRecord;
    const diffPuzzleNumExtraGuesses = objGetOrCreate(
      ctx.diff,
      "diffPuzzleNumExtraGuesses",
      {}
    );
    diffPuzzleNumExtraGuesses[puzName] =
      (diffPuzzleNumExtraGuesses[puzName] ?? 0) + updTypeToDiff(updType) * num;
  },
  [TeamProgressEventType.GET_CANNED_HINT]: (ctx, eventRecord, updType) => {
    const {
      info: { puzName, num },
    } = eventRecord;
    getPuzzleDiffCannedHints(ctx.diff, puzName).diffTotal +=
      updTypeToDiff(updType) * num;
  },
  [TeamProgressEventType.OPEN_CANNED_HINT]: (ctx, eventRecord, updType) => {
    const {
      info: { puzName },
    } = eventRecord;
    getPuzzleDiffCannedHints(ctx.diff, puzName).diffOpened +=
      updTypeToDiff(updType);
  },
  [TeamProgressEventType.GET_HINT_TOKEN]: (ctx, eventRecord, updType) => {
    const { timestamp } = eventRecord;
    updGlobalCounter(ctx.diff, updType, eventRecord);
    ctx.diff.lastHintTokenTime = timestamp;
  },
  [TeamProgressEventType.GET_COLLECTIBLE]: (ctx, eventRecord, updType) => {
    const {
      timestamp,
      info: { collectibleId, num },
    } = eventRecord;
    ctx.diff.diffCollectibleState ??= {};
    const existingState = ctx.diff.diffCollectibleState[collectibleId];
    if (existingState === undefined) {
      ctx.diff.diffCollectibleState[collectibleId] = {
        num: updTypeToDiff(updType) * num,
        timestamp,
      };
    } else {
      existingState.num += updTypeToDiff(updType) * num;
      existingState.timestamp = Math.min(existingState.timestamp, timestamp);
    }
  },
  [TeamProgressEventType.CUTSCENE_CHOICE]: (ctx, eventRecord, updType) => {
    const {
      info: { keyframeId, index },
    } = eventRecord;
    ctx.diff.cutsceneChoices ??= {};
    ctx.diff.cutsceneChoices[keyframeId] = index;
  },
  [TeamProgressEventType.GET_OLIVE_WISH]: (ctx, eventRecord, updType) => {
    updGlobalCounter(ctx.diff, updType, eventRecord);
  },
  [TeamProgressEventType.GET_BLOCKS_WISH]: (ctx, eventRecord, updType) => {
    updGlobalCounter(ctx.diff, updType, eventRecord);
  },
};

const handleEvent = <TEventType extends TeamProgressEventType>(
  ctx: HandlerContext,
  eventRecord: TeamProgressEventRecord<TEventType>,
  updType: TeamProgressEventUpdateType
) => {
  const eventType: TEventType = eventRecord.type;
  handlers[eventType](ctx, eventRecord, updType);
};
