import { ScopeType } from "sockapi/scope-types";
import { scopeToString } from "sockapi/scopes";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import { SavedHuntBackendState } from "sockapi/scope-specs/state-download-spec";
import { TeamProgressEventType } from "sockapi/team-progress";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
  doesScopeEngineHaveSlowSyncState,
} from "sock-server/scope-engines";
import { RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";
import { SCHEMA_VERSION } from "sock-server/backend-interface/MockBackendInterface";

class StateDownloadEngine {
  ctx: ScopeEngineContext<ScopeType.STATE_DOWNLOAD>;

  constructor(ctx: ScopeEngineContext<ScopeType.STATE_DOWNLOAD>) {
    this.ctx = ctx;
  }

  getPermissions(conn: ClientConnection): ScopePermissions {
    // Permissions are managed per-substep to allow clients to
    // download their state after hunt ends.
    return ScopePermissions.FULL;
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.STATE_DOWNLOAD>
  ) {
    const {
      downloadScopes,
      downloadTeams,
      downloadAll,
      downloadPublicCustomPuzzles,
      downloadPosthunt,
    } = payload;
    const { server, controller } = this.ctx;

    // Restrict what non-admins are allowed to download.
    if (!reqCtx.conn.isAdmin) {
      if (!server.getIsHuntOver()) return reqCtx.setPermissionDenied();
      const { downloadTeams, ...restOfPayload } = payload;
      if (Object.keys(restOfPayload).length > 0)
        return reqCtx.setPermissionDenied();
      for (const teamId of downloadTeams ?? []) {
        if (teamId !== reqCtx.conn.teamId) return reqCtx.setPermissionDenied();
      }
    }

    const resp: SavedHuntBackendState = {
      version: SCHEMA_VERSION,
      defaultTeamId:
        downloadTeams !== undefined && downloadTeams.length === 1
          ? downloadTeams[0]
          : undefined,
      teams: {},
      scopeState: {},
    };

    // Download these first as this also downloads skeleton team progress
    // for the authors of custom puzzles, which might get overriden by
    // later downloads with actual team progress.
    if ((downloadPosthunt ?? false) || (downloadPublicCustomPuzzles ?? false)) {
      for (const engine of Object.values(
        controller.getEnginesForType(ScopeType.TEAM_PUZZLE_105)
      )) {
        const { scope } = engine.ctx;
        if (!engine.getHasPublicCustomPuzzle()) continue;
        resp.scopeState[scopeToString(scope)] = {
          scope,
          state: engine.getPublicState(),
        };
        const { teamId } = scope;
        const { displayName, isInactive, isHidden, members } =
          server.getTeamController(teamId);
        resp.teams[teamId] = {
          displayName,
          isInactive,
          isHidden,
          members,
          viewTimes: {},
          hints: {},
          teamProgress: { events: {} },
          isPlaceholder: true,
        };
      }
    }

    for (const scope of downloadScopes ?? []) {
      const engine = controller.getEngineIfExists(scope);
      if (engine === undefined) continue;
      if (!doesScopeEngineHaveSlowSyncState(engine)) continue;
      resp.scopeState[scopeToString(scope)] = {
        scope,
        state: engine.getSlowSyncState(),
      };
    }

    if (
      downloadTeams !== undefined ||
      (downloadAll ?? false) ||
      (downloadPosthunt ?? false)
    ) {
      const downloadTeamsSet = new Set(downloadTeams ?? []);
      const allEngineTrackingState = Object.values(controller.engines)
        .map((engineTrackingStateForType) =>
          Object.values(engineTrackingStateForType)
        )
        .flat();
      Object.assign(
        resp.scopeState,
        Object.fromEntries(
          allEngineTrackingState
            .filter(({ scope }) => {
              if (downloadAll ?? false) return true;
              const { teamId: scopeTeamId }: { teamId?: string } = {
                teamId: undefined,
                ...scope,
              };
              if (scopeTeamId === undefined) return false;
              return downloadTeamsSet.has(scopeTeamId);
            })
            .flatMap(({ scope, engine }) => {
              if (!doesScopeEngineHaveSlowSyncState(engine)) return [];
              return [
                [
                  scopeToString(scope),
                  {
                    scope,
                    state: engine.getSlowSyncState(),
                  },
                ],
              ];
            })
        )
      );

      for (const teamController of server.teamControllers.values()) {
        const {
          teamId,
          displayName,
          isInactive,
          isHidden,
          members,
          progressTracker,
        } = teamController;
        if (
          !(
            (downloadAll ?? false) ||
            (downloadPosthunt ?? false) ||
            downloadTeamsSet.has(teamId)
          )
        )
          continue;
        const hintIds = !(downloadPosthunt ?? false)
          ? Object.values(teamController.getHintsEngine().state.hints)
              .map((hints) => Object.keys(hints))
              .flat()
          : [];
        const hints = Object.fromEntries(
          hintIds.flatMap((hintId) => {
            const engine = controller.getEngineIfExists({
              type: ScopeType.HINT,
              hintId,
            });
            if (engine === undefined) return [];
            return [[hintId, engine.state]];
          })
        );
        // Strip down the state for posthunt.
        const teamProgressToDownload = (() => {
          if (!(downloadPosthunt ?? false)) return progressTracker.state;
          const { events, hopeSnapshot } = progressTracker.state;
          return {
            events: {
              [TeamProgressEventType.SOLVE_ATTEMPT]:
                events[TeamProgressEventType.SOLVE_ATTEMPT],
              [TeamProgressEventType.UNLOCK_PUZZLE]:
                events[TeamProgressEventType.UNLOCK_PUZZLE],
              [TeamProgressEventType.SPIN_GACHA]:
                events[TeamProgressEventType.SPIN_GACHA],
              [TeamProgressEventType.GET_HOPE]:
                events[TeamProgressEventType.GET_HOPE],
              [TeamProgressEventType.GET_COLLECTIBLE]:
                events[TeamProgressEventType.GET_COLLECTIBLE],
            },
            hopeSnapshot,
          };
        })();
        resp.teams[teamId] = {
          displayName,
          isInactive,
          isHidden,
          members,
          viewTimes: {},
          hints,
          teamProgress: teamProgressToDownload,
        };
      }
    }

    return reqCtx.sendStepResp(this.ctx.scope, resp);
  }
}

export type StateDownloadServerScopeSpec = {
  engine: StateDownloadEngine;
};

export const stateDownloadServerScopeHooks: ServerScopeHooks<ScopeType.STATE_DOWNLOAD> =
  {
    createEngine: ({ engineCtx }) => {
      return new StateDownloadEngine(engineCtx);
    },
    engineIdentityFunc: (engine) => engine,
  };
