import { SockAPIErrorCode } from "sockapi/errors";
import { normalizeAnswer } from "sockapi/answers";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import {
  SockAPIScopeUpdatePayload,
  applyScopeUpdate,
} from "sockapi/scope-updates";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import {
  PUZZLE_105_CONFIG,
  Puzzle105SharePermission,
  Puzzle105CheckTitleResponse,
  Puzzle105StatusType,
  Puzzle105Status,
  getTeamPuzzle105ReopenTime,
  getTeamPuzzle105ReviewTime,
  isTeamPuzzle105Editable,
  validateTeamPuzzle105,
  validateTeamPuzzle105MaxLengths,
  getTeamPuzzle105PosthuntState,
} from "sockapi/scope-specs/team-puzzle-105-spec";
import { AdminQueuePuzzleSubmissionTaskSummary } from "sockapi/scope-specs/admin-queue-spec";
import {
  PuzzleSubmissionType,
  AdminQueueTaskType,
} from "sockapi/scope-specs/admin-queue-task-spec";
import { PuzzleData } from "sockapi/puzzle-data";
import { HuntNotificationType } from "sockapi/client-notifications";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";
import RateLimitTracker from "sock-server/RateLimitTracker";
import { TeamController } from "sock-server/server-controllers/team-controller";
import {
  PUZZLE_105_FEEDER_SOLUTION_ORDER,
  PUZZLE_105_TITLES,
} from "sock-server/static-data/puzzle-105-SPOILERS";

export const SEQUENCE_ID_FREE_PUZZLE = "free";

const PUZZLE_105_THROTTLE = 30 * 60 * 1000; // 30 minutes
const PUZZLE_105_GRACE_PERIOD = 10 * 1000; // 10 seconds

export const makeCustomPuzzleData = (
  teamId: string,
  title: string,
  answer: string,
  submitStatus: Puzzle105Status & { type: Puzzle105StatusType.ACCEPTED },
  puzzleContent: string,
  solutionContent: string
): PuzzleData => ({
  puzName: `${PUZZLE_105_CONFIG.customPuzzlePuzNamePrefix}${teamId}`,
  slug: `${PUZZLE_105_CONFIG.customPuzzleSlugPrefix}${teamId}`,
  displayName: title.trim(),
  // Custom puzzles should be ordered alphabetically.
  order: 0,
  emoji: "",
  round: "custom",
  answer,
  // HACK: Only for the real hunt.
  unlockInEdges:
    submitStatus.unlockInEdges ??
    (teamId === "203" && title.trim() === "Undespaired"
      ? [{ unlockGroupId: "despair", val: 1 }]
      : teamId === "359" && title.trim() === "A Necklace of Stars"
      ? [{ unlockGroupId: "necklace", val: 1 }]
      : teamId === "595" && title.trim() === "5050"
      ? [{ unlockGroupId: "fifty", val: 1 }]
      : teamId === "712" && title.trim() === "What's the answer? LIV"
      ? [{ unlockGroupId: "whats", val: 3 }]
      : []),
  unlockOutEdges: [],
  customPuzzleData: {
    srcTeamId: teamId,
  },
  rarity: submitStatus.rarity,
  isCustomPuzzleAvailableInGacha:
    submitStatus.isAvailableInGacha ?? false ? true : undefined,
  customPuzzleContent: puzzleContent,
  customPuzzleSolutionContent: solutionContent,
});

class TeamPuzzle105Engine {
  ctx: ScopeEngineContext<ScopeType.TEAM_PUZZLE_105>;
  teamController: TeamController;
  state: SockAPIScopeState<ScopeType.TEAM_PUZZLE_105>;
  checkTitleRateLimiter: RateLimitTracker;
  adminQueueTaskId?: string;

  constructor(
    ctx: ScopeEngineContext<ScopeType.TEAM_PUZZLE_105>,
    teamController: TeamController,
    initState?: SockAPIScopeState<ScopeType.TEAM_PUZZLE_105>
  ) {
    this.ctx = ctx;
    this.teamController = teamController;
    this.checkTitleRateLimiter = new RateLimitTracker(
      this.ctx.server.clock,
      PUZZLE_105_CONFIG.checkTitleRateLimitIntervalMinutes * 60 * 1000,
      PUZZLE_105_CONFIG.checkTitleRateLimitNum
    );
    const feeders = [...PUZZLE_105_FEEDER_SOLUTION_ORDER].sort();
    this.state = initState ?? {
      confirmedTitles: {},
      order: feeders,
      enums: {},
      formFields: {
        title: "",
        answer: "",
        hexColor: "000000",
        difficulty: null,
        flavortext: "",
        solution: "",
        instructions: "",
        notes: "",
        sharePermission: null,
      },
    };
  }

  init() {
    this.setAdminQueueTask();
    this.updateCustomPuzzle();
  }

  getAdminQueueTask(): AdminQueuePuzzleSubmissionTaskSummary | undefined {
    const {
      formFields: { sharePermission },
      status: submitStatus,
    } = this.state;
    const { teamId } = this.ctx.scope;
    if (submitStatus === undefined) return undefined;

    switch (submitStatus.type) {
      case Puzzle105StatusType.WRONG_ORDER:
        return undefined;
      case Puzzle105StatusType.PENDING:
      case Puzzle105StatusType.ACCEPTED:
      case Puzzle105StatusType.REJECTED:
        // Add the task if it is open, or was previously responded to.
        return {
          type: AdminQueueTaskType.PUZZLE_SUBMISSION,
          submissionType: PuzzleSubmissionType.PUZZLE_105,
          teamId,
          requestTime: submitStatus.submitTime,
          respondTime: getTeamPuzzle105ReviewTime(this.state) ?? undefined,
          isPublicCustomPuzzle:
            submitStatus.type === Puzzle105StatusType.ACCEPTED &&
            submitStatus.isAvailableInGacha
              ? true
              : undefined,
          isPrivate:
            sharePermission === Puzzle105SharePermission.NONE
              ? true
              : undefined,
        };
    }
  }

  setAdminQueueTask() {
    const adminQueueEngine = this.ctx.server.getAdminQueueEngine();
    const taskSummary = this.getAdminQueueTask();
    if (taskSummary !== undefined) {
      if (this.adminQueueTaskId === undefined)
        this.adminQueueTaskId = adminQueueEngine.addSubmissionTask(taskSummary);
      else adminQueueEngine.updateTask(this.adminQueueTaskId, taskSummary);
    }
  }

  getPermissions(conn: ClientConnection) {
    return conn.teamId === this.ctx.scope.teamId
      ? ScopePermissions.FULL
      : ScopePermissions.ADMIN_ONLY;
  }

  getPublicState(): SockAPIScopeState<ScopeType.TEAM_PUZZLE_105> {
    // Posthunt state should all be captured in the MDX, so wipe
    // all redundant state.
    return getTeamPuzzle105PosthuntState(this.state);
  }

  getState() {
    return this.state;
  }

  getSlowSyncState() {
    return this.state;
  }

  isCorrect() {
    return this.state.order.every(
      (puzName, i) => puzName === PUZZLE_105_FEEDER_SOLUTION_ORDER[i]
    );
  }

  getHasPublicCustomPuzzle(): boolean {
    const { status } = this.state;
    if (status === undefined) return false;
    if (status.type !== Puzzle105StatusType.ACCEPTED) return false;
    if (!(status.isAvailableInGacha ?? false)) return false;
    return true;
  }

  applyAndBroadcastUpdate(
    payload: SockAPIScopeUpdatePayload<ScopeType.TEAM_PUZZLE_105>
  ) {
    applyScopeUpdate(ScopeType.TEAM_PUZZLE_105, this.state, payload);
    this.ctx.update(this.ctx.scope, payload);
  }

  async handleImageSubmissionAsync(url: string) {
    applyScopeUpdate(ScopeType.TEAM_PUZZLE_105, this.state, {
      diagramUrl: url,
    });
    await this.ctx.fastSyncAsync(this.ctx.scope);
    // Get the latest diagramUrl since it might have changed during
    // the await.
    this.ctx.update(this.ctx.scope, {
      diagramUrl: this.state.diagramUrl,
    });
  }

  async setStatusAsync(submitStatus: Puzzle105Status) {
    const isNewlyRejected =
      submitStatus.type === Puzzle105StatusType.REJECTED &&
      this.state.status !== undefined &&
      this.state.status.type !== Puzzle105StatusType.REJECTED;
    const { puzName } = PUZZLE_105_CONFIG;
    const {
      server,
      scope: { teamId },
    } = this.ctx;

    if (submitStatus.type === Puzzle105StatusType.ACCEPTED) {
      await this.teamController.modifyTeamProgressAsync((progressTracker) => {
        progressTracker.addSolvePuzzle(puzName);
      });
    }

    this.applyAndBroadcastUpdate({
      setStatus: submitStatus,
    });

    if (isNewlyRejected)
      server.broadcastNotifToTeam(teamId, {
        type: HuntNotificationType.SUBMISSION_REJECTED,
        puzName,
      });

    this.setAdminQueueTask();
    this.updateCustomPuzzle();
  }

  updateCustomPuzzle() {
    const {
      server,
      scope: { teamId },
    } = this.ctx;
    const {
      status: submitStatus,
      formFields: { title, answer, flavortext, solution, instructions },
      diagramUrl,
    } = this.state;
    if (
      !(
        submitStatus !== undefined &&
        submitStatus.type === Puzzle105StatusType.ACCEPTED
      )
    )
      return;

    const puzzleContent = `
      <Puzzle105Content data={${JSON.stringify({
        teamId,
        flavortext,
        diagramUrl,
        instructions,
      })}} />
    `;
    const solutionContent = `
      <p>${solution
        .split("\n")
        .map((line) => `{${JSON.stringify(line)}}`)
        .join("<br/>")}</p>
    `;

    server.setCustomPuzzle(
      makeCustomPuzzleData(
        teamId,
        title,
        answer,
        submitStatus,
        puzzleContent,
        solutionContent
      ),
      submitStatus.cannedHint
    );

    // Making the puzzle available in gacha can affect
    // the leaderboard solve count, so update it here.
    server.handleTeamSummaryUpdate(teamId, {
      numSolves: this.teamController.getLeaderboardNumSolves(),
    });
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.TEAM_PUZZLE_105>
  ) {
    const { server } = this.ctx;
    const puzName = PUZZLE_105_CONFIG.puzName;
    const {
      checkTitle,
      downloadDiagramUrl,
      submit,
      ignoreSubmitErrors: ignoreSubmitErrorsOpt,
      setStatus,
      ...updPayload
    } = payload;
    const { movePuz, formFields } = updPayload;

    if (setStatus !== undefined) {
      if (!reqCtx.conn.isAdmin) return reqCtx.setPermissionDenied();
      // Accepted custom puzzles should never be set to anything else,
      // because other teams might have already unlocked it, but a
      // non-accepted custom puzzle would not be added to PuzzlesDB.
      // This would cause init to fail on next server restart since
      // we would not have access to the puzzle state.
      if (
        this.state.status !== undefined &&
        this.state.status.type === Puzzle105StatusType.ACCEPTED &&
        setStatus.type !== Puzzle105StatusType.ACCEPTED
      )
        return reqCtx.setBadRequest();
      await this.setStatusAsync(
        (() => {
          switch (setStatus.type) {
            case Puzzle105StatusType.ACCEPTED:
            case Puzzle105StatusType.REJECTED:
              return {
                ...setStatus,
                reviewTime: server.clock.genTimestamp(),
              };
            default:
              return setStatus;
          }
        })()
      );
      return reqCtx.sendStepResp(this.ctx.scope, {});
    }

    // Fail silently for these as they may be due to race conditions.
    if (
      !reqCtx.conn.isAdmin &&
      (!this.teamController.isPuzzleUnlocked(puzName) ||
        this.teamController.isPuzzleSolvedOrPendingSolved(puzName) ||
        !isTeamPuzzle105Editable(this.state))
    )
      return reqCtx.sendStepResp(this.ctx.scope, {});

    if (checkTitle !== undefined) {
      const { puzName, title } = checkTitle;
      if (this.state.confirmedTitles[puzName] !== undefined)
        return reqCtx.sendStepResp(this.ctx.scope, {
          checkTitleResp: Puzzle105CheckTitleResponse.ALREADY_SOLVED,
        });
      const canonicalTitle = PUZZLE_105_TITLES[puzName];
      const isCorrect =
        normalizeAnswer(title) === normalizeAnswer(canonicalTitle);
      // The request only counts towards the rate limit if it is wrong.
      if (
        (this.state.checkTitleThrottledTime !== undefined &&
          server.clock.genTimestamp() < this.state.checkTitleThrottledTime) ||
        !this.checkTitleRateLimiter.checkRequest(isCorrect ? 0 : 1)
      ) {
        return reqCtx.sendStepResp(this.ctx.scope, {
          checkTitleResp: Puzzle105CheckTitleResponse.RATE_LIMITED,
        });
      }
      server
        .getOrCreateEngineOrThrow({
          type: ScopeType.TEAM_TITLES,
          teamId: this.ctx.scope.teamId,
        })
        .applyAndBroadcastUpdate({
          titles: { [puzName]: isCorrect ? canonicalTitle : title },
        });
      if (isCorrect)
        this.applyAndBroadcastUpdate({
          setConfirmedTitles: { [puzName]: canonicalTitle },
        });
      // If we see that we're at the rate limit here, then
      // then we should still process the current request
      // but inform the client that we're rate limited.
      if (this.checkTitleRateLimiter.isRateLimited())
        this.applyAndBroadcastUpdate({
          setCheckTitleThrottledTime:
            server.clock.genTimestamp() +
            PUZZLE_105_CONFIG.checkTitleRateLimitIntervalMinutes * 60 * 1000 +
            PUZZLE_105_GRACE_PERIOD,
        });
      return reqCtx.sendStepResp(this.ctx.scope, {
        checkTitleResp: isCorrect
          ? Puzzle105CheckTitleResponse.CORRECT
          : Puzzle105CheckTitleResponse.WRONG,
      });
    }

    if (downloadDiagramUrl ?? false) {
      await server.triggerSlowSyncDownloadAsync(true);
      // Upon response, the diagram URL should be synced from the backend.
      return reqCtx.sendStepResp(this.ctx.scope, {});
    }

    if (submit !== undefined) {
      const ignoreSubmitErrors =
        reqCtx.conn.isAdmin && (ignoreSubmitErrorsOpt ?? false);
      // Return silently if submission is not allowed for the current
      // status, or is invalid, as this may be due to a race condition.
      if (validateTeamPuzzle105(this.state).length > 0 && !ignoreSubmitErrors)
        return reqCtx.sendStepResp(this.ctx.scope, {});

      const timeNow = server.clock.genTimestamp();
      const reopenTime = getTeamPuzzle105ReopenTime(this.state);
      const isThrottled =
        reopenTime !== null && timeNow < reopenTime - PUZZLE_105_GRACE_PERIOD;
      if (isThrottled && !ignoreSubmitErrors)
        return reqCtx.setPermissionDenied();

      const isCorrect = this.isCorrect() || ignoreSubmitErrors;

      if (!isCorrect) {
        await this.teamController.modifyTeamProgressAsync((progressTracker) => {
          progressTracker.addSolveAttempt(puzName, {});
        });
        await this.setStatusAsync({
          type: Puzzle105StatusType.WRONG_ORDER,
          reopenTime: timeNow + PUZZLE_105_THROTTLE,
          submitTime: timeNow,
        });
        return reqCtx.sendStepResp(this.ctx.scope, {});
      }

      await this.setStatusAsync({
        type: Puzzle105StatusType.PENDING,
        submitTime: timeNow,
      });
      return reqCtx.sendStepResp(this.ctx.scope, {});
    }

    if (movePuz !== undefined) {
      const { puzName, diff } = movePuz;
      if (PUZZLE_105_TITLES[puzName] === undefined)
        return reqCtx.setPermissionDenied();
      if (![-1, 1].includes(diff)) return reqCtx.setPermissionDenied();
    }

    if (
      formFields !== undefined &&
      validateTeamPuzzle105MaxLengths(formFields).length > 0
    )
      return reqCtx.setPermissionDenied();

    this.applyAndBroadcastUpdate(updPayload);
  }
}

export type TeamPuzzle105ServerScopeSpec = {
  engine: TeamPuzzle105Engine;
};

export const teamPuzzle105ServerScopeHooks: ServerScopeHooks<ScopeType.TEAM_PUZZLE_105> =
  {
    createEngine: ({ execCtx, engineCtx, initState }) => {
      const { controller, scope } = engineCtx;
      const teamController = controller.getTeamControllerIfExists(scope.teamId);
      if (teamController === null)
        return execCtx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_TEAM, null);
      return new TeamPuzzle105Engine(engineCtx, teamController, initState);
    },
    engineIdentityFunc: (engine) => engine,
  };
