import localforage from "localforage";
import { PUZZLES_DB } from "./mock-server-static-data";
import { WSReq } from "sockapi/sockapi-messages";
import { MockServerOpts } from "sockapi/server-config";
import { SavedHuntBackendState } from "sockapi/scope-specs/state-download-spec";
import { SockServer } from "sock-server/SockServer";
import { BrowserRawWSInterface } from "sock-server/server-interface/BrowserRawWSInterface";
import {
  MockDB,
  MockBackendInterface,
} from "sock-server/backend-interface/MockBackendInterface";
import { MockAuthenticationInterface } from "sock-server/authentication-interface/MockAuthenticationInterface";
import { LoadBalancedFarmerClient } from "sock-server/FarmerClient";
import { posthuntDataJson } from "./client-static-data";
// Do not import `settings` here as it depends on `window`
// which is not accessible to the shared worker.

/** The key that the mock DB is stored into in local storage. */
const LOCAL_FORAGE_KEY = "__mockdb";

let sockServer: SockServer | undefined = undefined;
let isForceResetPending = false;

const makeMockServer = async (
  opts: MockServerOpts,
  forceReset?: { initState?: SavedHuntBackendState }
): Promise<SockServer> => {
  // No localStorage in worker context, so we use IndexedDB.
  const store = localforage.createInstance({
    driver: localforage.INDEXEDDB,
    name: opts.browserStoragePrefix,
  });

  if (
    sockServer === undefined ||
    (forceReset !== undefined && !isForceResetPending)
  ) {
    if (forceReset !== undefined) {
      isForceResetPending = true;
      await store.clear();
    }

    const prevSockServer = sockServer;
    const existingState: MockDB | null = await store.getItem(LOCAL_FORAGE_KEY);
    const posthuntData =
      (opts.isPosthunt ?? false) && posthuntDataJson !== ""
        ? (JSON.parse(posthuntDataJson) as SavedHuntBackendState)
        : undefined;
    const initState = (() => {
      if (forceReset === undefined)
        return existingState === null ? posthuntData : undefined;
      if (forceReset.initState === undefined) return posthuntData;
      if (posthuntData === undefined) return forceReset.initState;
      if (forceReset.initState.version !== posthuntData.version) {
        // Add migration here if necessary.
        throw new Error(
          `Unable to migrate state from version ${forceReset.initState.version} to ${posthuntData.version}`
        );
      }
      const state = posthuntData;
      state.defaultTeamId = forceReset.initState.defaultTeamId;
      Object.assign(state.scopeState, forceReset.initState.scopeState);
      for (const [teamId, teamState] of Object.entries(
        forceReset.initState.teams
      )) {
        if (
          state.teams[teamId] !== undefined &&
          (teamState.isPlaceholder ?? false)
        )
          continue;
        state.teams[teamId] = teamState;
      }
      return state;
    })();

    sockServer = new SockServer({
      backend: new MockBackendInterface(
        PUZZLES_DB,
        existingState,
        async (db) => {
          await store.setItem(LOCAL_FORAGE_KEY, db);
        },
        {
          bootstrapTeam: opts.bootstrapTeam,
          initUnlocks: forceReset === undefined,
          isDev: !opts.isPosthunt,
          initState,
        }
      ),
      // We'll be dumping WS messages from the client side,
      // so don't dump them from the server side.
      dumpWsMessages: false,
      dumpBackendRequests: opts.dumpBackendRequests ?? false,
      loadPuzzlesDBAsync: async () => PUZZLES_DB,
      periodicSyncInterval: 200, // 0.2s
      authInterface: new MockAuthenticationInterface(),
      // Pretend all WSReqs are valid to skip typia in offline mode.
      isWSReq: (req): req is WSReq => true,
      enableDevFeatures: true,
      logRequestErrDebugInfoAsync: async (msg) => {
        console.error(msg);
      },
      // Actual word2vec is only supported on a real server.
      farmerServerInterface: new LoadBalancedFarmerClient(
        opts.farmerWsEndpoints.map(
          (endpoint) => new BrowserRawWSInterface(endpoint)
        )
      ),
      logWord2Vec: true,
      forceHuntEnd: opts.forceHuntEnd,
      isPosthunt: opts.isPosthunt,
    });
    await sockServer.initAsync();

    if (prevSockServer !== undefined) {
      prevSockServer.teardown();
    }

    if (forceReset) isForceResetPending = false;
  }
  return sockServer;
};
export default makeMockServer;
