import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import {
  SockAPIScopeUpdatePayload,
  applyScopeUpdate,
} from "sockapi/scope-updates";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import {
  AdminQueueTaskType,
  AdminTaskNotificationBackendState,
} from "sockapi/scope-specs/admin-queue-task-spec";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { BackendScopeState } from "sockapi/backend-scope-state";
import { RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";
import { AdminTaskNotificationData } from "sock-server/AdminNotifier";

class AdminQueueTaskEngine {
  ctx: ScopeEngineContext<ScopeType.ADMIN_QUEUE_TASK>;
  state: SockAPIScopeState<ScopeType.ADMIN_QUEUE_TASK>;
  notificationBackendState: AdminTaskNotificationBackendState;

  constructor(
    ctx: ScopeEngineContext<ScopeType.ADMIN_QUEUE_TASK>,
    initState: BackendScopeState<ScopeType.ADMIN_QUEUE_TASK>
  ) {
    this.ctx = ctx;
    const { notificationBackendState, ...state } = initState;
    this.state = state;
    this.notificationBackendState = notificationBackendState ?? {};
  }

  getPermissions(conn: ClientConnection) {
    return ScopePermissions.ADMIN_ONLY;
  }

  getState() {
    return this.state;
  }

  getSlowSyncState() {
    return {
      ...this.state,
      notificationBackendState: this.notificationBackendState,
    };
  }

  notifyChangeToAdmin(isInit: boolean) {
    const {
      server,
      controller,
      scope: { taskId },
    } = this.ctx;
    const adminNotifier = server.opts.adminNotifier;
    if (adminNotifier === undefined) return;

    const taskSummary = server.getAdminQueueEngine().state.tasks[taskId];
    const { task, claim } = this.state;
    const commonData = {
      taskSummary,
      claim,
      backendState: this.notificationBackendState,
      isInit,
    };
    const notifData = ((): AdminTaskNotificationData => {
      switch (task.type) {
        case AdminQueueTaskType.HINT: {
          const { hintId } = task;
          const hintEngine = controller.getEngineIfExists({
            type: ScopeType.HINT,
            hintId,
          });
          if (hintEngine === undefined)
            throw new Error(`could not find hint engine for ${hintId}`);
          const hint = hintEngine.state;
          const teamDisplayName = server.getTeamController(
            hint.teamId
          ).displayName;
          return {
            ...commonData,
            type: AdminQueueTaskType.HINT,
            hintId,
            hint,
            teamDisplayName,
          };
        }
        case AdminQueueTaskType.PUZZLE_SUBMISSION: {
          const { submissionType, teamId } = task;
          const teamDisplayName = server.getTeamController(teamId).displayName;
          return {
            ...commonData,
            type: AdminQueueTaskType.PUZZLE_SUBMISSION,
            submissionType,
            teamDisplayName,
          };
        }
      }
    })();

    // We should always dereference the disable flag here
    // so that it can be updted dynamically.
    if (!(server.config.disableAdminNotifier ?? false))
      adminNotifier.notifyTask(notifData).then(
        (newBackendState) => {
          if (newBackendState !== undefined)
            this.notificationBackendState = newBackendState;
          this.ctx.onModify();
        },
        (error) => console.warn("adminNotifier error:", error)
      );
  }

  applyAndBroadcastUpdate(
    payload: SockAPIScopeUpdatePayload<ScopeType.ADMIN_QUEUE_TASK>
  ) {
    applyScopeUpdate(ScopeType.ADMIN_QUEUE_TASK, this.state, payload);
    this.ctx.update(this.ctx.scope, payload);
    this.notifyChangeToAdmin(false);
  }

  clearClaimer() {
    if (this.state.claim === undefined) return;
    this.applyAndBroadcastUpdate({ claim: null });
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.ADMIN_QUEUE_TASK>
  ) {
    if (payload.claim !== undefined) {
      const { staffId, force } = payload.claim;
      if (
        this.state.claim !== undefined &&
        this.state.claim.staffId !== staffId &&
        !(force ?? false)
      ) {
        return reqCtx.sendStepResp(this.ctx.scope, false);
      }
      this.applyAndBroadcastUpdate({
        claim: {
          staffId,
          claimTime: this.ctx.server.clock.genTimestamp(),
        },
      });
      return reqCtx.sendStepResp(this.ctx.scope, true);
    }
    if (payload.unclaim ?? false) {
      this.clearClaimer();
      return reqCtx.sendStepResp(this.ctx.scope, true);
    }
    return reqCtx.sendStepResp(this.ctx.scope, false);
  }
}

export type AdminQueueTaskServerScopeSpec = {
  engine: AdminQueueTaskEngine;
};

export const adminQueueTaskServerScopeHooks: ServerScopeHooks<ScopeType.ADMIN_QUEUE_TASK> =
  {
    createEngine: ({ execCtx, engineCtx, initState }) => {
      if (initState === undefined)
        return execCtx.setErrCodeAndReturn(
          SockAPIErrorCode.PERMISSION_DENIED,
          null
        );

      return new AdminQueueTaskEngine(engineCtx, initState);
    },
    engineIdentityFunc: (engine) => engine,
  };
