import { arrayZip } from "util/util";

import { SockAPIErrorCode } from "sockapi/errors";
import { normalizeAnswer } from "sockapi/answers";
import { ScopeType } from "sockapi/scope-types";
import {
  SockAPIScopeUpdatePayload,
  applyScopeUpdate,
} from "sockapi/scope-updates";
import { SockAPIScopeState } from "sockapi/scope-state";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import {
  FarmerGraphEvaluation,
  FarmerGraph,
  FarmerServerStepResponseType,
  SockAPIFarmerStageMetadata,
  SockAPIFarmerStage,
  FarmerStageUpdate,
  FarmerAddNodePayload,
  FarmerAddNodeResponseType,
  makeInitFarmerGraph,
  isFarmerStageUndoable,
} from "sockapi/scope-specs/team-farmer-spec";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { BackendScopeState } from "sockapi/backend-scope-state";
import { RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";
import { TeamController } from "sock-server/server-controllers/team-controller";
import {
  FarmerStage,
  FARMER_STAGES,
  FARMER_UNLOCK_EDGES,
  FARMER_EXTRACT_EDGES,
  FARMER_START_STAGE,
} from "sock-server/static-data/farmer-SPOILERS";

const PUZNAME_FARMER = "farmer";

const makeSockAPIFarmerStageMetadata = ({
  displayName,
  instruction,
  targetGraphSpec,
}: FarmerStage): SockAPIFarmerStageMetadata => ({
  displayName,
  instruction,
  targetImgName: targetGraphSpec?.imgName,
  targetNumNodes: targetGraphSpec?.numNodes,
});

const makeSockAPIFarmerStage = (stage: FarmerStage): SockAPIFarmerStage => ({
  metadata: makeSockAPIFarmerStageMetadata(stage),
  graph: makeInitFarmerGraph(),
  prevGraphs: [],
  nextGraphs: [],
});

const makeFarmerInitState = () => ({
  stages: {
    [FARMER_START_STAGE]: makeSockAPIFarmerStage(
      FARMER_STAGES[FARMER_START_STAGE]
    ),
  },
  mapEdges: [],
  mapExtraEdges: [],
});

const getIsGraphFull = (
  stage: FarmerStage,
  graph: FarmerGraph,
  isWorkshop: boolean
) =>
  stage.targetGraphSpec !== undefined &&
  graph.nodes.length === stage.targetGraphSpec.numNodes &&
  !isWorkshop;
const getEvaluationForGraph = (
  stage: FarmerStage,
  graph: FarmerGraph,
  isWorkshop: boolean
): FarmerGraphEvaluation | undefined => {
  const { targetGraphSpec, targetLetter } = stage;
  const isFull = getIsGraphFull(stage, graph, isWorkshop);
  const matchesSpec =
    targetGraphSpec !== undefined &&
    isFull &&
    targetGraphSpec.checkGraph(graph);
  return isFull
    ? {
        success: matchesSpec,
        highlightNodes:
          matchesSpec && targetLetter !== undefined
            ? graph.nodes
                .filter((node) => node.content.includes(targetLetter))
                .map((node) => node.nodeId)
            : undefined,
      }
    : undefined;
};

class TeamFarmerEngine {
  ctx: ScopeEngineContext<ScopeType.TEAM_FARMER>;
  teamController: TeamController;
  state: SockAPIScopeState<ScopeType.TEAM_FARMER>;
  nextNodeId: number;

  constructor(
    ctx: ScopeEngineContext<ScopeType.TEAM_FARMER>,
    teamController: TeamController,
    initState?: BackendScopeState<ScopeType.TEAM_FARMER>
  ) {
    this.ctx = ctx;
    this.teamController = teamController;
    this.state =
      initState !== undefined
        ? {
            ...initState,
            stages: Object.fromEntries(
              Object.entries(initState.stages).map(
                ([graphId, stageTeamData]) => [
                  graphId,
                  {
                    metadata: makeSockAPIFarmerStageMetadata(
                      FARMER_STAGES[graphId]
                    ),
                    ...stageTeamData,
                  },
                ]
              )
            ),
          }
        : makeFarmerInitState();
    this.nextNodeId = 0;
  }

  getPermissions(conn: ClientConnection): ScopePermissions {
    return conn.teamId === this.ctx.scope.teamId
      ? ScopePermissions.FULL
      : ScopePermissions.ADMIN_ONLY;
  }

  getState(): SockAPIScopeState<ScopeType.TEAM_FARMER> {
    return this.state;
  }

  getSlowSyncState(): BackendScopeState<ScopeType.TEAM_FARMER> {
    return {
      ...this.state,
      stages: Object.fromEntries(
        Object.entries(this.state.stages).map(([graphId, stage]) => {
          const { metadata, ...stageTeamData } = stage;
          return [graphId, stageTeamData];
        })
      ),
    };
  }

  applyAndBroadcastUpdate(
    payload: SockAPIScopeUpdatePayload<ScopeType.TEAM_FARMER>
  ) {
    applyScopeUpdate(ScopeType.TEAM_FARMER, this.state, payload);
    this.ctx.update(this.ctx.scope, payload);
  }

  genNodeId(): string {
    const nodeId = `${this.ctx.server.sockServerId}_${this.nextNodeId}`;
    this.nextNodeId++;
    return nodeId;
  }

  getSolveUnlocksUpdate(
    graphId: string
  ): SockAPIScopeUpdatePayload<ScopeType.TEAM_FARMER> {
    const existingSolves = new Set(
      Object.entries(this.state.stages)
        .filter(([graphId, stage]) => stage.isSolved)
        .map(([graphId]) => graphId)
    );
    const solves = new Set([...existingSolves, graphId]);
    const unlockEdges = FARMER_UNLOCK_EDGES.filter(
      ([from, to]) => solves.has(from) || solves.has(to)
    );
    const existingUnlocks = new Set(Object.keys(this.state.stages));
    const newUnlocks = [...new Set(unlockEdges.flat())].filter(
      (graphId) => !existingUnlocks.has(graphId)
    );
    return {
      addStages: Object.fromEntries(
        Array.from(newUnlocks, (graphId) => [
          graphId,
          makeSockAPIFarmerStage(FARMER_STAGES[graphId]),
        ])
      ),
      mapEdges: unlockEdges.map(([nodeId1, nodeId2]) => ({ nodeId1, nodeId2 })),
      mapExtraEdges: FARMER_EXTRACT_EDGES.filter(
        ([from, to]) => solves.has(from) && solves.has(to)
      ).map(([nodeId1, nodeId2]) => ({ nodeId1, nodeId2 })),
    };
  }

  private async handleAddNodeAsync(
    reqCtx: RequestContext,
    payload: FarmerAddNodePayload,
    isWorkshop: boolean
  ) {
    const { graphId, content: contentUnnormalized } = payload;
    const graph = this.state.stages[graphId]?.graph;
    if (graph === undefined) return reqCtx.setPermissionDenied();

    const content = normalizeAnswer(contentUnnormalized);

    const stage = FARMER_STAGES[graphId];
    if (stage === undefined)
      throw new Error(
        `expect stage ${stage} to exist if it is found in the scope state`
      );
    const { checkNode, checkEdge, category } = stage;

    // Check for duplicates.
    if (graph.nodes.some((node) => node.content === content))
      return reqCtx.sendStepResp(this.ctx.scope, {
        addNodeResp: { type: FarmerAddNodeResponseType.DUPLICATE },
      });

    if (content.length <= 0)
      return reqCtx.sendStepResp(this.ctx.scope, {
        addNodeResp: { type: FarmerAddNodeResponseType.INVALID_WORD },
      });

    if (getIsGraphFull(stage, graph, isWorkshop))
      return reqCtx.sendStepResp(this.ctx.scope, {
        addNodeResp: { type: FarmerAddNodeResponseType.TOO_MANY_NODES },
      });

    const newNodeId = this.genNodeId();
    const newNode = {
      nodeId: newNodeId,
      content,
    };
    const candidates = graph.nodes.filter(
      (node) => checkEdge === undefined || checkEdge(node, newNode)
    );

    const resp =
      await this.ctx.server.farmerServerInterface.getSimilaritiesAsync({
        s: content,
        candidates: candidates.map((node) => node.content),
        category,
      });
    switch (resp.type) {
      case FarmerServerStepResponseType.ERROR:
        return reqCtx.sendStepResp(this.ctx.scope, {
          addNodeResp: { type: FarmerAddNodeResponseType.SERVER_ERROR },
        });
      case FarmerServerStepResponseType.INVALID_WORD:
        return reqCtx.sendStepResp(this.ctx.scope, {
          addNodeResp: { type: FarmerAddNodeResponseType.INVALID_WORD },
        });
      case FarmerServerStepResponseType.NOT_IN_CATEGORY:
        return reqCtx.sendStepResp(this.ctx.scope, {
          addNodeResp: { type: FarmerAddNodeResponseType.NOT_IN_CATEGORY },
        });
    }

    if (checkNode !== undefined && !checkNode(newNode))
      return reqCtx.sendStepResp(this.ctx.scope, {
        addNodeResp: { type: FarmerAddNodeResponseType.INVALID_NODE },
      });

    // If this is the first node, accept it.
    if (graph.nodes.length === 0) {
      this.applyAndBroadcastUpdate({
        updStages: {
          [graphId]: {
            addNodes: [newNode],
          },
        },
      });
      return reqCtx.sendStepResp(this.ctx.scope, {
        addNodeResp: { type: FarmerAddNodeResponseType.SUCCESS },
      });
    }

    if (candidates.length === 0)
      return reqCtx.sendStepResp(this.ctx.scope, {
        addNodeResp: { type: FarmerAddNodeResponseType.NO_EDGE },
      });

    const { similarities } = resp;
    if (similarities.length !== candidates.length)
      throw new Error(
        `got ${similarities.length} similarities but expected ${candidates.length}`
      );

    if (this.ctx.server.opts.logWord2Vec ?? false)
      console.log(
        content,
        Object.fromEntries(
          arrayZip(
            candidates.map((x) => x.content),
            similarities
          )
        )
      );
    const { currBestNodeId } = candidates.reduce<{
      currBestNodeId: string | null;
      currBestScore: number;
    }>(
      (acc, { nodeId: otherNodeId }, i) =>
        similarities[i] < acc.currBestScore
          ? acc
          : {
              currBestNodeId: otherNodeId,
              currBestScore: similarities[i],
            },
      { currBestNodeId: null, currBestScore: 0 }
    );
    const parentNodeId = currBestNodeId ?? candidates[0].nodeId;
    const stageUpd: FarmerStageUpdate = {
      addNodes: [newNode],
      addEdges: [
        {
          nodeId1: parentNodeId,
          nodeId2: newNodeId,
        },
      ],
    };
    applyScopeUpdate(ScopeType.TEAM_FARMER, this.state, {
      updStages: { [graphId]: stageUpd },
    });

    const evaluation = getEvaluationForGraph(stage, graph, isWorkshop);
    const isSuccess = evaluation?.success ?? false;
    const solveUnlocksUpdate = isSuccess
      ? this.getSolveUnlocksUpdate(graphId)
      : undefined;
    const evalStageUpd: FarmerStageUpdate = {
      evaluation,
      setSolved: isSuccess ? true : undefined,
    };
    applyScopeUpdate(ScopeType.TEAM_FARMER, this.state, {
      updStages: { [graphId]: evalStageUpd },
      ...(solveUnlocksUpdate ?? {}),
    });

    this.ctx.update(this.ctx.scope, {
      updStages: {
        [graphId]: { ...stageUpd, ...evalStageUpd },
      },
      ...(solveUnlocksUpdate ?? {}),
    });

    return reqCtx.sendStepResp(this.ctx.scope, {
      addNodeResp: { type: FarmerAddNodeResponseType.SUCCESS },
    });
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.TEAM_FARMER>
  ) {
    const {
      isWorkshop: isWorkshopUnchecked = false,
      addNode,
      undoGraph,
      redoGraph,
      resetGraph,
      adminSetGraphSolved,
      adminResetProgress,
    } = payload;
    const puzName = PUZNAME_FARMER;
    const isWorkshop = reqCtx.conn.isAdmin && isWorkshopUnchecked;

    // Fail silently as this may be due to race conditions.
    // Don't check for unsolved to allow playing with the interface
    // after solving.
    if (!reqCtx.conn.isAdmin && !this.teamController.isPuzzleUnlocked(puzName))
      return reqCtx.sendStepResp(this.ctx.scope, {});

    if (addNode !== undefined) {
      return await this.handleAddNodeAsync(reqCtx, addNode, isWorkshop);
    }

    if (undoGraph !== undefined) {
      const stage = this.state.stages[undoGraph];
      if (stage === undefined) return reqCtx.setPermissionDenied();
      // This may be due to a race condition, so return silently.
      if (!isFarmerStageUndoable(stage))
        return reqCtx.sendStepResp(this.ctx.scope, {});
      this.applyAndBroadcastUpdate({
        updStages: {
          [undoGraph]: {
            undo: true,
          },
        },
      });
      return reqCtx.sendStepResp(this.ctx.scope, {});
    }

    if (redoGraph !== undefined) {
      const graph = this.state.stages[redoGraph]?.graph;
      if (graph === undefined) return reqCtx.setPermissionDenied();
      const stage = FARMER_STAGES[redoGraph];
      if (stage === undefined)
        throw new Error(
          `expect stage ${stage} to exist if it is found in the scope state`
        );
      const stageUpd: FarmerStageUpdate = { redo: true };
      applyScopeUpdate(ScopeType.TEAM_FARMER, this.state, {
        updStages: {
          [redoGraph]: stageUpd,
        },
      });
      const evaluation = getEvaluationForGraph(stage, graph, isWorkshop);
      const evalStageUpd: FarmerStageUpdate = { evaluation };
      applyScopeUpdate(ScopeType.TEAM_FARMER, this.state, {
        updStages: { [redoGraph]: evalStageUpd },
      });
      this.ctx.update(this.ctx.scope, {
        updStages: {
          [redoGraph]: { ...stageUpd, ...evalStageUpd },
        },
      });
      return reqCtx.sendStepResp(this.ctx.scope, {});
    }

    if (resetGraph !== undefined) {
      const stage = this.state.stages[resetGraph];
      if (stage === undefined) return reqCtx.setPermissionDenied();
      if (stage.graph.nodes.length <= 0)
        return reqCtx.sendStepResp(this.ctx.scope, {});
      this.applyAndBroadcastUpdate({
        updStages: {
          [resetGraph]: {
            reset: true,
          },
        },
      });
      return reqCtx.sendStepResp(this.ctx.scope, {});
    }

    if (adminSetGraphSolved !== undefined) {
      if (!reqCtx.conn.isAdmin) return reqCtx.setPermissionDenied();
      const upd = this.getSolveUnlocksUpdate(adminSetGraphSolved);
      this.applyAndBroadcastUpdate({
        ...upd,
        updStages: {
          [adminSetGraphSolved]: {
            setSolved: true,
          },
        },
      });
      return reqCtx.sendStepResp(this.ctx.scope, {});
    }

    if (adminResetProgress !== undefined) {
      if (!reqCtx.conn.isAdmin) return reqCtx.setPermissionDenied();
      this.state = makeFarmerInitState();
      this.ctx.assign(this.ctx.scope, this.state);
      return reqCtx.sendStepResp(this.ctx.scope, {});
    }

    return reqCtx.sendStepResp(this.ctx.scope, {});
  }
}

export type TeamFarmerServerScopeSpec = {
  engine: TeamFarmerEngine;
};

export const teamFarmerServerScopeHooks: ServerScopeHooks<ScopeType.TEAM_FARMER> =
  {
    createEngine: ({ execCtx, engineCtx, initState }) => {
      const { controller, scope } = engineCtx;
      const teamController = controller.getTeamControllerIfExists(scope.teamId);
      if (teamController === null)
        return execCtx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_TEAM, null);
      return new TeamFarmerEngine(engineCtx, teamController, initState);
    },
    engineIdentityFunc: (engine) => engine,
  };
