import { SockAPIErrorCode, SockAPIError } from "sockapi/errors";
import {
  SockAPIStepResponseScope,
  SockAPIScopeStepResponsePayload,
} from "sockapi/scope-steps";
import {
  RawClientConnection,
  ClientConnection,
} from "sock-server/client-connections";

/**
 * A context for executing server functionality.
 * Server functionality can be executed either in response to a request
 * or triggered by the server. This affects error handling.
 * Server-triggered execution has full permissions.
 */
export class ExecContext {
  /** Only set if we are executing in response to a client request. */
  conn?: RawClientConnection;
  /** The error seen when handling the current request, if any. */
  err?: SockAPIError;
  /** If set, closes the connection after emitting the error. */
  isFatal?: boolean;
  /** Used to dump debug info about error responses in dev. */
  logErrDebugInfoAsync?: (msg: string) => Promise<void>;
  /**
   * The raw request message currently being processed,
   * for debugging purposes.
   */
  activeMsgRaw?: string;

  constructor(logErrDebugInfoAsync?: (msg: string) => Promise<void>) {
    this.logErrDebugInfoAsync = logErrDebugInfoAsync;
  }

  setErr(err: SockAPIError, isFatal?: boolean): void {
    if (this.logErrDebugInfoAsync !== undefined)
      this.logErrDebugInfoAsync(
        `encountered error ${JSON.stringify(err)}${
          this.activeMsgRaw !== undefined
            ? ` while processing request ${this.activeMsgRaw}`
            : ""
        }!\n${new Error().stack}\n`
      ).catch(console.error);
    if (this.conn === undefined)
      throw new Error(
        `encountered error ${JSON.stringify(
          err
        )} in server-triggered exec context`
      );
    if (this.err !== undefined)
      throw new Error(
        `new error generated when previous error ${JSON.stringify(
          this.err
        )} not yet flushed`
      );
    this.err = err;
    this.isFatal = isFatal;
  }

  setErrCode(errCode: SockAPIErrorCode, isFatal?: boolean): void {
    this.setErr({ errCode }, isFatal);
  }

  /** Utility function to support returning with an error. */
  setErrAndReturn<TRet>(err: SockAPIError, ret: TRet): TRet {
    this.setErr(err);
    return ret;
  }

  /** Utility function to support returning with an error. */
  setErrCodeAndReturn<TRet>(errCode: SockAPIErrorCode, ret: TRet): TRet {
    this.setErrCode(errCode);
    return ret;
  }

  setBadRequest() {
    this.setErrCode(SockAPIErrorCode.BAD_REQUEST);
  }

  setBadRequestAndReturn<TRet>(ret: TRet) {
    this.setErrCodeAndReturn(SockAPIErrorCode.BAD_REQUEST, ret);
  }

  setPermissionDenied() {
    this.setErrCode(SockAPIErrorCode.PERMISSION_DENIED);
  }

  setHuntIsOver() {
    this.setErrCode(SockAPIErrorCode.HUNT_IS_OVER);
  }

  /**
   * Flush the context error. If respond is true (default true),
   * complete the request and respond with the error.
   * We expect at most one error to be sent back with any request.
   */
  flushErr(respond?: boolean): SockAPIError | undefined {
    const { conn, err } = this;
    if (err === undefined) return undefined;
    delete this.err;
    if (conn === undefined)
      throw new Error("flushing error in server-triggered exec context");

    if (respond ?? true) {
      conn.sendErr(err);
      delete this.activeMsgRaw;
      if (this.isFatal ?? false) conn.close();
    }

    return err;
  }
}

export class RawRequestContext extends ExecContext {
  conn: RawClientConnection;

  constructor(
    conn: RawClientConnection,
    logErrDebugInfoAsync?: (msg: string) => Promise<void>
  ) {
    super(logErrDebugInfoAsync);
    this.conn = conn;
  }

  sendStepResp<TScope extends SockAPIStepResponseScope>(
    scope: TScope,
    payload: SockAPIScopeStepResponsePayload<TScope["type"]>
  ): void {
    this.conn.sendStepResp(scope, payload);
  }
}

export type RequestContext = RawRequestContext & { conn: ClientConnection };
