import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import { BIG_BRANCH_CONFIG } from "sockapi/scope-specs/team-big-branch-spec";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";
import { TeamController } from "sock-server/server-controllers/team-controller";
import RateLimitTracker from "sock-server/RateLimitTracker";
import { BIG_BRANCH_INTERMEDIATE_ANSWERS } from "sock-server/static-data/big-branch-SPOILERS";

class TeamBigBranchEngine {
  ctx: ScopeEngineContext<ScopeType.TEAM_BIG_BRANCH>;
  teamController: TeamController;
  rateLimiter: RateLimitTracker;

  constructor(
    ctx: ScopeEngineContext<ScopeType.TEAM_BIG_BRANCH>,
    teamController: TeamController
  ) {
    this.ctx = ctx;
    this.teamController = teamController;
    this.rateLimiter = new RateLimitTracker(
      this.ctx.server.clock,
      BIG_BRANCH_CONFIG.rateLimit.intervalMs, // 15 seconds
      BIG_BRANCH_CONFIG.rateLimit.num // 5 requests per interval
    );
  }

  getPermissions(conn: ClientConnection): ScopePermissions {
    return conn.teamId === this.ctx.scope.teamId
      ? ScopePermissions.FULL
      : ScopePermissions.ADMIN_ONLY;
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.TEAM_BIG_BRANCH>
  ) {
    const { checkAnswer } = payload;
    const puzName = BIG_BRANCH_CONFIG.puzName;

    // Fail silently as this may be due to race conditions.
    // Don't check for unsolved to allow playing with the interface
    // after solving.
    if (!reqCtx.conn.isAdmin && !this.teamController.isPuzzleUnlocked(puzName))
      return reqCtx.sendStepResp(this.ctx.scope, {});

    if (checkAnswer !== undefined) {
      if (!this.rateLimiter.checkRequest(1))
        return reqCtx.sendStepResp(this.ctx.scope, {
          checkAnswerResp: {
            isRateLimited: true,
          },
        });
      return reqCtx.sendStepResp(this.ctx.scope, {
        checkAnswerResp: {
          isCorrect: BIG_BRANCH_INTERMEDIATE_ANSWERS.includes(checkAnswer),
        },
      });
    }

    return reqCtx.sendStepResp(this.ctx.scope, {});
  }
}

export type TeamBigBranchServerScopeSpec = {
  engine: TeamBigBranchEngine;
};

export const teamBigBranchServerScopeHooks: ServerScopeHooks<ScopeType.TEAM_BIG_BRANCH> =
  {
    createEngine: ({ execCtx, engineCtx }) => {
      const { controller, scope } = engineCtx;
      const teamController = controller.getTeamControllerIfExists(scope.teamId);
      if (teamController === null)
        return execCtx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_TEAM, null);
      return new TeamBigBranchEngine(engineCtx, teamController);
    },
    engineIdentityFunc: (engine) => engine,
  };
