/**
 * The number of a collectible if it gets rolled. If not specified
 * here, then only one would ever be rolled at a time.
 */
const collectiblesRanges: {
  [collectibleId: string]: { min: number; max: number };
} = {
  dust: { min: 1000, max: 10000 },
  gold: { min: 10, max: 100 },
};
export const GACHA_CONFIG = {
  mainForkWeights: {
    puzzle: 0.9,
    jackpot: 0.03,
    cannedHint: 0.02,
    hintToken: 0.02,
    fun: 0.03,
  },
  puzzleWeightsByRarity: {
    [Rarity.COMMON]: 1,
    [Rarity.UNCOMMON]: 0.7,
    [Rarity.RARE]: 0.5,
    [Rarity.EPIC]: 0.4,
    [Rarity.LEGENDARY]: 0.3,
  },
  /**
   * Weight to assign to celestials. This weight makes getting any of
   * them about as rare as getting a legendary puzzle.
   */
  celestialWeight: 0,
  /**
   * Number of unlocks before we let teams start unlocking
   * custom puzzles. */
  customPuzzleUnlockReq: 100,
  /**
   * Before the team has unlocked this many micropuzzles, rig
   * the gacha to always unlock micropuzzles, for story consistency.
   */
  numInitialMicropuzzles: UNLOCKING_CONFIG.numInitialMicropuzzles,
  huntWidthRigging: {
    min: 5,
    max: 8,
    solvesBuff: {
      min: 7 / 180,
      max: 12 / 180,
    },
    customPuzzleContribution: 0.7,
    /** If you are width limited, probability you get a puzzle anyway. */
    ignoreProbability: 0.2,
    /** Amount of hunt width to increase by per dust. */
    dustContribution: 1 / 30000,
  },
  /**
   * Time after unlocking a puzzle before a team can get a canned
   * hint for it.
   */
  cannedHintBufferTimeHoursByRarity: {
    [Rarity.COMMON]: 0.4,
    [Rarity.UNCOMMON]: 0.6,
    [Rarity.RARE]: 0.8,
    [Rarity.EPIC]: 1.1,
    [Rarity.LEGENDARY]: 1.5,
  },
  /** Amount of hope gained from jackpot events. */
  jackpotAmount: {
    min: 200,
    max: 400,
  },
  celestialRigging: {
    thresholds: [0.025, 0.1, 0.2, 0.325, 0.45, 0.575, 0.7],
    weight: 0.75,
  },
  /** Probability to get an Olive wish, if eligible. */
  riggedOliveWeight: 0.2,
  /** Probability to get a Blocks wish, if eligible. */
  riggedBlocksWeight: 0.2,
  hintRigging: {
    /** Don't give a rigged hint request if the width is too low. */
    numOpenPuzzlesNeeded: 3,
    /**
     * Time after first solve (i.e. intro cutscene) to release a
     * rigged hint.
     */
    releaseTimeHours: [
      3,
      9,
      15,
      21,
      27,
      33,
      39,
      45,
      57,
      24 * 3,
      24 * 3 + 12,
      24 * 4,
      24 * 4 + 12,
      24 * 5,
      24 * 5 + 12,
      24 * 6,
      24 * 6 + 12,
      24 * 7,
      24 * 7 + 12,
      24 * 8,
      24 * 8 + 12,
      24 * 9,
      24 * 9 + 12,
    ],

    /**
     * Throttle to ensure that a team doesn't get too many rigged hint
     * tokens at once after a long period of inactivity.
     * Teams may get a rigged hint as long as either `numWishes` or
     * `timeHours` have elapsed since they last got a hint token.
     */
    throttle: {
      numWishes: 3,
      timeHours: 1,
    },
  },
  /** Weight multiplier for sequences, to prioritize them slightly. */
  sequencesBias: 2,
  /** Puzzle to queue next if a puzzle is solved. */
  sequenceRigging: {
    /** First letters -> last letters */
    puz317: "puz318",
  },
  hopePoolRigging: {
    puzName: "puz313",
    weightPerSolve: 1 / 5,
  },
  /**
   * Knobs to increase the probability of canned hints for specific
   * puzzles that depend on getting canned hints, given that the team
   * rolls a canned hint.
   *
   * We probably don't need to use this anymore now that canned hints
   * get more likely with time.
   */
  cannedHintPerPuzzleMultipliers: {},
  collectiblesRanges,
};

type TeamProgressGachaAnalysis = {
  numUnlockedFindCelestial: number;
  numUnlockedMicropuzzles: number;
  numSolvedMicropuzzles: number;
  firstSolvePuzzleRecord: TeamProgressEventRecord<TeamProgressEventType.SOLVE_ATTEMPT> | null;
  openPuzzlesWidth: number;
  openPuzzles: Set<string>;
  eligibleNonSequencePuzzles: Set<string>;
  eligibleSequencePuzzles: Set<string>;
  eligibleFindCelestials: Set<string>;
  cannedHintEligiblePuzzles: Map<
    string,
    { effectiveTimeSinceEligibleMs: number }
  >;
  eligibleCollectibles: Set<string>;
};

const computeTeamProgressGachaAnalysis = (
  config: typeof GACHA_CONFIG,
  server: SockServer,
  teamId: string,
  progressTracker: TeamProgressTracker
): TeamProgressGachaAnalysis => {
  const timeNow = server.clock.genTimestamp();
  const analysis: TeamProgressGachaAnalysis = {
    numUnlockedFindCelestial: 0,
    numUnlockedMicropuzzles: 0,
    numSolvedMicropuzzles: 0,
    firstSolvePuzzleRecord: null,
    openPuzzlesWidth: 0,
    openPuzzles: new Set(),
    eligibleNonSequencePuzzles: new Set(),
    eligibleSequencePuzzles: new Set(),
    eligibleFindCelestials: new Set(),
    cannedHintEligiblePuzzles: new Map(),
    eligibleCollectibles: new Set(),
  };

  for (const [
    puzName,
    eventRecord,
  ] of progressTracker.view.puzzleUnlocks.entries()) {
    const {
      isStory = false,
      isFindCelestial = false,
      rarity = Rarity.COMMON,
      customPuzzleData,
    } = server.getPuzzle(puzName);
    const puzzleNumCannedHints = server.getCannedHintsForPuzzle(puzName).length;
    const isSolved = progressTracker.view.puzzleSolves.has(puzName);
    const timeSinceUnlockMs = timeNow - eventRecord.timestamp;
    const teamNumCannedHints =
      progressTracker.view.puzzleNumTotalCannedHints.get(puzName) ?? 0;

    if (isFindCelestial) analysis.numUnlockedFindCelestial++;
    if (server.puzzlesDB.isMicropuzzle(puzName)) {
      analysis.numUnlockedMicropuzzles++;
      if (isSolved) analysis.numSolvedMicropuzzles++;
    }
    if (!isSolved) {
      analysis.openPuzzles.add(puzName);
      analysis.openPuzzlesWidth += (() => {
        if (isStory) return 0;
        if (customPuzzleData !== undefined)
          return config.huntWidthRigging.customPuzzleContribution;
        return 1;
      })();
    }
    const cannedHintBufferTimeMs = hoursToMs(
      config.cannedHintBufferTimeHoursByRarity[rarity]
    );
    // If a puzzle has multiple canned hints, delay subsequent
    // canned hints so that the canned hints are spread out
    // over the buffer time interval, which serves as a rough
    // estimate for how long the puzzle takes to solve.
    const effectiveTimeSinceEligibleForCannedHintMs =
      timeSinceUnlockMs -
      cannedHintBufferTimeMs * (1 + teamNumCannedHints / puzzleNumCannedHints);
    if (
      !isSolved &&
      teamNumCannedHints < puzzleNumCannedHints &&
      effectiveTimeSinceEligibleForCannedHintMs > 0
    )
      analysis.cannedHintEligiblePuzzles.set(puzName, {
        effectiveTimeSinceEligibleMs: effectiveTimeSinceEligibleForCannedHintMs,
      });
  }

  for (const [
    puzName,
    eventRecord,
  ] of progressTracker.view.puzzleSolves.entries()) {
    if (
      analysis.firstSolvePuzzleRecord === null ||
      eventRecord.timestamp < analysis.firstSolvePuzzleRecord.timestamp
    )
      analysis.firstSolvePuzzleRecord = eventRecord;
  }

  for (const puzData of server.getPuzzles()) {
    const {
      puzName,
      isFindCelestial = false,
      isAutoUnlocked = false,
      customPuzzleData,
      isCustomPuzzleAvailableInGacha = false,
    } = puzData;
    const isUnlocked = progressTracker.view.puzzleUnlocks.has(puzName);
    if (
      !isAutoUnlocked &&
      !isUnlocked &&
      progressTracker.isPuzzleUnlockReqsSatisfied(puzName) &&
      (customPuzzleData === undefined ||
        ((customPuzzleData.srcTeamId === teamId ||
          isCustomPuzzleAvailableInGacha) &&
          analysis.numUnlockedMicropuzzles >= config.customPuzzleUnlockReq))
    ) {
      if (server.puzzlesDB.isSequencePuzzle(puzName))
        analysis.eligibleSequencePuzzles.add(puzName);
      else analysis.eligibleNonSequencePuzzles.add(puzName);
      if (isFindCelestial) analysis.eligibleFindCelestials.add(puzName);
    }
  }

  for (const [collectibleId, { maxNumPerTeam }] of Object.entries(
    server.puzzlesDB.collectiblesData
  )) {
    if (
      maxNumPerTeam !== undefined &&
      (progressTracker.view.collectibleState.get(collectibleId)?.num ?? 0) >=
        maxNumPerTeam
    )
      continue;
    analysis.eligibleCollectibles.add(collectibleId);
  }

  return analysis;
};

class TeamGachaEngine {
  ctx: ScopeEngineContext<ScopeType.TEAM_GACHA>;
  teamController: TeamController;
  config: typeof GACHA_CONFIG;
  nextPullId: number;
  lastPullTime?: number;

  constructor(
    ctx: ScopeEngineContext<ScopeType.TEAM_GACHA>,
    teamController: TeamController
  ) {
    this.ctx = ctx;
    this.teamController = teamController;
    this.config = GACHA_CONFIG;
    this.nextPullId = 0;
  }

  getPermissions(conn: ClientConnection) {
    return conn.teamId === this.ctx.scope.teamId
      ? ScopePermissions.FULL
      : ScopePermissions.ADMIN_ONLY;
  }

  getState() {
    const pullEvents = this.teamController.progressTracker.getEventsForType(
      TeamProgressEventType.SPIN_GACHA
    );
    const pulls = Object.fromEntries(
      Object.values(pullEvents).map(
        ({ timestamp, info: { pullId, prize } }) => [
          pullId,
          { ...prize, timestamp },
        ]
      )
    );
    return { pulls };
  }

  genPullId(): string {
    const {
      server: { sockServerId },
      scope: { teamId },
    } = this.ctx;
    return `${sockServerId}_${teamId}_${this.nextPullId++}`;
  }

  handleUpdate(upd: SockAPIScopeUpdatePayload<ScopeType.TEAM_GACHA>) {
    this.ctx.update(this.ctx.scope, upd);
  }

  makeCollectiblePrize(
    collectibleId: string
  ): GachaPrize<GachaPrizeType.COLLECTIBLE> {
    const range = this.config.collectiblesRanges[collectibleId];
    return {
      type: GachaPrizeType.COLLECTIBLE,
      collectibleId,
      num:
        range === undefined
          ? 1
          : Math.floor(randomInRange(range.min, range.max)),
    };
  }

  makeDefaultCollectiblePrize(): GachaPrize<GachaPrizeType.COLLECTIBLE> {
    return this.makeCollectiblePrize("dust");
  }

  getCollectiblePrize(
    progressTracker: TeamProgressTracker,
    analysis: TeamProgressGachaAnalysis
  ): GachaPrize<GachaPrizeType.COLLECTIBLE> {
    const {
      server: { puzzlesDB },
    } = this.ctx;

    const prize = forkSelect<GachaPrize<GachaPrizeType.COLLECTIBLE>>(
      // Don't increase the probability of collecting special collectibles
      // even as the pool becomes smaller.
      Object.entries(puzzlesDB.collectiblesData).map(([collectibleId]) => {
        return {
          choice: () => {
            const isEligible = analysis.eligibleCollectibles.has(collectibleId);
            return isEligible
              ? this.makeCollectiblePrize(collectibleId)
              : this.makeDefaultCollectiblePrize();
          },
          weight: 1,
        };
      })
    );
    if (prize === null)
      throw new Error(
        "no eligible collectibles but we should always be able to get dust"
      );
    return prize;
  }

  /**
   * Get a single unlock prize. Might be used elsewhere besides
   * direct wishing.
   */
  getPuzzlePrize(
    progressTracker: TeamProgressTracker,
    analysis: TeamProgressGachaAnalysis
  ): GachaPrize<GachaPrizeType.UNLOCK> | null {
    const { server } = this.ctx;
    const { puzzlesDB } = server;

    const getRarityWeightedPuzzlePrizeFromList = (puzNames: Iterable<string>) =>
      weightedSelect<GachaPrize<GachaPrizeType.UNLOCK>>(
        Array.from(puzNames, (puzName) => {
          const {
            rarity = Rarity.COMMON,
            isMeta = false,
            isFindCelestial = false,
          } = server.getPuzzle(puzName);
          // Prioritize metas even though they are marked high rarity
          // for aesthetic reasons.
          const weight =
            puzName === this.config.hopePoolRigging.puzName
              ? this.config.hopePoolRigging.weightPerSolve *
                analysis.numSolvedMicropuzzles
              : isFindCelestial
              ? this.config.celestialWeight
              : this.config.puzzleWeightsByRarity[
                  isMeta ? Rarity.COMMON : rarity
                ];
          return {
            choice: { type: GachaPrizeType.UNLOCK, puzName },
            weight,
          };
        })
      );

    // Give sequences a separate pool and prioritize them slightly.
    const sequenceFraction =
      puzzlesDB.numSequencePuzzles / puzzlesDB.numNonCustomMicropuzzles;
    const sequenceWeight = sequenceFraction * this.config.sequencesBias;
    const nonSequenceWeight = 1 - sequenceFraction;

    return forkSelect<GachaPrize<GachaPrizeType.UNLOCK>>([
      {
        choice: () =>
          getRarityWeightedPuzzlePrizeFromList(
            analysis.eligibleSequencePuzzles
          ),
        weight: sequenceWeight,
      },
      {
        choice: () =>
          getRarityWeightedPuzzlePrizeFromList(
            analysis.eligibleNonSequencePuzzles
          ),
        weight: nonSequenceWeight,
      },
    ]);
  }

  getCannedHintPrize(
    progressTracker: TeamProgressTracker,
    analysis: TeamProgressGachaAnalysis
  ): GachaPrize<GachaPrizeType.CANNED_HINT> | null {
    const cannedHintPerPuzzleMultipliers: { [puzName: string]: number } =
      this.config.cannedHintPerPuzzleMultipliers;
    return weightedSelect<GachaPrize<GachaPrizeType.CANNED_HINT>>(
      Array.from(
        analysis.cannedHintEligiblePuzzles.entries(),
        ([puzName, { effectiveTimeSinceEligibleMs }]) => ({
          choice: { type: GachaPrizeType.CANNED_HINT, puzName },
          weight:
            (cannedHintPerPuzzleMultipliers[puzName] ?? 1) *
            effectiveTimeSinceEligibleMs,
        })
      )
    );
  }

  private getNormalPrize(
    progressTracker: TeamProgressTracker,
    analysis: TeamProgressGachaAnalysis
  ): GachaPrize | null {
    const minWidth =
      this.config.huntWidthRigging.min +
      analysis.numSolvedMicropuzzles *
        this.config.huntWidthRigging.solvesBuff.min;
    const maxWidth =
      this.config.huntWidthRigging.max +
      analysis.numSolvedMicropuzzles *
        this.config.huntWidthRigging.solvesBuff.max +
      (progressTracker.view.collectibleState.get("dust")?.num ?? 0) *
        this.config.huntWidthRigging.dustContribution;
    const hintBias = lerpClamp(
      0,
      this.config.mainForkWeights.puzzle,
      (analysis.openPuzzlesWidth - minWidth) / (maxWidth - minWidth)
    );

    return forkSelect<GachaPrize>([
      {
        // If we're out of puzzles to unlock, get something instead
        // of giving up to prevent increasing the probability of
        // jackpot.
        choice: () =>
          this.getPuzzlePrize(progressTracker, analysis) ??
          this.getCannedHintPrize(progressTracker, analysis) ??
          this.makeDefaultCollectiblePrize(),
        weight: this.config.mainForkWeights.puzzle - hintBias,
      },
      {
        // If we're out of hints to give, get something instead of
        // giving up to prevent increasing the probability of jackpot.
        choice: () =>
          this.getCannedHintPrize(progressTracker, analysis) ??
          (Math.random() < this.config.huntWidthRigging.ignoreProbability
            ? this.getPuzzlePrize(progressTracker, analysis)
            : this.makeDefaultCollectiblePrize()),
        weight: this.config.mainForkWeights.cannedHint + hintBias,
      },
      {
        choice: () => ({ type: GachaPrizeType.HINT_TOKEN }),
        weight: this.config.mainForkWeights.hintToken,
      },
      {
        choice: () => ({
          type: GachaPrizeType.JACKPOT,
          amount: Math.floor(
            randomInRange(
              this.config.jackpotAmount.min,
              this.config.jackpotAmount.max
            )
          ),
          msgIndex: Math.floor(Math.random() * JACKPOT_MESSAGES.length),
        }),
        weight: this.config.mainForkWeights.jackpot,
      },
      {
        choice: () => this.getCollectiblePrize(progressTracker, analysis),
        weight: this.config.mainForkWeights.fun,
      },
    ]);
  }

  computeAnalysis(
    progressTracker: TeamProgressTracker
  ): TeamProgressGachaAnalysis {
    const {
      server,
      scope: { teamId },
    } = this.ctx;
    return computeTeamProgressGachaAnalysis(
      this.config,
      server,
      teamId,
      progressTracker
    );
  }

  private getStandardPullResult(
    progressTracker: TeamProgressTracker
  ): GachaPrize {
    const {
      server,
      scope: { teamId },
    } = this.ctx;
    const timeNow = server.clock.genTimestamp();
    const analysis = this.computeAnalysis(progressTracker);
    const timeSinceFirstSolvedPuzzleMs =
      analysis.firstSolvePuzzleRecord === null
        ? 0
        : timeNow - analysis.firstSolvePuzzleRecord.timestamp;
    const nextHintTokenReleaseTime =
      this.config.hintRigging.releaseTimeHours[
        progressTracker.view.getNumTotalHintTokens()
      ];
    const numUnusedHintTokens =
      server.scopeEnginesController
        .getEngineIfExists({ type: ScopeType.TEAM_HINTS, teamId })
        ?.getNumUnusedHintTokens() ?? 0;
    const { numWishesSinceLastHintToken, lastHintTokenTime } =
      progressTracker.view;
    const isEligibleForRiggedHint =
      nextHintTokenReleaseTime !== undefined &&
      timeSinceFirstSolvedPuzzleMs >= hoursToMs(nextHintTokenReleaseTime) &&
      analysis.openPuzzlesWidth - numUnusedHintTokens >=
        this.config.hintRigging.numOpenPuzzlesNeeded &&
      (numWishesSinceLastHintToken === undefined ||
        numWishesSinceLastHintToken >=
          this.config.hintRigging.throttle.numWishes ||
        lastHintTokenTime === undefined ||
        timeNow - lastHintTokenTime >=
          hoursToMs(this.config.hintRigging.throttle.timeHours));

    if (analysis.numUnlockedMicropuzzles < this.config.numInitialMicropuzzles) {
      const prize = this.getPuzzlePrize(progressTracker, analysis);
      if (prize === null)
        throw new Error(
          "expect team to have micropuzzles to unlock at early hunt"
        );
      return prize;
    }

    for (const [srcPuzName, dstPuzName] of Object.entries(
      this.config.sequenceRigging
    )) {
      if (
        progressTracker.hasSolvePuzzle(srcPuzName) &&
        !progressTracker.hasUnlockPuzzle(dstPuzName)
      )
        return { type: GachaPrizeType.UNLOCK, puzName: dstPuzName };
    }

    if (isEligibleForRiggedHint) return { type: GachaPrizeType.HINT_TOKEN };

    const nextCelestialRiggingThreshold =
      this.config.celestialRigging.thresholds[
        analysis.numUnlockedFindCelestial
      ];
    const isEligibleForCelestialRigging =
      nextCelestialRiggingThreshold !== undefined &&
      analysis.numSolvedMicropuzzles /
        server.puzzlesDB.numNonCustomMicropuzzles >=
        nextCelestialRiggingThreshold;
    const isEligibleForOliveRigging =
      analysis.openPuzzles.has(OLIVE_CONFIG.puzName) &&
      // Add 1 to account for the initial wish.
      progressTracker.view.getNumOliveWishes() + 1 < OLIVE_CONFIG.maxWishes;
    const isEligibleForBlocksRigging =
      analysis.openPuzzles.has(BLOCKS_CONFIG.puzName) &&
      BLOCKS_SERVER_CONFIG.initialNumBoards +
        progressTracker.view.getNumBlocksWishes() *
          BLOCKS_SERVER_CONFIG.numBoardsPerWish <
        BLOCKS_SERVER_CONFIG.maxNumBoards;

    if (
      isEligibleForOliveRigging &&
      Math.random() < this.config.riggedOliveWeight
    )
      return { type: GachaPrizeType.OLIVE };
    if (
      isEligibleForBlocksRigging &&
      Math.random() < this.config.riggedBlocksWeight
    )
      return { type: GachaPrizeType.BLOCKS };
    if (
      isEligibleForCelestialRigging &&
      Math.random() < this.config.celestialRigging.weight
    ) {
      const celestialPrize = weightedSelect<GachaPrize>(
        Array.from(analysis.eligibleFindCelestials, (puzName) => ({
          choice: { type: GachaPrizeType.UNLOCK, puzName },
          weight: 1,
        }))
      );
      if (celestialPrize !== null) return celestialPrize;
    }
    if (analysis.openPuzzlesWidth < this.config.huntWidthRigging.min) {
      const prize = this.getPuzzlePrize(progressTracker, analysis);
      if (prize !== null) return prize;
    }
    const prize = this.getNormalPrize(progressTracker, analysis);
    if (prize === null) throw new Error("we should never allow this");
    return prize;
  }

  getPrize(progressTracker: TeamProgressTracker): GachaPrize {
    return this.getStandardPullResult(progressTracker);
  }

  genPullResult(
    progressTracker: TeamProgressTracker,
    pullId: string,
    ignoreHopeLimit: boolean
  ): PullAck {
    const timeNow = this.ctx.server.clock.genTimestamp();

    const currentHope = actualHope(progressTracker.getTotalHope(), timeNow);
    if (PULL_COST > currentHope && !ignoreHopeLimit) {
      return {
        type: PullAckType.NOT_ENOUGH_HOPE,
      };
    }

    const prize = this.getPrize(progressTracker);
    // let typescript ensure that this is exhaustive
    ((): true => {
      switch (prize.type) {
        case GachaPrizeType.UNLOCK: {
          progressTracker.addUnlockPuzzle(prize.puzName);
          return true;
        }
        case GachaPrizeType.COLLECTIBLE: {
          progressTracker.addEvent(TeamProgressEventType.GET_COLLECTIBLE, {
            collectibleId: prize.collectibleId,
            num: prize.num,
            reason: { type: TeamProgressEventReasonType.GACHA },
          });
          return true;
        }
        case GachaPrizeType.JACKPOT: {
          progressTracker.addEvent(TeamProgressEventType.GET_HOPE, {
            hopeAmt: prize.amount,
            reason: { type: TeamProgressEventReasonType.GACHA },
          });
          return true;
        }
        case GachaPrizeType.CANNED_HINT: {
          progressTracker.addEvent(TeamProgressEventType.GET_CANNED_HINT, {
            num: 1,
            puzName: prize.puzName,
            reason: { type: TeamProgressEventReasonType.GACHA },
          });
          return true;
        }
        case GachaPrizeType.HINT_TOKEN:
        case GachaPrizeType.OLIVE:
        case GachaPrizeType.BLOCKS: {
          const prizeTypeToEventType: {
            [prizeType in
              | GachaPrizeType.HINT_TOKEN
              | GachaPrizeType.OLIVE
              | GachaPrizeType.BLOCKS]: TeamProgressGlobalCounterEventType;
          } = {
            [GachaPrizeType.HINT_TOKEN]: TeamProgressEventType.GET_HINT_TOKEN,
            [GachaPrizeType.OLIVE]: TeamProgressEventType.GET_OLIVE_WISH,
            [GachaPrizeType.BLOCKS]: TeamProgressEventType.GET_BLOCKS_WISH,
          };
          progressTracker.addEvent(prizeTypeToEventType[prize.type], {
            num: 1,
            reason: { type: TeamProgressEventReasonType.GACHA },
          });
          return true;
        }
      }
    })();
    progressTracker.addSpinGacha(pullId, prize);
    return {
      type: PullAckType.SUCCESS,
      pullId,
      pull: { ...prize, timestamp: timeNow },
    };
  }

  async handlePullAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.TEAM_GACHA>
  ): Promise<SockAPIScopeStepResponsePayload<ScopeType.TEAM_GACHA>> {
    const {
      scope: { teamId },
      server,
    } = this.ctx;
    const ignoreHopeLimit =
      reqCtx.conn.isAdmin && (payload.ignoreHopeLimit ?? false);

    const pullId = this.genPullId();
    server.broadcastNotifToTeam(teamId, {
      type: HuntNotificationType.BEGIN_PULL,
      pullId,
    });
    const result: PullAck = await this.teamController.modifyTeamProgressAsync(
      (progressTracker) => {
        // We assume that the intro cutscene is done when wishing,
        // so just always make it so.
        progressTracker.addSolvePuzzle(PUZZLE_CONFIG.cutscenePuzNames.intro);
        return this.genPullResult(progressTracker, pullId, ignoreHopeLimit);
      }
    );
    server.broadcastNotifToTeam(teamId, {
      type: HuntNotificationType.END_PULL,
      pullId,
      pull: result.type === PullAckType.SUCCESS ? result.pull : undefined,
    });
    if (result.type === PullAckType.SUCCESS)
      this.lastPullTime = this.ctx.server.clock.genTimestamp();

    return result;
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.TEAM_GACHA>
  ) {
    if (
      this.lastPullTime !== undefined &&
      this.ctx.server.clock.genTimestamp() - this.lastPullTime <
        PULL_ANIMATION_TIME_MS - 300
    )
      return reqCtx.sendStepResp(this.ctx.scope, {
        type: PullAckType.TOO_SOON,
      });
    const numPulls = reqCtx.conn.isAdmin ? payload.numPulls ?? 1 : 1;
    reqCtx.sendStepResp(
      this.ctx.scope,
      (
        await Promise.all(
          Array.from(Array(numPulls), () =>
            this.handlePullAsync(reqCtx, payload)
          )
        )
      )[0]
    );
  }
}

export type TeamGachaServerScopeSpec = {
  engine: TeamGachaEngine;
};

export const teamGachaServerScopeHooks: ServerScopeHooks<ScopeType.TEAM_GACHA> =
  {
    createEngine: ({ execCtx, engineCtx }) => {
      const { controller, scope } = engineCtx;
      const teamController = controller.getTeamControllerIfExists(scope.teamId);
      if (teamController === null) return null;
      return new TeamGachaEngine(engineCtx, teamController);
    },
    engineIdentityFunc: (engine) => engine,
  };
