import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import { CLIENT_SHARED_CUTSCENE_CHOICES } from "sockapi/scope-specs/team-cutscenes-spec";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { ClientConnection } from "sock-server/client-connections";
import { TeamController } from "sock-server/server-controllers/team-controller";

class TeamEngine {
  ctx: ScopeEngineContext<ScopeType.TEAM>;
  teamController: TeamController;

  constructor(
    ctx: ScopeEngineContext<ScopeType.TEAM>,
    teamController: TeamController
  ) {
    this.ctx = ctx;
    this.teamController = teamController;
  }

  getPermissions(conn: ClientConnection): ScopePermissions {
    const { server, scope } = this.ctx;
    if (conn.teamId === scope.teamId) return ScopePermissions.FULL;
    if (server.getIsHuntOver()) return ScopePermissions.READ;
    return ScopePermissions.ADMIN_ONLY;
  }

  getState(): SockAPIScopeState<ScopeType.TEAM> {
    const { server } = this.ctx;
    const { displayName, progressTracker } = this.teamController;
    const puzzlesDict = Object.fromEntries(
      Array.from(progressTracker.view.puzzleUnlocks.keys(), (puzName) => [
        puzName,
        this.teamController.getClientPuzzleState(puzName),
      ])
    );
    const cutsceneChoices = Object.fromEntries(
      CLIENT_SHARED_CUTSCENE_CHOICES.flatMap<[string, number]>((keyframeId) => {
        const choice = progressTracker.view.cutsceneChoices.get(keyframeId);
        return choice !== undefined ? [[keyframeId, choice]] : [];
      })
    );
    const extraGuesses = Object.fromEntries(
      progressTracker.view.puzzleNumExtraGuesses.entries()
    );

    return {
      displayName,
      puzzles: puzzlesDict,
      huntStartTime: server.huntStartTime,
      huntEndTime: server.huntEndTime,
      hopeSnapshot: progressTracker.getTotalHope(),
      finishedData: this.teamController.getFinishedData(),
      cutsceneChoices,
      lockedSolutions: this.teamController.getLockedSolutions(),
      extraGuesses:
        Object.keys(extraGuesses).length > 0 ? extraGuesses : undefined,
    };
  }
}

export type TeamServerScopeSpec = {
  engine: TeamEngine;
};

export const teamServerScopeHooks: ServerScopeHooks<ScopeType.TEAM> = {
  createEngine: ({ execCtx, engineCtx }) => {
    const { controller, scope } = engineCtx;
    const teamController = controller.getTeamControllerIfExists(scope.teamId);
    if (teamController === null)
      return execCtx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_TEAM, null);
    return new TeamEngine(engineCtx, teamController);
  },
  engineIdentityFunc: (engine) => engine,
};
