export enum SyncObjectType {
  FAST = "fast",
  MISC = "misc",
}

export type SockServerConfig = {
  /**
   * Maximum number of teams to batch into one request when syncing
   * misc team state to backend.
   */
  miscTeamStateTeamsSyncLimit: number;
  /** Maximum number of parallel sync backend requests. */
  syncThrottleParallelism: number;
  /** Maximum number of scopes to sync at once. */
  scopesSyncLimit: number;
  /**
   * Amount of time to throttle the slow sync, depending on the
   * slow sync type.
   */
  slowSyncThrottle: { [syncObjectType in SyncObjectType]: number };
  /** Whether to disable the admin notifier. */
  disableAdminNotifier?: boolean;
};

export type MockTeamOpts = {
  teamId: string;
  displayName: string;
};

export type MockServerOpts = {
  dumpBackendRequests?: boolean;
  browserStoragePrefix: string;
  forceHuntEnd?: boolean;
  isPosthunt?: boolean;
  bootstrapTeam?: MockTeamOpts;
  farmerWsEndpoints: string[];
};
