import { SolveAttemptInfo } from "sockapi/solve-attempts";
import { HopeSnapshot } from "sockapi/gacha/hope";
import { GachaPrize } from "sockapi/gacha/pulls";

export enum TeamProgressEventReasonType {
  MISC = "misc",
  HINT_ANSWERED = "hint_answered",
  GACHA = "gacha",
  HOPE_POOL = "hope_pool",
}

export type TeamProgressEventReason =
  | {
      type: TeamProgressEventReasonType.MISC;
      reasonText?: string;
    }
  | {
      type: TeamProgressEventReasonType.HINT_ANSWERED;
    }
  | {
      type: TeamProgressEventReasonType.GACHA;
    }
  | {
      type: TeamProgressEventReasonType.HOPE_POOL;
      cycleId: number;
    };

export enum TeamProgressEventType {
  SOLVE_ATTEMPT = "solve_attempt",
  UNLOCK_PUZZLE = "unlock_puzzle",
  SPIN_GACHA = "spin_gacha",
  GET_HOPE = "get_hope",
  GET_EXTRA_GUESSES = "get_extra_guesses",
  GET_CANNED_HINT = "get_canned_hint",
  OPEN_CANNED_HINT = "open_canned_hint",
  GET_HINT_TOKEN = "get_hint_token",
  GET_COLLECTIBLE = "get_collectible",
  CUTSCENE_CHOICE = "cutscene_choice",
  GET_OLIVE_WISH = "get_olive_wish",
  GET_BLOCKS_WISH = "get_blocks_wish",
}

type TeamProgressCounterEventExtraInfo = {
  // may be negative
  num: number;
  reason: TeamProgressEventReason;
};
type TeamProgressGlobalCounterEventSpec = {
  extraInfo: TeamProgressCounterEventExtraInfo;
};

type TeamProgressEventSpecs = {
  [TeamProgressEventType.SOLVE_ATTEMPT]: {
    extraInfo: Omit<SolveAttemptInfo, "timestamp"> & {
      puzName: string;
      hopeGained?: number;
      solveHintPrizePuzName?: string;
    };
  };
  [TeamProgressEventType.UNLOCK_PUZZLE]: {
    extraInfo: {
      puzName: string;
      untitledPuzzleIndex?: number;
    };
  };
  [TeamProgressEventType.SPIN_GACHA]: {
    extraInfo: {
      pullId: string;
      prize: GachaPrize;
    };
  };
  [TeamProgressEventType.GET_HOPE]: {
    extraInfo: {
      // may be negative
      hopeAmt: number;
      reason: TeamProgressEventReason;
    };
  };
  [TeamProgressEventType.GET_EXTRA_GUESSES]: {
    extraInfo: { puzName: string } & TeamProgressCounterEventExtraInfo;
  };
  [TeamProgressEventType.GET_CANNED_HINT]: {
    extraInfo: { puzName: string } & TeamProgressCounterEventExtraInfo;
  };
  [TeamProgressEventType.OPEN_CANNED_HINT]: {
    extraInfo: { puzName: string };
  };
  [TeamProgressEventType.GET_HINT_TOKEN]: TeamProgressGlobalCounterEventSpec;
  [TeamProgressEventType.GET_COLLECTIBLE]: {
    extraInfo: { collectibleId: string } & TeamProgressCounterEventExtraInfo;
  };
  [TeamProgressEventType.CUTSCENE_CHOICE]: {
    extraInfo: {
      keyframeId: string;
      index: number;
    };
  };
  [TeamProgressEventType.GET_OLIVE_WISH]: TeamProgressGlobalCounterEventSpec;
  [TeamProgressEventType.GET_BLOCKS_WISH]: TeamProgressGlobalCounterEventSpec;
};
export type TeamProgressGlobalCounterEventType =
  | TeamProgressEventType.GET_HINT_TOKEN
  | TeamProgressEventType.GET_OLIVE_WISH
  | TeamProgressEventType.GET_BLOCKS_WISH;

/** Additional info to record about the event. */
type TeamProgressEventRecordExtraInfoDict = {
  [TEventType in TeamProgressEventType]: TeamProgressEventSpecs[TEventType]["extraInfo"];
};

export type TeamProgressEventRecordExtraInfo<
  TEventType extends TeamProgressEventType
> = TeamProgressEventRecordExtraInfoDict[TEventType];
type TeamProgressEventRecordInfo<TEventType extends TeamProgressEventType> =
  TeamProgressEventRecordExtraInfo<TEventType>;

/**
 * Once created, event records must never be modified, only deleted.
 * This is because event records may be shared between different
 * team progress trackers.
 * If information about an event needs to be modified dynamically,
 * use a scope engine to track that state instead.
 */
export type TeamProgressEventRecord<
  TEventType extends TeamProgressEventType = TeamProgressEventType
> = Readonly<{
  type: TEventType;
  timestamp: number;
  info: TeamProgressEventRecordInfo<TEventType>;
}>;

/**
 * Utility function to convince typescript that types match when creating
 * event records.
 */
export const createTeamProgressEventRecord = <
  TEventType extends TeamProgressEventType
>(
  type: TEventType,
  timestamp: number,
  info: TeamProgressEventRecordInfo<TEventType>
): TeamProgressEventRecord<TEventType> => ({
  type,
  timestamp,
  info,
});

export type TeamProgressEventsForType<
  TEventType extends TeamProgressEventType
> = {
  [timestamp: number]: TeamProgressEventRecord<TEventType>;
};

export type TeamProgress = {
  events: {
    [TEventType in TeamProgressEventType]?: TeamProgressEventsForType<TEventType>;
  };
  hopeSnapshot?: HopeSnapshot;
};

export const getTeamProgressEventsForType = <
  TEventType extends TeamProgressEventType
>(
  teamProgress: TeamProgress,
  eventType: TEventType
): TeamProgressEventsForType<TEventType> => {
  const existingEventsForType = teamProgress.events[eventType];
  if (existingEventsForType !== undefined) return existingEventsForType;
  const { events } = teamProgress;
  const eventsForType: (typeof teamProgress.events)[TEventType] = {};
  events[eventType] = eventsForType;
  return eventsForType;
};
