  [ScopeType.KEYFRAME]: keyframeServerScopeHooks,
  [ScopeType.TEAM_CUTSCENES]: teamCutscenesServerScopeHooks,
  [ScopeType.MOCK_DIRECT_BACKEND]: mockDirectBackendServerScopeHooks,
  [ScopeType.TEAM_OLIVE]: teamOliveServerScopeHooks,
  [ScopeType.ADMIN_QUEUE]: adminQueueServerScopeHooks,
  [ScopeType.ADMIN_QUEUE_TASK]: adminQueueTaskServerScopeHooks,
  [ScopeType.TEAM_BLOCKS]: teamBlocksServerScopeHooks,
  [ScopeType.TEAM_COLLECTIBLES]: teamCollectiblesServerScopeHooks,
  [ScopeType.TEAM_FARMER]: teamFarmerServerScopeHooks,
  [ScopeType.TEAM_BIG_BRANCH]: teamBigBranchServerScopeHooks,
  [ScopeType.TEAM_PUZZLE_144]: teamPuzzle144ServerScopeHooks,
  [ScopeType.HOPE_POOL]: hopePoolServerScopeHooks,
  [ScopeType.TEAM_HOPE_POOL]: teamHopePoolServerScopeHooks,
  [ScopeType.TEAM_HOPELESS]: teamHopelessServerScopeHooks,
  [ScopeType.TEAM_HISTORY]: teamHistoryServerScopeHooks,
  [ScopeType.TEAM_TITLES]: teamTitlesServerScopeHooks,
  [ScopeType.TEAM_PUZZLE_139]: teamPuzzle139ServerScopeHooks,
