import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import { PuzzleStatsSummary } from "sockapi/scope-specs/hunt-stats-spec";
import { PuzzleStats, PuzzleStatsUpdate } from "sockapi/scope-specs/stats-spec";
import { TeamProgressEventUpdateType } from "sockapi/team-progress-updates";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
  ScopeEngine,
} from "sock-server/scope-engines";
import { ClientConnection } from "sock-server/client-connections";
import {
  sumMap,
  incrementMap,
} from "sock-server/scope-specs/hunt-stats-server-spec";
import { SetMap, MapMap } from "util/util";

class StatsEngine {
  ctx: ScopeEngineContext<ScopeType.STATS>;
  customEngine: ScopeEngine<ScopeType.TEAM_PUZZLE_105> | null;

  unlocks: Map<string, number>;
  solves: Map<string, number>;
  guesses: SetMap<string, number>;
  hints: MapMap<string, number, boolean>;
  getCannedHints: Map<number, number>;
  openCannedHints: Map<number, number>;

  state: PuzzleStats;
  update: PuzzleStatsUpdate;
  summary: PuzzleStatsSummary;

  constructor(ctx: ScopeEngineContext<ScopeType.STATS>) {
    this.ctx = ctx;
    const { customPuzzleData } = ctx.server.getPuzzle(ctx.scope.puzName);
    this.customEngine =
      customPuzzleData === undefined
        ? null
        : ctx.server.getOrCreateEngineOrThrow({
            type: ScopeType.TEAM_PUZZLE_105,
            teamId: customPuzzleData.srcTeamId,
          });

    this.unlocks = new Map();
    this.solves = new Map();
    this.guesses = new SetMap();
    this.hints = new MapMap();
    this.getCannedHints = new Map();
    this.openCannedHints = new Map();

    this.state = this.calculateState();
    this.update = {};
    this.summary = this.calculateSummary();
  }

  getPermissions(conn: ClientConnection): ScopePermissions {
    if (this.ctx.server.getIsHuntOver()) return ScopePermissions.READ;
    if (conn.teamId === this.customEngine?.ctx.scope.teamId)
      return ScopePermissions.READ;
    return ScopePermissions.ADMIN_ONLY;
  }

  getState(): SockAPIScopeState<ScopeType.STATS> {
    return this.state;
  }

  private calculateState(): PuzzleStats {
    const stats: PuzzleStats = {
      unlocks: Object.fromEntries(this.unlocks),
      solves: Object.fromEntries(this.solves),
      guesses: {},
      hints: 0,
      getCannedHints: sumMap(this.getCannedHints),
      openCannedHints: this.openCannedHints.size,
    };
    for (const [teamId, set] of this.guesses.l1Entries())
      stats.guesses[teamId] = set.size;
    for (const [teamId, map] of this.hints.l1Entries()) stats.hints += map.size;
    return stats;
  }

  private calculateSummary(): PuzzleStatsSummary {
    const { order } = this.ctx.server.getPuzzle(this.ctx.scope.puzName);
    const stats: PuzzleStatsSummary = {
      order: this.customEngine?.state.status?.submitTime ?? order,
      unlocks: this.unlocks.size,
      solves: this.solves.size,
      guesses: 0,
      hints: 0,
      getCannedHints: sumMap(this.getCannedHints),
      openCannedHints: this.openCannedHints.size,
      unhinted: 0,
      hinted: 0,
      unsolved: 0,
    };
    for (const [teamId, set] of this.guesses.l1Entries()) {
      stats.guesses += set.size;
      const usedToken = [...this.hints.l1Get(teamId).values()].some((b) => b);
      if (!this.solves.has(teamId)) stats.unsolved++;
      else if (usedToken) stats.hinted++;
      else stats.unhinted++;
    }
    for (const [teamId, map] of this.hints.l1Entries()) stats.hints += map.size;
    return stats;
  }

  handleSolveAttempt(
    type: TeamProgressEventUpdateType,
    teamId: string,
    timestamp: number,
    isCorrect: boolean
  ): true {
    switch (type) {
      case TeamProgressEventUpdateType.ADD: {
        if (this.guesses.add(teamId, timestamp)) {
          this.update.guesses ??= {};
          this.update.guesses[teamId] = (this.update.guesses[teamId] ?? 0) + 1;
        }
        if (isCorrect && !this.solves.has(teamId)) {
          (this.update.solves ??= {})[teamId] = timestamp;
          this.solves.set(teamId, timestamp);
        }
        return true;
      }
      case TeamProgressEventUpdateType.REMOVE: {
        if (this.guesses.delete(teamId, timestamp)) {
          this.update.guesses ??= {};
          this.update.guesses[teamId] = (this.update.guesses[teamId] ?? 0) - 1;
        }
        if (isCorrect && this.solves.has(teamId)) {
          (this.update.solves ??= {})[teamId] = null;
          this.solves.delete(teamId);
        }
        return true;
      }
    }
  }

  handleUnlockPuzzle(
    type: TeamProgressEventUpdateType,
    teamId: string,
    timestamp: number
  ): true {
    switch (type) {
      case TeamProgressEventUpdateType.ADD: {
        if (!this.unlocks.has(teamId)) {
          (this.update.unlocks ??= {})[teamId] = timestamp;
          this.unlocks.set(teamId, timestamp);
        }
        return true;
      }
      case TeamProgressEventUpdateType.REMOVE: {
        if (this.unlocks.has(teamId)) {
          (this.update.unlocks ??= {})[teamId] = null;
          this.unlocks.delete(teamId);
        }
        return true;
      }
    }
  }

  handleHint(
    type: TeamProgressEventUpdateType,
    teamId: string,
    timestamp: number,
    usesToken: boolean
  ): true {
    switch (type) {
      case TeamProgressEventUpdateType.ADD: {
        if (this.hints.setIfAbsent(teamId, timestamp, usesToken))
          this.update.hints = (this.update.hints ?? 0) + 1;
        return true;
      }
      case TeamProgressEventUpdateType.REMOVE: {
        if (this.hints.delete(teamId, timestamp))
          this.update.hints = (this.update.hints ?? 0) - 1;
        return true;
      }
    }
  }

  handleGetCannedHint(
    type: TeamProgressEventUpdateType,
    timestamp: number,
    num: number
  ) {
    const delta = incrementMap(this.getCannedHints, type, timestamp, num);
    if (delta !== 0)
      this.update.getCannedHints = (this.update.getCannedHints ?? 0) + delta;
  }

  handleOpenCannedHint(type: TeamProgressEventUpdateType, timestamp: number) {
    const delta = incrementMap(this.openCannedHints, type, timestamp, 1);
    if (delta !== 0)
      this.update.openCannedHints = (this.update.openCannedHints ?? 0) + delta;
  }

  flushUpdate(): boolean {
    if (Object.keys(this.update).length === 0) return false;
    this.ctx.update(this.ctx.scope, this.update);
    this.state = this.calculateState();
    this.update = {};
    this.summary = this.calculateSummary();
    return true;
  }
}

export type StatsServerScopeSpec = {
  engine: StatsEngine;
};

export const statsServerScopeHooks: ServerScopeHooks<ScopeType.STATS> = {
  createEngine: ({ execCtx, engineCtx }) => {
    return new StatsEngine(engineCtx);
  },
  engineIdentityFunc: (engine) => engine,
};
