import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import { SockAPITeamCollectibleState } from "sockapi/scope-specs/team-collectibles-spec";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import { getTeamCollectibleIcons } from "sockapi/gacha/pulls";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { ClientConnection } from "sock-server/client-connections";
import { TeamProgressUpdateDiff } from "sock-server/team-progress/team-progress-diffs";
import { TeamController } from "sock-server/server-controllers/team-controller";

class TeamCollectiblesEngine {
  ctx: ScopeEngineContext<ScopeType.TEAM_COLLECTIBLES>;
  teamController: TeamController;

  constructor(
    ctx: ScopeEngineContext<ScopeType.TEAM_COLLECTIBLES>,
    teamController: TeamController
  ) {
    this.ctx = ctx;
    this.teamController = teamController;
  }

  getPermissions(conn: ClientConnection) {
    // Everyone can see everyone else's collectibles.
    return ScopePermissions.READ;
  }

  getCollectibleState(collectibleId: string): SockAPITeamCollectibleState {
    const { num = 0, timestamp = 0 } =
      this.teamController.progressTracker.view.collectibleState.get(
        collectibleId
      ) ?? {};
    const { name, nounPrefix, icon, isHiddenFromTeamName } =
      this.ctx.server.puzzlesDB.collectiblesData[collectibleId];
    return {
      name,
      nounPrefix,
      icon,
      isHiddenFromTeamName,
      num,
      timestamp,
    };
  }

  getState(): SockAPIScopeState<ScopeType.TEAM_COLLECTIBLES> {
    const { collectibleState } = this.teamController.progressTracker.view;

    return {
      collectibles: Object.fromEntries(
        Array.from(collectibleState.entries(), ([collectibleId]) => [
          collectibleId,
          this.getCollectibleState(collectibleId),
        ])
      ),
    };
  }

  getIcons(): string {
    return getTeamCollectibleIcons(
      this.getState(),
      this.ctx.server.huntEndTime
    );
  }

  handleTeamProgressUpdate(diff: TeamProgressUpdateDiff) {
    const { diffCollectibleState = {} } = diff;
    if (Object.keys(diffCollectibleState).length === 0) return;

    this.ctx.update(this.ctx.scope, {
      collectibles: Object.fromEntries(
        Object.keys(diffCollectibleState).map((collectibleId) => [
          collectibleId,
          this.getCollectibleState(collectibleId),
        ])
      ),
    });
  }
}

export type TeamCollectiblesServerScopeSpec = {
  engine: TeamCollectiblesEngine;
};

export const teamCollectiblesServerScopeHooks: ServerScopeHooks<ScopeType.TEAM_COLLECTIBLES> =
  {
    createEngine: ({ execCtx, engineCtx }) => {
      const { controller, scope } = engineCtx;
      const teamController = controller.getTeamControllerIfExists(scope.teamId);
      if (teamController === null)
        return execCtx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_TEAM, null);
      return new TeamCollectiblesEngine(engineCtx, teamController);
    },
    engineIdentityFunc: (engine) => engine,
  };
