import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import {
  DirectBackendReqType,
  DirectBackendReqPayload,
  DirectBackendRespPayload,
  DirectBackendReq,
  DirectBackendResp,
} from "sockapi/direct-backend";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { BackendScopeState } from "sockapi/backend-scope-state";
import { RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";

class MockDirectBackendEngine {
  ctx: ScopeEngineContext<ScopeType.MOCK_DIRECT_BACKEND>;
  state: BackendScopeState<ScopeType.MOCK_DIRECT_BACKEND>;

  constructor(
    ctx: ScopeEngineContext<ScopeType.MOCK_DIRECT_BACKEND>,
    initState?: BackendScopeState<ScopeType.MOCK_DIRECT_BACKEND>
  ) {
    this.ctx = ctx;
    this.state = initState ?? {
      submittedImages: {},
    };
  }

  getPermissions(conn: ClientConnection) {
    return (this.ctx.server.opts.enableDevFeatures ?? false) &&
      conn.teamId === this.ctx.scope.teamId
      ? ScopePermissions.FULL
      : ScopePermissions.ADMIN_ONLY;
  }

  private async handleReqAsync<TReqType extends DirectBackendReqType>(
    reqCtx: RequestContext,
    req: DirectBackendReq<TReqType>,
    file?: string
  ): Promise<DirectBackendResp<TReqType> | null> {
    const handlers: {
      [TReqType in DirectBackendReqType]: (
        payload: DirectBackendReqPayload<TReqType>
      ) => Promise<DirectBackendRespPayload<TReqType> | null>;
    } = {
      [DirectBackendReqType.DUMMY]: async () => "Mock direct backend is up!",
      [DirectBackendReqType.SUBMIT_IMAGE]: async ({ slot }) => {
        if (file === undefined) return reqCtx.setBadRequestAndReturn(null);
        const teamId = reqCtx.conn.teamId;
        const imageId = `${slot}_${teamId}`;
        this.state.submittedImages[imageId] = file;
        this.ctx.onModify();
        await this.ctx.server.handleImageSubmissionAsync(teamId, slot, file);
        return "";
      },
      [DirectBackendReqType.GET_ERRATUM_EMAILS]: async ({ puzName }) => [
        "solver1@example.com",
        "solver2@example.com",
        "solver3@example.com",
      ],
    };
    const reqType: TReqType = req.type;
    const respPayload = await handlers[reqType](req.payload);
    if (respPayload === null) return null;
    return {
      type: reqType,
      payload: respPayload,
    };
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.MOCK_DIRECT_BACKEND>
  ) {
    const { req, file } = payload;
    const resp = await this.handleReqAsync(reqCtx, req, file);
    if (resp === null) return;
    reqCtx.sendStepResp(this.ctx.scope, resp);
  }
}

export type MockDirectBackendServerScopeSpec = {
  engine: MockDirectBackendEngine;
};

export const mockDirectBackendServerScopeHooks: ServerScopeHooks<ScopeType.MOCK_DIRECT_BACKEND> =
  {
    createEngine: ({ engineCtx, initState }) => {
      return new MockDirectBackendEngine(engineCtx, initState);
    },
    engineIdentityFunc: (engine) => engine,
  };
