import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { ClientConnection } from "sock-server/client-connections";

class KeyframeEngine {
  ctx: ScopeEngineContext<ScopeType.KEYFRAME>;
  state: SockAPIScopeState<ScopeType.KEYFRAME>;

  constructor(
    ctx: ScopeEngineContext<ScopeType.KEYFRAME>,
    state: SockAPIScopeState<ScopeType.KEYFRAME>
  ) {
    this.ctx = ctx;
    this.state = state;
  }

  getPermissions(conn: ClientConnection) {
    const teamController = this.ctx.server.getTeamController(conn.teamId);
    if (!teamController.isPuzzleUnlocked(this.state.puzName))
      return ScopePermissions.ADMIN_ONLY;
    const { keyframeReq } = this.state;
    if (
      keyframeReq !== undefined &&
      !teamController.getIsConditionSatisfied(keyframeReq)
    )
      return ScopePermissions.ADMIN_ONLY;
    return ScopePermissions.READ;
  }

  getState() {
    return this.state;
  }
}

export type KeyframeServerScopeSpec = {
  engine: KeyframeEngine;
};

export const keyframeServerScopeHooks: ServerScopeHooks<ScopeType.KEYFRAME> = {
  createEngine: ({ execCtx, engineCtx, initState }) => {
    const { server, scope } = engineCtx;
    const keyframe = server.puzzlesDB.keyframesData.keyframes[scope.keyframeId];
    if (keyframe === undefined)
      return execCtx.setErrCodeAndReturn(
        SockAPIErrorCode.INVALID_KEYFRAME,
        null
      );
    return new KeyframeEngine(engineCtx, keyframe);
  },
  engineIdentityFunc: (engine) => engine,
};
