import { ScopeType } from "sockapi/scope-types";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";

class BigBoardEngine {
  ctx: ScopeEngineContext<ScopeType.BIG_BOARD>;

  constructor(ctx: ScopeEngineContext<ScopeType.BIG_BOARD>) {
    this.ctx = ctx;
  }

  getPermissions() {
    return ScopePermissions.ADMIN_ONLY;
  }

  getState() {
    const { server } = this.ctx;
    return {
      teams: Object.fromEntries(
        Array.from(server.teamControllers.values(), (teamController) => [
          teamController.teamId,
          teamController.getClientBigBoardState(),
        ])
      ),
    };
  }
}

export type BigBoardServerScopeSpec = {
  engine: BigBoardEngine;
};

export const bigBoardServerScopeHooks: ServerScopeHooks<ScopeType.BIG_BOARD> = {
  createEngine: ({ engineCtx }) => {
    return new BigBoardEngine(engineCtx);
  },
  engineIdentityFunc: (engine) => engine,
};
