import { ScopeType } from "sockapi/scope-types";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { ClientConnection } from "sock-server/client-connections";

class TeamErrataEngine {
  ctx: ScopeEngineContext<ScopeType.TEAM_ERRATA>;

  constructor(ctx: ScopeEngineContext<ScopeType.TEAM_ERRATA>) {
    this.ctx = ctx;
  }

  getPermissions(conn: ClientConnection) {
    return conn.teamId === this.ctx.scope.teamId
      ? ScopePermissions.FULL
      : ScopePermissions.ADMIN_ONLY;
  }

  getState() {
    const {
      server,
      scope: { teamId },
    } = this.ctx;
    return Object.fromEntries(
      Object.values(
        server.scopeEnginesController.getEnginesForType(
          ScopeType.ERRATUM_CONTENT
        )
      )
        .filter((engine) => engine.isVisibleToTeam(teamId))
        .map(
          ({
            ctx: {
              scope: { erratumId },
            },
            state: { puzName, timestamp },
          }) => [erratumId, { puzName, timestamp }]
        )
    );
  }
}

export type TeamErrataServerScopeSpec = {
  engine: TeamErrataEngine;
};

export const teamErrataServerScopeHooks: ServerScopeHooks<ScopeType.TEAM_ERRATA> =
  {
    createEngine: ({ engineCtx }) => {
      return new TeamErrataEngine(engineCtx);
    },
    engineIdentityFunc: (engine) => engine,
  };
