import { SockServerClock } from "sock-server/SockServer";

class RateLimitTracker {
  clock: SockServerClock;
  intervalMs: number;
  capacity: number;
  currInterval: number;
  currIntervalLoad: number;

  constructor(clock: SockServerClock, intervalMs: number, capacity: number) {
    this.clock = clock;
    this.intervalMs = intervalMs;
    this.capacity = capacity;
    this.currInterval = 0;
    this.currIntervalLoad = 0;
  }

  private updateCurrInterval(): void {
    const newInterval = Math.floor(this.clock.genTimestamp() / this.intervalMs);
    if (newInterval !== this.currInterval) {
      this.currInterval = newInterval;
      this.currIntervalLoad = 0;
    }
  }

  isRateLimited(): boolean {
    this.updateCurrInterval();
    return this.currIntervalLoad >= this.capacity;
  }

  /**
   * Returns whether the request should be allowed based on the
   * rate limit, and updates the current load.
   * `weight` sets the amount of load the request contributes
   * to the rate limit.
   */
  checkRequest(weight?: number): boolean {
    if (this.isRateLimited()) return false;
    this.currIntervalLoad += weight ?? 1;
    return true;
  }
}

export default RateLimitTracker;
