import { ScopeType } from "sockapi/scope-types";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import {
  PuzzleContent,
  arePuzzleContentEqual,
} from "sockapi/scope-specs/puzzle-content-spec";
import { ClientConnection } from "sock-server/client-connections";
import PUZZLE_CONFIG from "sock-server/puzzles/puzzle-config";

class PuzzleContentEngine {
  ctx: ScopeEngineContext<ScopeType.PUZZLE_CONTENT>;
  rawContent?: PuzzleContent;

  constructor(
    ctx: ScopeEngineContext<ScopeType.PUZZLE_CONTENT>,
    rawContent?: PuzzleContent
  ) {
    this.ctx = ctx;
    this.rawContent = rawContent;
  }

  getPermissions(conn: ClientConnection) {
    const {
      server,
      scope: { puzName },
    } = this.ctx;
    if (server.getIsHuntOver()) return ScopePermissions.READ;
    const dependentUnlock = PUZZLE_CONFIG.contentOnlyPuzzles[puzName];
    return server
      .getTeamController(conn.teamId)
      .isPuzzleUnlocked(dependentUnlock ?? puzName)
      ? ScopePermissions.READ
      : ScopePermissions.ADMIN_ONLY;
  }

  getState() {
    const {
      server,
      scope: { puzName },
    } = this.ctx;
    const puzData = server.tryGetPuzzle(puzName);
    const customPuzzleContent = puzData?.customPuzzleContent ?? undefined;
    const firstKeyframeId =
      server.puzzlesDB.keyframesData.firstKeyframeIds[puzName];
    return customPuzzleContent !== undefined && this.rawContent === undefined
      ? {
          hash: "",
          content: customPuzzleContent,
        }
      : {
          ...(this.rawContent ?? {
            hash: "",
            content: firstKeyframeId !== undefined ? "<Cutscene />" : "",
          }),
          firstKeyframeId,
        };
  }

  updateContent(): void {
    const {
      server,
      scope: { puzName },
    } = this.ctx;
    const newContent = server.puzzlesDB.content.puzzles[puzName];
    if (arePuzzleContentEqual(newContent, this.rawContent)) return;

    this.rawContent = newContent;
    this.ctx.assign(this.ctx.scope, this.getState());
  }
}

export type PuzzleContentServerScopeSpec = {
  engine: PuzzleContentEngine;
};

export const puzzleContentServerScopeHooks: ServerScopeHooks<ScopeType.PUZZLE_CONTENT> =
  {
    createEngine: ({ execCtx, engineCtx }) => {
      const { server, scope } = engineCtx;
      const { puzName } = scope;

      return new PuzzleContentEngine(
        engineCtx,
        server.puzzlesDB.content.puzzles[puzName]
      );
    },
    engineIdentityFunc: (engine) => engine,
  };
