import { PromiseCallbacks, objectKeysUnsafe } from "util/util";
import { AsyncLock } from "util/locks";
import { SockAPIErrorCode } from "sockapi/errors";
import { Scope, simplifyScope, scopeToString } from "sockapi/scopes";
import {
  serverScopeEngineHooks,
  serverSockAPIScopeEngineHooks,
  getSteppableScopeEngine,
} from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineType,
  ScopeEngine,
  SockAPIScopeEngineType,
  SteppableScopeEngineType,
  BackendScopeEngineType,
  ScopeEngineStep,
  ScopeEngineContext,
  doesScopeEngineHaveSlowSyncState,
} from "sock-server/scope-engines";
import { WSRespType, WSResp } from "sockapi/sockapi-messages";
import { SyncObjectType } from "sockapi/server-config";
import { ClientConnection } from "sock-server/client-connections";
import { RequestContext, ExecContext } from "sock-server/exec-contexts";
import { SockServer } from "sock-server/SockServer";
import {
  BackendScope,
  BackendScopeState,
  BackendScopeStateEntry,
} from "sockapi/backend-scope-state";
import {
  SyncLoopController,
  SyncTracker,
} from "sock-server/backend-interface/backend-sync";
import { TeamController } from "sock-server/server-controllers/team-controller";
import { TeamAnswerableController } from "sock-server/server-controllers/team-answerable-controller";

const isConnectionPermitted = (
  permissions: ScopePermissions,
  conn: ClientConnection,
  isReadonly: boolean
): boolean => {
  // Admins can access any valid scope.
  if (conn.isAdmin) return true;

  switch (permissions) {
    case ScopePermissions.ADMIN_ONLY:
      return false;
    case ScopePermissions.READ:
      return isReadonly;
    case ScopePermissions.FULL:
      return true;
  }
  // Don't provide a default case so that typescript will
  // throw a build error if this is not exhaustive.
};

type ScopeEngineTrackingState<TScopeType extends ScopeEngineType> = {
  scope: Scope<TScopeType>;
  engine: ScopeEngine<TScopeType>;
  syncTracker: SyncTracker;
  /** Lock for scope steps to this engine. */
  stepLock?: AsyncLock;
  /**
   * Cache tag for the most recent scope state.
   * The client saves the cache tag along with the most recent
   * scope state in the scope state cache.
   * When the client reconnects next time, it sends the tag along
   * with the subscription request.
   * If the tag matches, the server does not need to send down
   * another copy of the scope state.
   */
  cacheTag?: string;
  /**
   * Serialized state packet to reuse, if no updates have been made
   * since the last state download.
   * Only used if state packet caching is enabled.
   */
  cachedSerializedStatePacket?: string;
};

type ScopeEngineTrackingStateForType<TScopeType extends ScopeEngineType> = {
  [scopeId: string]: ScopeEngineTrackingState<TScopeType>;
};

type FastSyncTask = {
  scope: Scope<BackendScopeEngineType>;
  revNumber: number;
  callbacks: PromiseCallbacks;
};

export class ScopeEnginesController {
  server: SockServer;
  engines: {
    [TScopeType in ScopeEngineType]?: ScopeEngineTrackingStateForType<TScopeType>;
  };

  syncLoopController: SyncLoopController;
  /**
   * Scope IDs for scopes that have been seen in a previous iteration
   * of the sync, in the current sync loop.
   * Used to ensure that we cycle through all scopes in each sync loop.
   */
  seenScopes: Set<string>;
  /**
   * Scopes requested for fast sync. They would be visited first
   * in the sync loop. The revision number is necessary to disambiguate
   * between multiple fast syncs of the same scope. The promise gets
   * completed when the specified revision has been synced.
   */
  fastSyncTasks: FastSyncTask[];

  constructor(server: SockServer) {
    this.server = server;
    this.engines = {};
    this.syncLoopController = new SyncLoopController(
      async () => await this.runSyncAsync()
    );
    this.seenScopes = new Set();
    this.fastSyncTasks = [];
  }

  teardown() {
    this.engines = {};
  }

  getTeamControllerIfExists(teamId: string): TeamController | null {
    return this.server.teamControllers.get(teamId) ?? null;
  }

  getAnswerableControllerIfUnlocked(
    ctx: ExecContext,
    teamId: string,
    puzName: string
  ): TeamAnswerableController | null {
    const teamController = this.getTeamControllerIfExists(teamId);
    if (teamController === null)
      return ctx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_TEAM, null);
    if (!teamController.isPuzzleUnlocked(puzName))
      return ctx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_PUZZLE, null);
    // Answerable controllers should only be created for answerables.
    const puzData = this.server.getPuzzle(puzName);
    if (puzData.answer === null)
      return ctx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_PUZZLE, null);
    return teamController.getAnswerableController(puzName);
  }

  private getEngineTrackingStateForType<TScopeType extends ScopeEngineType>(
    type: TScopeType
  ): ScopeEngineTrackingStateForType<TScopeType> {
    const engineTrackingStateForType = this.engines[type];
    if (engineTrackingStateForType !== undefined)
      return engineTrackingStateForType;
    const newEnginesForType = {};
    this.engines[type] = newEnginesForType;
    return newEnginesForType;
  }

  getEnginesForType<TScopeType extends ScopeEngineType>(
    type: TScopeType
  ): { [scopeId: string]: ScopeEngine<TScopeType> } {
    return Object.fromEntries(
      Object.entries(this.getEngineTrackingStateForType(type)).map(
        ([scopeId, { engine }]) => [scopeId, engine]
      )
    );
  }

  private createCacheTag(syncTracker: SyncTracker) {
    // The cache tag needs to include the serverId, since
    // the backend state may change between server restarts.
    return `${this.server.sockServerId}__${syncTracker.revNumber}`;
  }

  private handleModify<TScopeType extends ScopeEngineType>(
    scope: Scope<TScopeType>
  ): string {
    const engineTrackingState = this.getEngineTrackingStateIfExists(scope);
    if (engineTrackingState === undefined)
      throw new Error(`tried to modify unknown scope ${scopeToString(scope)}`);
    const { syncTracker } = engineTrackingState;
    syncTracker.handleModify();

    delete engineTrackingState.cachedSerializedStatePacket;

    const cacheTag = this.createCacheTag(syncTracker);
    engineTrackingState.cacheTag = cacheTag;
    return cacheTag;
  }

  fastSyncAsync<TScopeType extends BackendScopeEngineType>(
    scope: Scope<TScopeType>
  ): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      const engineTrackingState = this.getEngineTrackingStateIfExists(scope);
      if (engineTrackingState === undefined)
        throw new Error(
          `tried to fast sync unknown scope ${scopeToString(scope)}`
        );

      const { syncTracker } = engineTrackingState;
      syncTracker.handleModify();
      this.fastSyncTasks.push({
        scope,
        revNumber: syncTracker.revNumber,
        callbacks: { resolve, reject },
      });
      this.syncLoopController.triggerLoopAsync().catch(reject);
    });
  }

  createEngineContext<TScopeType extends ScopeEngineType>(
    scope: Scope<TScopeType>
  ): ScopeEngineContext<TScopeType> {
    return {
      scope,
      server: this.server,
      controller: this,
      assign: (scope, state) => {
        const tag = this.handleModify(scope);
        this.server.broadcastScopeAssign(scope, state, tag);
      },
      update: (scope, payload) => {
        const tag = this.handleModify(scope);
        this.server.broadcastScopeUpdate(scope, payload, tag);
      },
      onModify: () => this.handleModify(scope),
      fastSyncAsync: (scope) => this.fastSyncAsync(scope),
    };
  }

  /** Creates an engine and adds it to the tracking state. */
  private createEngineTrackingState<TScope extends Scope<ScopeEngineType>>(
    ctx: ExecContext,
    scope: TScope,
    initState?: TScope["type"] extends BackendScopeEngineType
      ? BackendScopeState<TScope["type"]>
      : undefined
  ): ScopeEngineTrackingState<TScope["type"]> | null {
    const engineTrackingStateForType = this.getEngineTrackingStateForType(
      scope.type
    );
    const scopeId = scopeToString(scope);
    if (engineTrackingStateForType[scopeId] !== undefined)
      throw new Error(`engine for ${scopeId} already exists`);

    const scopeType: TScope["type"] = scope.type;
    const engine = serverScopeEngineHooks[scopeType].createEngine({
      execCtx: ctx,
      engineCtx: this.createEngineContext(simplifyScope<TScope["type"]>(scope)),
      initState,
    });
    if (engine === null) return null;
    const syncTracker = new SyncTracker(
      this.server.config,
      this.server.clock,
      // TODO: make the sync throttle scopes-based
      SyncObjectType.MISC,
      (dirtyState) => {
        // TODO: think about how to reflect dirty state to the client
      }
    );
    engineTrackingStateForType[scopeId] = {
      scope,
      engine,
      syncTracker,
      cacheTag: this.createCacheTag(syncTracker),
    };
    return engineTrackingStateForType[scopeId];
  }

  createEngine<TScope extends Scope<ScopeEngineType>>(
    ctx: ExecContext,
    scope: TScope,
    initState?: TScope["type"] extends BackendScopeEngineType
      ? BackendScopeState<TScope["type"]>
      : undefined,
    skipEngineInit?: boolean
  ): ScopeEngine<TScope["type"]> | null {
    const engine =
      this.createEngineTrackingState(ctx, scope, initState)?.engine ?? null;
    if (!(skipEngineInit ?? false)) engine?.init?.();
    return engine;
  }

  private getEngineTrackingStateIfExists<TScope extends Scope<ScopeEngineType>>(
    scope: TScope
  ): ScopeEngineTrackingState<TScope["type"]> | undefined {
    const scopeId = scopeToString(scope);
    return this.getEngineTrackingStateForType(scope.type)[scopeId];
  }

  private getOrCreateEngineTrackingState<TScope extends Scope<ScopeEngineType>>(
    ctx: ExecContext,
    scope: TScope
  ): ScopeEngineTrackingState<TScope["type"]> | null {
    const engineTrackingState = this.getEngineTrackingStateIfExists(scope);
    if (engineTrackingState !== undefined) return engineTrackingState;
    return this.createEngineTrackingState(ctx, scope);
  }

  getEngineIfExists<TScope extends Scope<ScopeEngineType>>(
    scope: TScope
  ): ScopeEngine<TScope["type"]> | undefined {
    return this.getEngineTrackingStateIfExists(scope)?.engine;
  }

  getOrCreateEngine<TScope extends Scope<ScopeEngineType>>(
    ctx: ExecContext,
    scope: TScope
  ): ScopeEngine<TScope["type"]> | null {
    const engineTrackingState = this.getOrCreateEngineTrackingState(ctx, scope);
    return engineTrackingState?.engine ?? null;
  }

  handleSubscribe<TScope extends Scope<SockAPIScopeEngineType>>(
    ctx: RequestContext,
    scope: TScope,
    cacheTagFromClient?: string
  ): boolean {
    const engineTrackingState = this.getOrCreateEngineTrackingState(ctx, scope);
    if (engineTrackingState === null) return false;

    const { engine, cachedSerializedStatePacket, cacheTag } =
      engineTrackingState;

    const permissions = engine.getPermissions(ctx.conn);
    if (!isConnectionPermitted(permissions, ctx.conn, true)) {
      if (this.server.opts.logRequestErrDebugInfoAsync !== undefined) {
        this.server.opts
          .logRequestErrDebugInfoAsync(
            `got permission denied while requesting for scope ${JSON.stringify(
              scope
            )}`
          )
          .catch((err) => console.error(err));
      }
      return ctx.setErrCodeAndReturn(SockAPIErrorCode.PERMISSION_DENIED, false);
    }
    // If the client already has the most recent state in cache,
    // we don't need to send down the state packet.
    if (
      cacheTagFromClient !== undefined &&
      cacheTag !== undefined &&
      cacheTag === cacheTagFromClient
    )
      return true;

    const serializedPacket =
      cachedSerializedStatePacket ??
      (() => {
        // Put this on a separate line to typecheck the packet.
        const packet: WSResp = {
          type: WSRespType.SCOPE_ASSIGN,
          assign: {
            scope,
            state: engine.getState(),
            tag: cacheTag,
          },
        };
        const serializedPacket = JSON.stringify(packet);

        const scopeType: TScope["type"] = scope.type;
        if (serverSockAPIScopeEngineHooks[scopeType].cacheStatePacket ?? false)
          engineTrackingState.cachedSerializedStatePacket = serializedPacket;

        return serializedPacket;
      })();

    ctx.conn.sendRaw(serializedPacket);
    return true;
  }

  async handleStepAsync<TScope extends Scope<SteppableScopeEngineType>>(
    ctx: RequestContext,
    step: ScopeEngineStep<TScope>
  ): Promise<void> {
    const { conn } = ctx;
    const { scope, payload } = step;
    const engineTrackingState = this.getOrCreateEngineTrackingState(ctx, scope);
    if (engineTrackingState === null) return;

    const { engine } = engineTrackingState;
    const permissions = engine.getPermissions(conn);
    if (!isConnectionPermitted(permissions, conn, false))
      return ctx.setErrCode(SockAPIErrorCode.PERMISSION_DENIED);

    const scopeType: TScope["type"] = scope.type;
    engineTrackingState.stepLock ??= new AsyncLock();
    const steppableEngine = getSteppableScopeEngine(scopeType, engine);
    await engineTrackingState.stepLock.acquireAndRunAsync(
      async () => await steppableEngine.handleStepAsync(ctx, payload)
    );
  }

  /**
   * Run one iteration of the sync. Should only be used by the sync loop.
   * Returns if the sync is already complete (i.e. there are no more
   * scope states to sync).
   */
  private async runSyncAsync(): Promise<boolean> {
    const packet: { [scopeId: string]: BackendScopeStateEntry } = {};
    const syncTrackers: SyncTracker[] = [];
    const addToPacket = <TScope extends BackendScope>(
      scope: TScope,
      state: BackendScopeState<TScope["type"]>,
      syncTracker: SyncTracker
    ) => {
      const scopeId = scopeToString(scope);
      // If multiple syncs are requested for the same scope
      // at the same time, don't track it multiple times.
      if (packet[scopeId] !== undefined) return;
      if (syncTracker.snapshot()) {
        packet[scopeId] = {
          scope,
          state,
        };
        syncTrackers.push(syncTracker);
        this.seenScopes.add(scopeId);
      }
    };
    for (const { scope, revNumber } of this.fastSyncTasks) {
      if (Object.keys(packet).length >= this.server.config.scopesSyncLimit)
        break;

      const engineTrackingState = this.getEngineTrackingStateIfExists(scope);
      if (engineTrackingState === undefined)
        throw new Error(
          `tried to fast sync unknown scope ${scopeToString(scope)}`
        );
      const { engine, syncTracker } = engineTrackingState;
      if (!doesScopeEngineHaveSlowSyncState(engine))
        throw new Error(
          `engine for ${scopeToString(scope)} doesn't have sync state`
        );
      if (syncTracker.syncedRevNumber >= revNumber) continue;
      // Add the scope even if it was seen in a previous sync loop
      // iteration, since the fast sync may be waiting for a more
      // recent version of the state.
      addToPacket(scope, engine.getSlowSyncState(), syncTracker);
    }
    for (const scopeType of objectKeysUnsafe(this.engines)) {
      for (const [scopeId, { scope, engine, syncTracker }] of Object.entries(
        this.engines[scopeType] ?? {}
      )) {
        // If any engine of this type doesn't have slow sync state,
        // then none of them do.
        if (!doesScopeEngineHaveSlowSyncState(engine)) break;
        if (Object.keys(packet).length >= this.server.config.scopesSyncLimit)
          break;
        if (this.seenScopes.has(scopeId)) continue;
        addToPacket(scope, engine.getSlowSyncState(), syncTracker);
      }
    }
    if (Object.keys(packet).length === 0) {
      // Reset the seen scopes to try them again on the next loop.
      this.seenScopes.clear();
      return true;
    }
    const didSyncSucceed = await (async () => {
      try {
        await this.server.syncDataThrottledAsync({
          scopeState: packet,
        });
        return true;
      } catch (err) {
        console.error(err);
        return false;
      }
    })();

    for (const syncTracker of syncTrackers) {
      syncTracker.handleSyncResult(didSyncSucceed);
    }

    const completedFastSyncTasks: FastSyncTask[] = [];
    const incompleteFastSyncTasks: FastSyncTask[] = [];
    for (const fastSyncTask of this.fastSyncTasks) {
      const { scope, revNumber } = fastSyncTask;
      const engineTrackingState = this.getEngineTrackingStateIfExists(scope);
      if (engineTrackingState === undefined)
        throw new Error(
          `could not find scope ${scopeToString(scope)} to complete fast sync`
        );

      if (engineTrackingState.syncTracker.syncedRevNumber < revNumber)
        incompleteFastSyncTasks.push(fastSyncTask);
      else completedFastSyncTasks.push(fastSyncTask);
    }
    this.fastSyncTasks = incompleteFastSyncTasks;

    for (const { callbacks } of completedFastSyncTasks) {
      callbacks.resolve();
    }

    return false;
  }
}
