import { EventController } from "util/events";
import { sleepAsync } from "util/timers";
import { AsyncSemaphore } from "util/locks";
import {
  FarmerServerStep,
  FarmerServerStepResponseType,
  FarmerServerStepResponse,
} from "sockapi/scope-specs/team-farmer-spec";
import {
  RawWSConnection,
  RawWSInterface,
} from "sock-server/server-interface/RawWSInterface";

const FARMER_SERVER_PASSWORD = "ciGh9ijeOopud4aGrohQu8eila1hieNe";

enum FarmerMsgType {
  STEP = "step",
  STEP_RESPONSE = "step_response",
  ERROR = "error",
}

type FarmerReq = {
  type: FarmerMsgType.STEP;
  stepId: string;
  password: string;
  step: FarmerServerStep;
};

type FarmerResp =
  | {
      type: FarmerMsgType.STEP_RESPONSE;
      stepId: string;
      stepResponse: FarmerServerStepResponse;
    }
  | {
      type: FarmerMsgType.ERROR;
    };

class FarmerClient {
  ws: RawWSInterface;
  conn?: RawWSConnection;
  isActive: boolean;
  onOpenEvent: EventController<RawWSConnection>;
  onRespOrCloseEvent: EventController<FarmerResp | null>;
  reconnectCounter: number;
  nextStepId: number;
  stepLock: AsyncSemaphore;

  constructor(ws: RawWSInterface) {
    this.ws = ws;
    this.isActive = true;
    this.onOpenEvent = new EventController();
    this.onRespOrCloseEvent = new EventController();
    this.reconnectCounter = 0;
    this.nextStepId = 0;
    this.stepLock = new AsyncSemaphore(100);
  }

  private genStepId(): string {
    const stepId = `farmer_step_${this.nextStepId}`;
    this.nextStepId++;
    return stepId;
  }

  private makeConnection() {
    const conn = this.ws.makeConnection(
      () => {
        this.reconnectCounter = 0;
        this.onOpenEvent.fire(conn);
      },
      () => {
        if (!this.isActive) return;
        this.onRespOrCloseEvent.fire(null);
        (async () => {
          // automatically reconnect
          await sleepAsync(Math.min(this.reconnectCounter, 5) * 1000);
          this.reconnectCounter++;
          this.makeConnection();
        })().catch(console.error);
      },
      (msg) => {
        this.onRespOrCloseEvent.fire(JSON.parse(msg));
      },
      (err) => {
        console.error(err);
      }
    );
    this.conn = conn;
    return conn;
  }

  private async lazyInitAsync(): Promise<RawWSConnection> {
    if (!this.isActive) throw new Error("attempt to use farmer while inactive");
    const conn = this.conn ?? this.makeConnection();
    return conn.isOpen() ? conn : await this.onOpenEvent.waitAsync();
  }

  async sendReqAsync(req: FarmerReq) {
    const conn = await this.lazyInitAsync();
    conn.send(JSON.stringify(req));
  }

  async getSimilaritiesAsync(
    step: FarmerServerStep
  ): Promise<FarmerServerStepResponse> {
    return await this.stepLock.acquireAndRunAsync(async () => {
      const stepId = this.genStepId();
      await this.sendReqAsync({
        type: FarmerMsgType.STEP,
        stepId,
        password: FARMER_SERVER_PASSWORD,
        step,
      });
      const { resp } = await this.onRespOrCloseEvent.waitForAsync((resp) =>
        resp === null ||
        resp.type === FarmerMsgType.ERROR ||
        (resp.type === FarmerMsgType.STEP_RESPONSE && resp.stepId === stepId)
          ? { resp }
          : null
      );

      if (resp === null || resp.type === FarmerMsgType.ERROR)
        return {
          type: FarmerServerStepResponseType.ERROR,
        };

      return resp.stepResponse;
    });
  }

  getLocksContention() {
    return this.stepLock.getNumWaitingOrActive();
  }

  teardown() {
    this.conn?.close();
    delete this.conn;
    this.isActive = false;
  }
}

export class LoadBalancedFarmerClient {
  clients: FarmerClient[];

  constructor(wsInterfaces: RawWSInterface[]) {
    this.clients = wsInterfaces.map((ws) => new FarmerClient(ws));
  }

  getSimilaritiesAsync(
    step: FarmerServerStep
  ): Promise<FarmerServerStepResponse> {
    return this.clients[
      Math.floor(Math.random() * this.clients.length)
    ].getSimilaritiesAsync(step);
  }

  getLocksContention() {
    return Math.max(
      ...this.clients.map((client) => client.getLocksContention())
    );
  }

  teardown() {
    for (const client of this.clients) {
      client.teardown();
    }
  }
}
