import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import {
  SockAPIScopeUpdatePayload,
  applyScopeUpdate,
} from "sockapi/scope-updates";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import { CUSTOM_TITLE_MAX_LENGTH } from "sockapi/scope-specs/team-titles-spec";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { BackendScopeState } from "sockapi/backend-scope-state";
import { RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";
import { TeamController } from "sock-server/server-controllers/team-controller";

class TeamTitlesEngine {
  ctx: ScopeEngineContext<ScopeType.TEAM_TITLES>;
  teamController: TeamController;
  state: SockAPIScopeState<ScopeType.TEAM_TITLES>;

  constructor(
    ctx: ScopeEngineContext<ScopeType.TEAM_TITLES>,
    teamController: TeamController,
    initState?: BackendScopeState<ScopeType.TEAM_TITLES>
  ) {
    this.ctx = ctx;
    this.teamController = teamController;
    this.state = initState ?? { titles: {} };
  }

  getPermissions(conn: ClientConnection): ScopePermissions {
    return conn.teamId === this.ctx.scope.teamId
      ? ScopePermissions.FULL
      : ScopePermissions.ADMIN_ONLY;
  }

  getState(): SockAPIScopeState<ScopeType.TEAM_TITLES> {
    const puzzle105Titles =
      this.ctx.server.scopeEnginesController.getEngineIfExists({
        type: ScopeType.TEAM_PUZZLE_105,
        teamId: this.ctx.scope.teamId,
      })?.state.confirmedTitles;

    return {
      titles: {
        ...this.state.titles,
        ...puzzle105Titles,
      },
    };
  }

  getSlowSyncState(): BackendScopeState<ScopeType.TEAM_TITLES> {
    return this.state;
  }

  applyAndBroadcastUpdate(
    payload: SockAPIScopeUpdatePayload<ScopeType.TEAM_TITLES>
  ) {
    applyScopeUpdate(ScopeType.TEAM_TITLES, this.state, payload);
    this.ctx.update(this.ctx.scope, payload);
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.TEAM_TITLES>
  ) {
    const { setTitle } = payload;
    if (setTitle !== undefined) {
      const { puzName, title } = setTitle;
      if (title.length > CUSTOM_TITLE_MAX_LENGTH) return reqCtx.setBadRequest();
      this.applyAndBroadcastUpdate({ titles: { [puzName]: title } });
      return reqCtx.sendStepResp(this.ctx.scope, "");
    }
    return reqCtx.sendStepResp(this.ctx.scope, "");
  }
}

export type TeamTitlesServerScopeSpec = {
  engine: TeamTitlesEngine;
};

export const teamTitlesServerScopeHooks: ServerScopeHooks<ScopeType.TEAM_TITLES> =
  {
    createEngine: ({ execCtx, engineCtx, initState }) => {
      const { controller, scope } = engineCtx;
      const teamController = controller.getTeamControllerIfExists(scope.teamId);
      if (teamController === null)
        return execCtx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_TEAM, null);
      return new TeamTitlesEngine(engineCtx, teamController, initState);
    },
    engineIdentityFunc: (engine) => engine,
  };
