import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import { MAX_ANSWER_LENGTH } from "sockapi/scope-specs/team-answerable-spec";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";
import { TeamAnswerableController } from "sock-server/server-controllers/team-answerable-controller";

class TeamAnswerableEngine {
  ctx: ScopeEngineContext<ScopeType.TEAM_ANSWERABLE>;
  answerableController: TeamAnswerableController;

  constructor(
    ctx: ScopeEngineContext<ScopeType.TEAM_ANSWERABLE>,
    answerableController: TeamAnswerableController
  ) {
    this.ctx = ctx;
    this.answerableController = answerableController;
  }

  getPermissions(conn: ClientConnection) {
    return conn.teamId === this.ctx.scope.teamId
      ? ScopePermissions.FULL
      : ScopePermissions.ADMIN_ONLY;
  }

  handleUpdateNumGuessesRemaining() {
    this.ctx.update(this.ctx.scope, {
      numGuessesRemaining: this.answerableController.getNumGuessesRemaining(),
    });
  }

  getState() {
    return {
      numGuessesRemaining: this.answerableController.getNumGuessesRemaining(),
      submissions: Object.fromEntries(
        Array.from(
          this.answerableController
            .getTeamController()
            .progressTracker.getSolveAttemptRecordsForPuzzle(
              this.ctx.scope.puzName
            ),
          ({ timestamp, info }) => {
            const { puzName, ...submissionInfo } = info;
            return [timestamp, { timestamp, ...submissionInfo }];
          }
        )
      ),
    };
  }

  private async handleSubmitAnswerAsync(
    reqCtx: RequestContext,
    answer: string
  ) {
    if (answer.length > MAX_ANSWER_LENGTH) return reqCtx.setBadRequest();

    const checkAnswerResult = await this.answerableController.checkAnswerAsync(
      reqCtx,
      answer
    );
    if (checkAnswerResult === null) return;
    reqCtx.sendStepResp(this.ctx.scope, checkAnswerResult);
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.TEAM_ANSWERABLE>
  ) {
    const { submitAnswer } = payload;
    if (submitAnswer !== undefined)
      await this.handleSubmitAnswerAsync(reqCtx, submitAnswer);
  }
}

export type TeamAnswerableServerScopeSpec = {
  engine: TeamAnswerableEngine;
};

export const teamAnswerableServerScopeHooks: ServerScopeHooks<ScopeType.TEAM_ANSWERABLE> =
  {
    createEngine: ({ execCtx, engineCtx }) => {
      const { controller, scope } = engineCtx;
      const answerableController = controller.getAnswerableControllerIfUnlocked(
        execCtx,
        scope.teamId,
        scope.puzName
      );
      if (answerableController === null) return null;
      return new TeamAnswerableEngine(engineCtx, answerableController);
    },
    engineIdentityFunc: (engine) => engine,
  };
