import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import { PuzzleStatsAdmin } from "sockapi/scope-specs/admin-stats-spec";
import { TeamProgressEventUpdateType } from "sockapi/team-progress-updates";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { ClientConnection } from "sock-server/client-connections";
import { SetMap } from "util/util";

class AdminStatsEngine {
  ctx: ScopeEngineContext<ScopeType.ADMIN_STATS>;

  guesses: SetMap<string, number>;

  state: PuzzleStatsAdmin;
  update: PuzzleStatsAdmin;

  constructor(ctx: ScopeEngineContext<ScopeType.ADMIN_STATS>) {
    this.ctx = ctx;

    this.guesses = new SetMap();

    this.state = {};
    this.update = {};
  }

  getPermissions(conn: ClientConnection): ScopePermissions {
    return ScopePermissions.ADMIN_ONLY;
  }

  getState(): SockAPIScopeState<ScopeType.ADMIN_STATS> {
    return this.state;
  }

  handleSolveAttempt(
    type: TeamProgressEventUpdateType,
    timestamp: number,
    answer: string,
    isCorrect: boolean,
    isWrong: boolean
  ): true {
    const key = JSON.stringify([answer, isCorrect, isWrong]);
    switch (type) {
      case TeamProgressEventUpdateType.ADD: {
        if (this.guesses.add(key, timestamp))
          this.update[key] = (this.update[key] ?? this.state[key] ?? 0) + 1;
        return true;
      }
      case TeamProgressEventUpdateType.REMOVE: {
        if (this.guesses.delete(key, timestamp))
          this.update[key] = (this.update[key] ?? this.state[key] ?? 0) - 1;
        return true;
      }
    }
  }

  flushUpdate() {
    if (Object.keys(this.update).length === 0) return;
    this.ctx.update(this.ctx.scope, this.update);
    Object.assign(this.state, this.update);
    this.update = {};
  }
}

export type AdminStatsServerScopeSpec = {
  engine: AdminStatsEngine;
};

export const adminStatsServerScopeHooks: ServerScopeHooks<ScopeType.ADMIN_STATS> =
  {
    createEngine: ({ execCtx, engineCtx }) => {
      return new AdminStatsEngine(engineCtx);
    },
    engineIdentityFunc: (engine) => engine,
  };
