import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import { BackendScopeState } from "sockapi/backend-scope-state";
import { createState, mergeUpdate } from "sockapi/scope-specs/wrapup-spec";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";

class WrapupEngine {
  ctx: ScopeEngineContext<ScopeType.WRAPUP>;
  state: SockAPIScopeState<ScopeType.WRAPUP>;

  constructor(
    ctx: ScopeEngineContext<ScopeType.WRAPUP>,
    initState?: BackendScopeState<ScopeType.WRAPUP>
  ) {
    this.ctx = ctx;
    this.state = initState ?? createState();
  }

  getPermissions(conn: ClientConnection): ScopePermissions {
    return ScopePermissions.ADMIN_ONLY;
  }

  getState(): SockAPIScopeState<ScopeType.WRAPUP> {
    return this.state;
  }

  getSlowSyncState(): BackendScopeState<ScopeType.WRAPUP> {
    return this.state;
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.WRAPUP>
  ) {
    const { id, claimer } = payload;
    const prevClaimer = this.state[id]?.claimer;
    if (claimer && prevClaimer && claimer !== prevClaimer)
      return reqCtx.setErrCode(SockAPIErrorCode.CLIENT_ERROR);
    mergeUpdate(this.state, payload);
    this.ctx.update(this.ctx.scope, payload);
    return reqCtx.sendStepResp(this.ctx.scope, true);
  }
}

export type WrapupServerScopeSpec = {
  engine: WrapupEngine;
};

export const wrapupServerScopeHooks: ServerScopeHooks<ScopeType.WRAPUP> = {
  createEngine: ({ execCtx, engineCtx, initState }) => {
    return new WrapupEngine(engineCtx, initState);
  },
  engineIdentityFunc: (engine) => engine,
};
