import { ScopeType } from "sockapi/scope-types";
import { ErratumContent } from "sockapi/scope-specs/erratum-content-spec";
import { HintResponse } from "sockapi/scope-specs/hint-spec";
import { SubmitImageSlot } from "sockapi/direct-backend";
import { TeamProgress } from "sockapi/team-progress";
import {
  BackendScopeState,
  BackendScopeStateEntry,
} from "sockapi/backend-scope-state";
import { BackendSyncReqPacket } from "sock-server/backend-interface/backend-sync";

export enum BackendNotificationDataType {
  HINT_ANSWERED = "hint_answered",
  TEAM_MEMBERS = "team_members",
  ERRATUM = "erratum",
  SUBMIT_IMAGE = "submit_image",
}

// Solve/unlock/victory notifications are handled by HuntLib.
type BackendNotificationHintAnsweredData = {
  type: BackendNotificationDataType.HINT_ANSWERED;
  hintId: string;
  response: HintResponse;
};

type BackendNotificationTeamMembersData = {
  type: BackendNotificationDataType.TEAM_MEMBERS;
  teamId: string;
  members: string[];
};

type BackendNotificationErratumData = {
  type: BackendNotificationDataType.ERRATUM;
  erratumId: string;
} & ErratumContent;

type BackendNotificationSubmitImage = {
  type: BackendNotificationDataType.SUBMIT_IMAGE;
  teamId: string;
  slot: SubmitImageSlot;
  url: string;
};

export type BackendNotification =
  | BackendNotificationHintAnsweredData
  | BackendNotificationTeamMembersData
  | BackendNotificationErratumData
  | BackendNotificationSubmitImage;

type GetInitDataBackendResp = {
  /**
   * Unique identifier for the sock server instance.
   * Should be different every time getInitData is called, even
   * if called again from the same machine.
   */
  sockServerId: string;
  /** Initial team data for all teams. */
  teams: { [teamId: string]: GetInitTeamDataBackendResp };
  scopeState: { [scopeId: string]: BackendScopeStateEntry };

  // Static hunt config to keep in sync with Django if we change them.
  maxGuessesPerPuzzle: number;
  huntStartTime: number;
  huntEndTime: number;
  huntCloseTime: number;

  /**
   * The next index for notifications that the server should start
   * pulling from.
   */
  notifsNextIndex: number;

  errata: { [erratumId: string]: ErratumContent };
};

export type GetInitTeamDataBackendResp = {
  userId: string;
  displayName: string;
  isInactive: boolean;
  isHidden: boolean;
  creationTime: number;
  members: string[];
  /** puzNames of puzzles viewed by the team. */
  views: readonly string[];
  hints: { [hintId: string]: BackendScopeState<ScopeType.HINT> };
  teamProgress: TeamProgress;
};

export type SyncDataBackendReq = {
  /** If specified, pulls notifications starting from the given index. */
  notifsStartIndex?: number;

  teamState?: { [teamId: string]: BackendSyncReqPacket };
  /** Scope state to overwrite. */
  scopeState?: { [scopeId: string]: BackendScopeStateEntry };
};

type SyncDataBackendResp = {
  notifs?: BackendNotification[];
  notifsNextIndex?: number;
};

export enum BackendReqType {
  GET_INIT_DATA = "get_init_data",
  GET_INIT_TEAM_DATA = "get_init_team_data",
  SYNC_DATA = "sync_data",
}

type BackendReqPayloadDict = {
  [BackendReqType.GET_INIT_DATA]: {
    workerId: string;
  };
  [BackendReqType.GET_INIT_TEAM_DATA]: {
    teamId: string;
  };
  [BackendReqType.SYNC_DATA]: SyncDataBackendReq;
};
type BackendRespPayloadDict = {
  [BackendReqType.GET_INIT_DATA]: GetInitDataBackendResp;
  [BackendReqType.GET_INIT_TEAM_DATA]: GetInitTeamDataBackendResp;
  [BackendReqType.SYNC_DATA]: SyncDataBackendResp;
};

export type BackendReqPayload<TReqType extends BackendReqType> =
  BackendReqPayloadDict[TReqType];
export type BackendRespPayload<TReqType extends BackendReqType> =
  BackendRespPayloadDict[TReqType];
export type BackendReq<TReqType extends BackendReqType = BackendReqType> = {
  type: TReqType;
  payload: BackendReqPayload<TReqType>;
};
export type BackendResp<TReqType extends BackendReqType = BackendReqType> = {
  type: TReqType;
  payload: BackendRespPayload<TReqType>;
};

/**
 * Interface to wrap all Django interaction functionality.
 * All Django interface calls should be wrapped up in here
 * so that we can swap them out in Django-less mode.
 */
export interface BackendInterface {
  /** Should only return anything for mock backends. */
  getDefaultTeamId: () => string | null;
  sendReqAsync: <TReqType extends BackendReqType>(
    req: BackendReq<TReqType>
  ) => Promise<BackendResp<TReqType>>;
}
