import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeUpdatePayload } from "sockapi/scope-updates";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import {
  TeamOliveGridState,
  OLIVE_CONFIG,
} from "sockapi/scope-specs/team-olive-spec";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { BackendScopeState } from "sockapi/backend-scope-state";
import { RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";
import { TeamController } from "sock-server/server-controllers/team-controller";
import {
  OLIVE_GRID1,
  OLIVE_GRID2,
} from "sock-server/static-data/olive-SPOILERS";

class TeamOliveEngine {
  ctx: ScopeEngineContext<ScopeType.TEAM_OLIVE>;
  teamController: TeamController;
  state: TeamOliveGridState;

  constructor(
    ctx: ScopeEngineContext<ScopeType.TEAM_OLIVE>,
    teamController: TeamController,
    initState?: BackendScopeState<ScopeType.TEAM_OLIVE>
  ) {
    this.ctx = ctx;
    this.teamController = teamController;
    this.state = {
      grid1: new Array(OLIVE_CONFIG.grid1Length).fill(null),
      grid2: new Array(OLIVE_CONFIG.grid2Width * OLIVE_CONFIG.grid2Height).fill(
        null
      ),
    };
    for (const index of initState?.grid1Reveals ?? []) {
      this.state.grid1[Number(index)] = OLIVE_GRID1[index];
    }
    for (const index of initState?.grid2Reveals ?? []) {
      this.state.grid2[Number(index)] = OLIVE_GRID2[index];
    }
  }

  getPermissions(conn: ClientConnection) {
    return conn.teamId === this.ctx.scope.teamId
      ? ScopePermissions.FULL
      : ScopePermissions.ADMIN_ONLY;
  }

  getState() {
    const numOliveWishes =
      this.teamController.progressTracker.view.getNumOliveWishes();
    return {
      ...this.state,
      numWishes: numOliveWishes,
      numTotalGrid1Tokens: Math.min(
        6 + 3 * numOliveWishes,
        OLIVE_CONFIG.grid1Length
      ),
      numTotalGrid2Tokens: Math.min(
        40 + 40 * numOliveWishes,
        OLIVE_CONFIG.grid2Width * OLIVE_CONFIG.grid2Height
      ),
    };
  }

  getSlowSyncState() {
    return {
      grid1Reveals: Object.entries(this.state.grid1)
        .filter(([_, val]) => val !== null)
        .map(([k, v]) => Number(k)),
      grid2Reveals: Object.entries(this.state.grid2)
        .filter(([_, val]) => val !== null)
        .map(([k, v]) => Number(k)),
    };
  }

  handleTeamProgressUpdate() {
    const { numWishes, numTotalGrid1Tokens, numTotalGrid2Tokens } =
      this.getState();
    this.ctx.update(this.ctx.scope, {
      numWishes,
      numTotalGrid1Tokens,
      numTotalGrid2Tokens,
    });
  }

  // Returns whether a grid reveal step is valid.
  validateReveal(
    reveal: number,
    grid: (string | null)[],
    numTotalTokens: number
  ) {
    if (!Number.isInteger(reveal) || reveal < 0 || reveal >= grid.length)
      return false;
    if (grid[reveal] !== null) return false; // already revealed

    // need a token to reveal (non null reveals < total tokens)
    const numReveals = grid.filter((v) => v !== null).length;
    return numReveals < numTotalTokens;
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.TEAM_OLIVE>
  ) {
    const { revealGrid1, revealGrid2 } = payload;

    const updatePayload: SockAPIScopeUpdatePayload<ScopeType.TEAM_OLIVE> = {};
    const state = this.getState();
    const puzName = OLIVE_CONFIG.puzName;

    // Fail silently as this may be due to race conditions.
    // Don't check for unsolved to allow playing with the interface
    // after solving.
    if (!reqCtx.conn.isAdmin && !this.teamController.isPuzzleUnlocked(puzName))
      return reqCtx.sendStepResp(this.ctx.scope, "");

    if (
      revealGrid1 !== undefined &&
      this.validateReveal(
        revealGrid1,
        this.state.grid1,
        state.numTotalGrid1Tokens
      )
    ) {
      this.state.grid1[revealGrid1] = OLIVE_GRID1[revealGrid1];
      updatePayload.setGrid1 = {
        [revealGrid1]: OLIVE_GRID1[revealGrid1],
      };
    }
    if (
      revealGrid2 !== undefined &&
      this.validateReveal(
        revealGrid2,
        this.state.grid2,
        state.numTotalGrid2Tokens
      )
    ) {
      this.state.grid2[revealGrid2] = OLIVE_GRID2[revealGrid2];
      updatePayload.setGrid2 = {
        [revealGrid2]: OLIVE_GRID2[revealGrid2],
      };
    }
    this.ctx.update(this.ctx.scope, updatePayload);
    return reqCtx.sendStepResp(this.ctx.scope, "");
  }
}

export type TeamOliveServerScopeSpec = {
  engine: TeamOliveEngine;
};

export const teamOliveServerScopeHooks: ServerScopeHooks<ScopeType.TEAM_OLIVE> =
  {
    createEngine: ({ execCtx, engineCtx, initState }) => {
      const { controller, scope } = engineCtx;
      const teamController = controller.getTeamControllerIfExists(scope.teamId);
      if (teamController === null)
        return execCtx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_TEAM, null);
      return new TeamOliveEngine(engineCtx, teamController, initState);
    },
    engineIdentityFunc: (engine) => engine,
  };
