import { jsonDeepCopy } from "util/util";
import { ScopeType } from "sockapi/scope-types";
import { Scope } from "sockapi/scopes";
import { ErratumContent } from "sockapi/scope-specs/erratum-content-spec";
import { HintResponseType } from "sockapi/scope-specs/hint-spec";
import {
  TeamBackendState,
  SavedHuntBackendState,
} from "sockapi/scope-specs/state-download-spec";
import { Puzzle105StatusType } from "sockapi/scope-specs/team-puzzle-105-spec";
import { applyTeamProgressUpdateNoTrigger } from "sockapi/team-progress-updates";
import { CustomPuzzles } from "sockapi/puzzle-data";
import { MockTeamOpts } from "sockapi/server-config";
import { BackendScopeStateEntry } from "sockapi/backend-scope-state";
import { makeCustomPuzzleData } from "sock-server/scope-specs/team-puzzle-105-server-spec";
import {
  BackendNotification,
  BackendInterface,
  BackendReqType,
  BackendReqPayload,
  BackendRespPayload,
  BackendReq,
  BackendResp,
} from "sock-server/backend-interface/BackendInterface";
import {
  TeamProgressEventType,
  TeamProgressEventReasonType,
} from "sockapi/team-progress";
import TeamProgressTracker from "sock-server/team-progress/TeamProgressTracker";
import { SockServerClock } from "sock-server/SockServer";
import { PuzzlesDB } from "sock-server/puzzles/PuzzlesDB";

/**
 * Version number of the mock DB schema.
 * The mock database may be saved to persistent storage periodically.
 * Every time the schema (the below type declarations) is modified,
 * the version number must be incremented to invalidate the old mock
 * database. This can also be used to invalidate the entire DB in
 * general during development.
 * If this is incremented posthunt, make sure to add migration functions
 * since teams may want to upload state downloaded in previous versions.
 */
export const SCHEMA_VERSION = 93;

export const DEFAULT_BOOTSTRAP_TEAM_ID = "TEST_TEAM";
const MAX_GUESSES_PER_PUZZLE = 20;

export type MockDB = {
  version: number;
  nextSockServerId: number;
  defaultTeamId: string;
  teams: { [teamId: string]: TeamBackendState };
  scopeState: { [scopeId: string]: BackendScopeStateEntry };
  errata: { [erratumId: string]: ErratumContent };
  notifs: BackendNotification[];
  /**
   * Set of puzNames known to the database.
   * This allows us to automatically invalidate when new puzzles are added.
   */
  puzzles: { [puzName: string]: true };
};

type BootstrapDBOpts = {
  bootstrapTeam?: MockTeamOpts;
  /** Create initial unlocks. */
  initUnlocks?: boolean;
  /**
   * The default bootstrap backend state is now the postsolve initial
   * state. Setting isDev recovers the old dev bootstrap state.
   */
  isDev?: boolean;
  initState?: SavedHuntBackendState;
};

const makeFreshTeamProgress = (
  puzzlesDB: PuzzlesDB,
  customPuzzles?: CustomPuzzles
) => {
  // This should only be used for bootstrap, which means
  // no custom puzzles, and no need to worry about hunt end
  // time for the leaderboard.
  const progressTracker = new TeamProgressTracker(
    () => puzzlesDB,
    () => customPuzzles ?? {},
    () => ({}),
    new SockServerClock(),
    0
  );
  return progressTracker;
};

const makeBootstrapDB = (
  puzzlesDB: PuzzlesDB,
  opts: BootstrapDBOpts
): MockDB => {
  const {
    bootstrapTeam = {
      teamId: DEFAULT_BOOTSTRAP_TEAM_ID,
      displayName: "Dev Test Team",
    },
    initUnlocks = false,
    isDev = false,
    initState,
  } = opts;
  const timeNow = Date.now();

  if (initState !== undefined && initState.version !== SCHEMA_VERSION) {
    // Add migration here if necessary.
    throw new Error(
      `Unable to migrate state from version ${initState.version} to ${SCHEMA_VERSION}`
    );
  }

  const teams: { [teamId: string]: TeamBackendState } = initState?.teams ?? {};
  if (initUnlocks) {
    const customPuzzles: CustomPuzzles = {};
    // HACK: Create a skeleton custom puzzles dictionary just for now to unlock
    // all custom puzzles by default.
    for (const entry of Object.values(initState?.scopeState ?? {})) {
      const isPuz105Entry = (
        entry: BackendScopeStateEntry
      ): entry is BackendScopeStateEntry<Scope<ScopeType.TEAM_PUZZLE_105>> =>
        entry.scope.type === ScopeType.TEAM_PUZZLE_105;
      if (!isPuz105Entry(entry)) continue;
      const { scope, state } = entry;
      if (scope.type !== ScopeType.TEAM_PUZZLE_105) continue;
      const { status } = state;
      if (status === undefined) continue;
      if (status.type !== Puzzle105StatusType.ACCEPTED) continue;
      if (!(status.isAvailableInGacha ?? false)) continue;
      const puzData = makeCustomPuzzleData(
        scope.teamId,
        state.formFields.title,
        state.formFields.answer,
        status,
        "",
        ""
      );
      customPuzzles[puzData.puzName] = puzData;
    }
    const bootstrapTeamProgressTracker = makeFreshTeamProgress(
      puzzlesDB,
      customPuzzles
    );
    bootstrapTeamProgressTracker.makeBootstrapProgress({
      unlockAllPuzzles: initUnlocks,
    });
    if (isDev)
      bootstrapTeamProgressTracker.addEvent(
        TeamProgressEventType.GET_HINT_TOKEN,
        { num: 5, reason: { type: TeamProgressEventReasonType.MISC } }
      );

    teams[bootstrapTeam.teamId] = {
      displayName: bootstrapTeam.displayName,
      isInactive: false,
      isHidden: false,
      members: ["team member 1", "team member 2"],
      viewTimes: {},
      hints: isDev
        ? {
            ["test_hint"]: {
              teamId: bootstrapTeam.teamId,
              puzName: "puz1",
              request: {
                content: "this is an example hint for testing",
                timestamp: timeNow,
              },
              response: {
                type: HintResponseType.REFUNDED,
                content: "this is an example hint response for testing",
                timestamp: timeNow + 1000,
              },
            },
            ["test_hint_2"]: {
              teamId: bootstrapTeam.teamId,
              puzName: "puz1",
              request: {
                content:
                  "this is a very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very, very long example hint for testing",
                timestamp: timeNow + 10000,
                isFollowup: true,
              },
              response: {
                type: HintResponseType.REFUNDED,
                content: "this is a refund just to make the hint count zero",
                timestamp: timeNow + 20000,
              },
            },
          }
        : {},
      teamProgress: bootstrapTeamProgressTracker.state,
    };
  }

  return {
    version: SCHEMA_VERSION,
    // In dev, we reset the db often, so use the current time
    // to ensure that it's unique between resets.
    nextSockServerId: Date.now(),
    defaultTeamId: initState?.defaultTeamId ?? bootstrapTeam.teamId,
    teams,
    scopeState: initState?.scopeState ?? {},
    errata:
      isDev && initState === undefined
        ? {
            "0": {
              puzName: "puz1",
              updatesText:
                "we did a thing on $PUZZLE. this is test erratum 1, it won't show in prod",
              puzzleText:
                "we did a thing. this is test erratum 1, it won't show in prod",
              timestamp: timeNow,
              isPublished: true,
            },
            "1": {
              puzName: null,
              updatesText:
                "we did a thing. this is test erratum 2, it won't show in prod; this should only be displayed on the errata page",
              puzzleText: "",
              timestamp: timeNow + 1000,
              isPublished: true,
            },
            "2": {
              puzName: "puz1",
              updatesText:
                "we did a thing on $PUZZLE. this is test erratum 3, it won't show in prod; this should only be displayed for admin",
              puzzleText:
                "we did a thing. this is test erratum 3, it won't show in prod; this should only be displayed for admin",
              timestamp: timeNow + 2000,
              isPublished: false,
            },
            "3": {
              puzName: "puz1",
              updatesText:
                "we did a thing on $PUZZLE. this is test erratum 4, it won't show in prod; this should only be displayed on the errata page",
              puzzleText: "",
              timestamp: timeNow + 3000,
              isPublished: true,
            },
            "4": {
              puzName: "puz1",
              updatesText: "",
              puzzleText:
                "we did a thing. this is test erratum 5, it won't show in prod; this should only be displayed on the puzzle page",
              timestamp: timeNow + 4000,
              isPublished: true,
            },
          }
        : {},
    notifs: [],
    puzzles: Object.fromEntries(
      Array.from(puzzlesDB.getPuzNames({}), (puzName) => [puzName, true])
    ),
  };
};

export class MockBackendInterface implements BackendInterface {
  puzzlesDB: PuzzlesDB;
  saveDbFunc: (db: MockDB) => Promise<void>;
  db: MockDB;
  /** If an update was made to the database, marks it for saving. */
  isDirty: boolean;
  /**
   * Whether there is already a job running to save the mock DB
   * to persistent storage.
   */
  isSaving: boolean;

  constructor(
    puzzlesDB: PuzzlesDB,
    savedDB: MockDB | null,
    saveDbFunc: (db: MockDB) => Promise<void>,
    bootstrapDBOpts?: BootstrapDBOpts
  ) {
    this.puzzlesDB = puzzlesDB;
    this.saveDbFunc = saveDbFunc;

    this.db =
      savedDB === null ||
      savedDB.version < SCHEMA_VERSION ||
      ((): boolean => {
        // Compare the puzzles known in the savedDB with the
        // current full set of puzzles.
        for (const puzName of Object.keys(savedDB.puzzles)) {
          if (puzzlesDB.tryGetPuzzle(puzName, {}) === null) return true;
        }
        for (const puzName of puzzlesDB.getPuzNames({})) {
          if (!(savedDB.puzzles[puzName] ?? false)) return true;
        }
        return false;
      })()
        ? makeBootstrapDB(puzzlesDB, bootstrapDBOpts ?? {})
        : savedDB;

    this.isDirty = false;
    this.isSaving = false;
  }

  private genSockServerId() {
    const sockServerId = `sockServer_${this.db.nextSockServerId}`;
    this.db.nextSockServerId++;
    this.isDirty = true;
    return sockServerId;
  }

  async getInitData(): Promise<
    BackendRespPayload<BackendReqType.GET_INIT_DATA>
  > {
    return {
      sockServerId: this.genSockServerId(),
      teams: Object.fromEntries(
        await Promise.all(
          Object.keys(this.db.teams).map((teamId) =>
            (async () => [teamId, await this.getInitTeamData(teamId)])()
          )
        )
      ),
      maxGuessesPerPuzzle: MAX_GUESSES_PER_PUZZLE,
      scopeState: Object.fromEntries(
        Object.entries(this.db.scopeState).filter(
          ([scopeId, { isArchived }]) => !(isArchived ?? false)
        )
      ),
      huntStartTime: Date.UTC(2024, 8, 20, 22, 28),
      huntEndTime: Date.UTC(2024, 8, 30, 22, 28),
      huntCloseTime: Date.UTC(2024, 9, 21, 22, 28),
      notifsNextIndex: this.db.notifs.length,
      errata: this.db.errata,
    };
  }

  async getInitTeamData(
    teamId: string
  ): Promise<BackendRespPayload<BackendReqType.GET_INIT_TEAM_DATA>> {
    const existingTeamData = this.db.teams[teamId];
    if (existingTeamData === undefined) {
      // Dev only, to allow solvers to arbitrarily create new teams.
      // Do not implement this in Django.
      console.log(`creating new team ${teamId}`);
      this.db.teams[teamId] = {
        displayName: teamId,
        isInactive: false,
        isHidden: false,
        members: [],
        viewTimes: {},
        hints: {},
        teamProgress: makeFreshTeamProgress(this.puzzlesDB).state,
      };
    }

    const {
      displayName,
      isInactive,
      isHidden,
      members,
      viewTimes,
      hints,
      teamProgress,
    } = this.db.teams[teamId];

    return {
      userId: teamId,
      displayName,
      isInactive,
      isHidden,
      creationTime: 0,
      members,
      views: Object.keys(viewTimes),
      hints,
      teamProgress,
    };
  }

  private getTeamData(teamId: string): TeamBackendState {
    if (this.db.teams[teamId] === undefined)
      throw new Error(`unknown team ${teamId}`);
    return this.db.teams[teamId];
  }

  async syncData(req: BackendReqPayload<BackendReqType.SYNC_DATA>) {
    const { teamState, scopeState } = req;
    for (const [teamId, teamUpdates] of Object.entries(teamState ?? {})) {
      const {
        isInactive,
        isHidden,
        displayName,
        viewTimes,
        teamProgressUpd,
        hints,
      } = teamUpdates;
      const teamData = this.getTeamData(teamId);
      if (isInactive !== undefined) {
        teamData.isInactive = isInactive;
        this.isDirty = true;
      }
      if (isHidden !== undefined) {
        teamData.isHidden = isHidden;
        this.isDirty = true;
      }
      if (displayName !== undefined) {
        teamData.displayName = displayName;
        this.isDirty = true;
      }
      for (const [puzName, viewTime] of Object.entries(viewTimes ?? {})) {
        teamData.viewTimes[puzName] = viewTime;
        this.isDirty = true;
      }
      if (teamProgressUpd !== undefined) {
        applyTeamProgressUpdateNoTrigger(
          teamData.teamProgress,
          teamProgressUpd
        );
        this.isDirty = true;
      }
      for (const [hintId, hint] of Object.entries(hints ?? {})) {
        teamData.hints[hintId] = hint;
        this.isDirty = true;
      }
    }
    for (const [scopeId, entry] of Object.entries(scopeState ?? {})) {
      this.db.scopeState[scopeId] = entry;
      this.isDirty = true;
    }

    if (this.isDirty && !this.isSaving) {
      this.isDirty = false;
      this.isSaving = true;
      await this.saveDbFunc(this.db).finally(() => {
        this.isSaving = false;
      });
    }

    return {};
  }

  async sendReqAsyncNoCopy<TReqType extends BackendReqType>(
    req: BackendReq<TReqType>
  ): Promise<BackendResp<TReqType>> {
    const handlers: {
      [TReqType in BackendReqType]: (
        req: BackendReqPayload<TReqType>
      ) => Promise<BackendRespPayload<TReqType>>;
    } = {
      [BackendReqType.GET_INIT_DATA]: (payload) => this.getInitData(),
      [BackendReqType.GET_INIT_TEAM_DATA]: (payload) =>
        this.getInitTeamData(payload.teamId),
      [BackendReqType.SYNC_DATA]: (payload) => this.syncData(payload),
    };
    const reqType: TReqType = req.type;
    return {
      type: reqType,
      payload: await handlers[reqType](req.payload),
    };
  }

  getDefaultTeamId(): string | null {
    return this.db.defaultTeamId;
  }

  async sendReqAsync<TReqType extends BackendReqType>(
    req: BackendReq<TReqType>
  ): Promise<BackendResp<TReqType>> {
    return jsonDeepCopy(await this.sendReqAsyncNoCopy(req));
  }
}
