import allPuzzlesJson from "sock-server/static-data/all-puzzles.json?raw";
import allPuzzleContentJson from "sock-server/static-data/all-puzzle-content.json?raw";
import puzzleMessagesJson from "sock-server/static-data/puzzle-messages.json?raw";
import cannedHintsJson from "sock-server/static-data/canned-hints.json?raw";
import customPuzzleCannedHintsJson from "sock-server/static-data/custom-puzzle-canned-hints.json?raw";
import keyframesJson from "sock-server/static-data/keyframes.json?raw";
import collectiblesJson from "sock-server/static-data/collectibles.json?raw";

import {
  AllPuzzles,
  AllPuzzleContent,
  PuzzleMessagesData,
  CannedHints,
  KeyframesData,
  CollectiblesData,
} from "sockapi/puzzle-data";
import { PuzzlesDB } from "sock-server/puzzles/PuzzlesDB";

export const PUZZLES_DB = new PuzzlesDB({
  allPuzzles: JSON.parse(allPuzzlesJson) as AllPuzzles,
  allPuzzleContent: JSON.parse(allPuzzleContentJson) as AllPuzzleContent,
  puzzleMessages: JSON.parse(puzzleMessagesJson) as PuzzleMessagesData,
  cannedHints: {
    ...(JSON.parse(cannedHintsJson) as CannedHints),
    ...(JSON.parse(customPuzzleCannedHintsJson) as CannedHints),
  },
  keyframesData: JSON.parse(keyframesJson) as KeyframesData,
  collectiblesData: JSON.parse(collectiblesJson) as CollectiblesData,
});
