import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import {
  ErratumContent,
  ErratumSummary,
} from "sockapi/scope-specs/erratum-content-spec";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { ClientConnection } from "sock-server/client-connections";

class ErratumContentEngine {
  ctx: ScopeEngineContext<ScopeType.ERRATUM_CONTENT>;
  state: SockAPIScopeState<ScopeType.ERRATUM_CONTENT>;

  constructor(
    ctx: ScopeEngineContext<ScopeType.ERRATUM_CONTENT>,
    initState: SockAPIScopeState<ScopeType.ERRATUM_CONTENT>
  ) {
    this.ctx = ctx;
    this.state = initState;
  }

  getPermissions(conn: ClientConnection) {
    return this.isVisibleToTeam(conn.teamId)
      ? ScopePermissions.READ
      : ScopePermissions.ADMIN_ONLY;
  }

  getState() {
    return this.state;
  }

  isVisibleToTeam(teamId: string): boolean {
    return (
      (this.state.puzName === null ||
        this.ctx.server
          .getTeamController(teamId)
          .isPuzzleUnlocked(this.state.puzName)) &&
      this.state.isPublished
    );
  }

  getSummary(): ErratumSummary {
    const { puzName, timestamp } = this.state;
    return { puzName, timestamp };
  }

  overrideContent(newContent: ErratumContent): void {
    this.state = newContent;
    this.ctx.assign(this.ctx.scope, this.state);
  }
}

export type ErratumContentServerScopeSpec = {
  engine: ErratumContentEngine;
};

export const erratumContentServerScopeHooks: ServerScopeHooks<ScopeType.ERRATUM_CONTENT> =
  {
    createEngine: ({ execCtx, engineCtx, initState }) => {
      if (initState === undefined)
        // We should not be automatically creating erratum content scopes.
        return execCtx.setErrCodeAndReturn(
          SockAPIErrorCode.PERMISSION_DENIED,
          null
        );
      return new ErratumContentEngine(engineCtx, initState);
    },
    engineIdentityFunc: (engine) => engine,
  };
