import { SetMap, MapMap } from "util/util";
import { SockAPIErrorCode } from "sockapi/errors";
import { Scope, scopeToString } from "sockapi/scopes";
import { ClientConnection } from "sock-server/client-connections";
import { RequestContext } from "sock-server/exec-contexts";

export class SubscriptionsTracker {
  /** Map from scope ID to a connID-conn map of subscribed connections. */
  subscriptionsByScope: MapMap<string, string, ClientConnection>;
  /**
   * For each conn ID, the scope IDs that the connection is
   * subscribed to.
   */
  subscriptionsByConn: SetMap<string, string>;

  constructor() {
    this.subscriptionsByScope = new MapMap();
    this.subscriptionsByConn = new SetMap();
  }

  /** Checks if a connection is subscribed to a scope. */
  isSubscribed(conn: ClientConnection, scope: Scope): boolean {
    const scopeId = scopeToString(scope);
    return this.subscriptionsByConn.has(conn.connId, scopeId);
  }

  /**
   * Subscribes a connection to a scope.
   * This assumes that the connection is not already subscribed
   * to the scope.
   */
  subscribe(ctx: RequestContext, scope: Scope): boolean {
    const { conn } = ctx;
    const scopeId = scopeToString(scope);
    if (!this.subscriptionsByConn.add(conn.connId, scopeId))
      throw new Error(`connection is already subscribed to scope ${scopeId}`);
    if (!this.subscriptionsByScope.setIfAbsent(scopeId, conn.connId, conn))
      throw new Error("subscription tracking structures not in sync");
    return true;
  }

  /**
   * Unsubscribe a connection from a scope.
   * Returns an error if the connection is not subscribed to the scope.
   */
  unsubscribe(ctx: RequestContext, scope: Scope): boolean {
    const { conn } = ctx;
    const scopeId = scopeToString(scope);
    if (!this.subscriptionsByConn.delete(conn.connId, scopeId))
      return ctx.setErrCodeAndReturn(SockAPIErrorCode.NOT_SUBSCRIBED, false);
    if (!this.subscriptionsByScope.delete(scopeId, conn.connId))
      throw new Error("subscription tracking structures not in sync");
    return true;
  }

  /** Unsubscribe a connection from all scopes. */
  clearConnSubscriptions(conn: ClientConnection): void {
    for (const scopeId of this.subscriptionsByConn.l1Get(conn.connId)) {
      this.subscriptionsByScope.delete(scopeId, conn.connId);
    }
    this.subscriptionsByConn.l1Delete(conn.connId);
  }

  /**
   * Delete a scope that is no longer valid, and unsubscribe
   * all connections.
   */
  deleteScope(scope: Scope) {
    const scopeId = scopeToString(scope);
    for (const [connId, conn] of this.subscriptionsByScope.l1Get(scopeId)) {
      this.subscriptionsByConn.delete(connId, scopeId);
    }
    this.subscriptionsByScope.l1Delete(scopeId);
  }

  getSubscribedConns(scope: Scope) {
    const scopeId = scopeToString(scope);
    return this.subscriptionsByScope.l1Get(scopeId);
  }
}
