import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import {
  Puzzle105SharePermission,
  Puzzle105StatusType,
} from "sockapi/scope-specs/team-puzzle-105-spec";
import {
  Puzzle139SharePermission,
  Puzzle139StatusType,
} from "sockapi/scope-specs/team-puzzle-139-spec";
import { PublicSubmissionType } from "sockapi/scope-specs/public-submissions-spec";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { ClientConnection } from "sock-server/client-connections";

class PublicSubmissionsEngine {
  ctx: ScopeEngineContext<ScopeType.PUBLIC_SUBMISSIONS>;
  cachedState?: SockAPIScopeState<ScopeType.PUBLIC_SUBMISSIONS>;

  constructor(ctx: ScopeEngineContext<ScopeType.PUBLIC_SUBMISSIONS>) {
    this.ctx = ctx;
  }

  getPermissions(conn: ClientConnection): ScopePermissions {
    return ScopePermissions.FULL;
  }

  private getStateNoCache(): SockAPIScopeState<ScopeType.PUBLIC_SUBMISSIONS> {
    const { controller, server } = this.ctx;
    const { huntEndTime } = server;
    const state: SockAPIScopeState<ScopeType.PUBLIC_SUBMISSIONS> = {
      submissions: {},
    };
    for (const engine of Object.values(
      controller.getEnginesForType(ScopeType.TEAM_PUZZLE_105)
    )) {
      const { teamId } = engine.ctx.scope;
      const {
        status,
        formFields: { title, sharePermission },
      } = engine.state;
      if (
        status !== undefined &&
        status.type === Puzzle105StatusType.ACCEPTED &&
        status.reviewTime <= huntEndTime &&
        sharePermission !== null
      ) {
        const submissionId = status.reviewTime.toString();
        const displayName = title.trim();
        const submissionInfo = {
          teamId,
          respondTime: status.reviewTime,
          displayName: displayName !== "" ? displayName : undefined,
        };
        if (status.isAvailableInGacha ?? false)
          state.submissions[submissionId] = {
            type: PublicSubmissionType.PUBLIC_CUSTOM_PUZZLE,
            ...submissionInfo,
          };
        else if (sharePermission !== Puzzle105SharePermission.NONE)
          state.submissions[submissionId] = {
            type: PublicSubmissionType.PRIVATE_CUSTOM_PUZZLE,
            ...submissionInfo,
          };
      }
    }
    for (const engine of Object.values(
      controller.getEnginesForType(ScopeType.TEAM_PUZZLE_139)
    )) {
      const { teamId } = engine.ctx.scope;
      const { status, sharePermission } = engine.state;
      if (
        status !== undefined &&
        status.type === Puzzle139StatusType.ACCEPTED &&
        status.reviewTime <= huntEndTime &&
        sharePermission !== undefined
      ) {
        const submissionId = status.reviewTime.toString();
        if (sharePermission === Puzzle139SharePermission.BOTH)
          state.submissions[submissionId] = {
            type: PublicSubmissionType.HUG_A_TREE,
            teamId,
            respondTime: status.reviewTime,
          };
        else if (sharePermission === Puzzle139SharePermission.FACT)
          state.submissions[submissionId] = {
            type: PublicSubmissionType.HUG_A_TREE_FACT_ONLY,
            teamId,
            respondTime: status.reviewTime,
          };
      }
    }
    return state;
  }

  getState(): SockAPIScopeState<ScopeType.PUBLIC_SUBMISSIONS> {
    // We only ever need to compute this once, since
    // we'll only run this after all submissions are
    // finalized after hunt ends.
    this.cachedState ??= this.getStateNoCache();
    return this.cachedState;
  }
}

export type PublicSubmissionsServerScopeSpec = {
  engine: PublicSubmissionsEngine;
};

export const publicSubmissionsServerScopeHooks: ServerScopeHooks<ScopeType.PUBLIC_SUBMISSIONS> =
  {
    createEngine: ({ engineCtx }) => {
      return new PublicSubmissionsEngine(engineCtx);
    },
    engineIdentityFunc: (engine) => engine,
  };
