import { mergeTeamProgressUpdate } from "sockapi/team-progress-updates";
import TeamProgressTracker from "sock-server/team-progress/TeamProgressTracker";

import {
  BackendFastSyncReqPacket,
  SyncTracker,
  SyncUpdatesTracker,
} from "sock-server/backend-interface/backend-sync";

/**
 * These can be different in principle if more state is required
 * to support merging updates.
 */
export type FastSyncUpdate = BackendFastSyncReqPacket;

const mergeFastSyncUpdate = (
  upd1: FastSyncUpdate,
  upd2: FastSyncUpdate
): void => {
  if (upd2.teamProgressUpd !== undefined) {
    if (upd1.teamProgressUpd === undefined)
      upd1.teamProgressUpd = upd2.teamProgressUpd;
    else mergeTeamProgressUpdate(upd1.teamProgressUpd, upd2.teamProgressUpd);
  }
};

export const fastSyncUpdateToSyncPacket = (
  upd: FastSyncUpdate,
  teamId: string
): BackendFastSyncReqPacket => {
  return upd;
};

export class FastSyncTracker {
  teamId: string;
  syncUpdatesTracker: SyncUpdatesTracker<FastSyncUpdate>;

  /**
   * Team progress tracker with most up-to-date progress that
   * may not yet be persisted to the backend.
   */
  teamProgressTracker: TeamProgressTracker;

  /**
   * This should be initialized with the initial team progress.
   * We make a clone here to separately track progress that
   * may not yet be persisted to the backend.
   */
  constructor(
    teamId: string,
    syncTracker: SyncTracker,
    teamProgressTracker: TeamProgressTracker
  ) {
    this.teamId = teamId;
    this.syncUpdatesTracker = new SyncUpdatesTracker(
      syncTracker,
      () => ({}),
      (upd1, upd2) => mergeFastSyncUpdate(upd1, upd2)
    );
    this.teamProgressTracker = teamProgressTracker.clone();
  }

  mergeUpdate({ teamProgressUpd, ...upd }: FastSyncUpdate): void {
    // Complete any requested progress updates.
    if (teamProgressUpd !== undefined)
      this.modifyTeamProgress((teamProgressTracker) =>
        this.teamProgressTracker.applyUpdate(teamProgressUpd)
      );

    // Merge the other updates.
    this.syncUpdatesTracker.mergeUpdate(upd);
  }

  modifyTeamProgress<TRet = void>(
    modifyFunc: (progressTracker: TeamProgressTracker) => TRet
  ): TRet {
    const ret = modifyFunc(this.teamProgressTracker);
    const teamProgressUpd = this.teamProgressTracker.triggerUpdatesAndFlush();
    if (teamProgressUpd !== null)
      this.syncUpdatesTracker.mergeUpdate({ teamProgressUpd });
    return ret;
  }

  /**
   * Starts a sync, and returns the updates to be synced
   * as a sync packet.
   *
   * Returns null if there is nothing to sync. In this case, a sync
   * is not considered to have started.
   */
  startSync(): FastSyncUpdate | null {
    return this.syncUpdatesTracker.startSync();
  }

  /**
   * If the sync succeeded, the tracker clears the synced updates
   * from future syncs.
   */
  endSync(didSyncSucceed: boolean): void {
    this.syncUpdatesTracker.endSync(didSyncSucceed);
  }
}
