import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import { HOPE_POOL_CONFIG } from "sockapi/scope-specs/hope-pool-spec";
import { actualHope } from "sockapi/gacha/hope";
import {
  TeamProgressEventType,
  TeamProgressEventReasonType,
} from "sockapi/team-progress";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";
import { TeamController } from "sock-server/server-controllers/team-controller";

export type TeamHopePoolCycleContribution = {
  cycleId: number;
  amt: number;
};

export const getMergedTeamHopePoolCycleContributions = (
  contrib1: TeamHopePoolCycleContribution | undefined,
  contrib2: TeamHopePoolCycleContribution
): TeamHopePoolCycleContribution => {
  const { cycleId } = contrib2;
  if (contrib1 === undefined) return contrib2;
  if (contrib1.cycleId !== cycleId) return contrib2;
  return {
    cycleId,
    amt: contrib1.amt + contrib2.amt,
  };
};

class TeamHopePoolEngine {
  ctx: ScopeEngineContext<ScopeType.TEAM_HOPE_POOL>;
  teamController: TeamController;

  constructor(
    ctx: ScopeEngineContext<ScopeType.TEAM_HOPE_POOL>,
    teamController: TeamController
  ) {
    this.ctx = ctx;
    this.teamController = teamController;
  }

  getPermissions(conn: ClientConnection): ScopePermissions {
    return conn.teamId === this.ctx.scope.teamId
      ? ScopePermissions.FULL
      : ScopePermissions.ADMIN_ONLY;
  }

  getState(): SockAPIScopeState<ScopeType.TEAM_HOPE_POOL> {
    const cycleContribution = (() => {
      const { hopePoolLatestCycleContribution } =
        this.teamController.progressTracker.view;
      if (hopePoolLatestCycleContribution === undefined) return 0;
      const { cycleId, amt } = hopePoolLatestCycleContribution;
      const hopePoolEngine = this.ctx.server.getOrCreateEngineOrThrow({
        type: ScopeType.HOPE_POOL,
      });
      if (hopePoolEngine.cycleId !== cycleId) return 0;
      return amt;
    })();
    const hopeFromSolve =
      this.teamController.progressTracker.view.puzzleSolves.get(
        HOPE_POOL_CONFIG.puzName
      )?.info.hopeGained;
    return { cycleContribution, hopeFromSolve };
  }

  broadcastAssign() {
    this.ctx.assign(this.ctx.scope, this.getState());
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.TEAM_HOPE_POOL>
  ) {
    const { contribute, claim } = payload;
    const puzName = HOPE_POOL_CONFIG.puzName;

    // Fail silently as it may be due to race conditions.
    if (!reqCtx.conn.isAdmin && !this.teamController.isPuzzleUnlocked(puzName))
      return reqCtx.sendStepResp(this.ctx.scope, "");

    const hopePoolEngine = this.ctx.server.getOrCreateEngineOrThrow({
      type: ScopeType.HOPE_POOL,
    });

    if (contribute !== undefined) {
      const amt = contribute;
      if (!(Number.isInteger(amt) && amt === HOPE_POOL_CONFIG.contributionAmt))
        return reqCtx.setBadRequest();

      const success = await this.teamController.modifyTeamProgressAsync(
        (progressTracker) => {
          const currHope = actualHope(
            progressTracker.getTotalHope(),
            this.ctx.server.clock.genTimestamp()
          );
          // Fail silently as it may be due to race conditions.
          if (currHope < HOPE_POOL_CONFIG.contributionAmt) {
            reqCtx.sendStepResp(this.ctx.scope, "");
            return false;
          }

          const { cycleId } = hopePoolEngine;
          progressTracker.addEvent(TeamProgressEventType.GET_HOPE, {
            hopeAmt: -amt,
            reason: {
              type: TeamProgressEventReasonType.HOPE_POOL,
              cycleId,
            },
          });
          return true;
        }
      );
      if (!success) return;

      hopePoolEngine.addHope(amt);
      this.broadcastAssign();
      return reqCtx.sendStepResp(this.ctx.scope, "");
    }

    if (claim ?? false) {
      const { poolAmt } = hopePoolEngine.state;
      // Fail silently as it may be due to race conditions.
      if (
        this.getState().cycleContribution !== 0 ||
        poolAmt < HOPE_POOL_CONFIG.minToClaim ||
        this.teamController.isPuzzleSolvedOrPendingSolved(puzName)
      )
        return reqCtx.sendStepResp(this.ctx.scope, "");

      hopePoolEngine.advanceCycle();

      await this.teamController.modifyTeamProgressAsync((progressTracker) => {
        progressTracker.addSolveAttempt(
          puzName,
          { isCorrect: true },
          poolAmt * HOPE_POOL_CONFIG.claimRewardMultiplier
        );
      });

      // Broadcast the state again to inform clients of the hope gained.
      this.broadcastAssign();
      return reqCtx.sendStepResp(this.ctx.scope, "");
    }

    return reqCtx.sendStepResp(this.ctx.scope, "");
  }
}

export type TeamHopePoolServerScopeSpec = {
  engine: TeamHopePoolEngine;
};

export const teamHopePoolServerScopeHooks: ServerScopeHooks<ScopeType.TEAM_HOPE_POOL> =
  {
    createEngine: ({ execCtx, engineCtx }) => {
      const { controller, scope } = engineCtx;
      const teamController = controller.getTeamControllerIfExists(scope.teamId);
      if (teamController === null)
        return execCtx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_TEAM, null);
      return new TeamHopePoolEngine(engineCtx, teamController);
    },
    engineIdentityFunc: (engine) => engine,
  };
